<template>
	<v-form v-model="valid" ref="form" :lazy-validation="lazy" class="pa-3">
		<v-btn color="primary" block depressed>
			<v-icon class="mr-3">mdi-facebook</v-icon>
			<span class="d-xs-flex d-sm-none">Daftar menggunakan <br> Facebook</span>
      <span class="d-none d-sm-flex">Daftar menggunakan Facebook</span>
		</v-btn>
		<p class="text-center my-3 grey--text">ATAU</p>
    <Notif :status="status" :msg="msg" @visible="visible" />
    <NotifCekNomor :status="statusCekNomor" :msg="msg2" @visible="visible2" />
		<v-text-field
      outlined
      label="Nama Lengkap"
      :rules="nameRule"
      v-model="dataPeserta.nama_lengkap"
      prepend-inner-icon="mdi-account"      
    ></v-text-field>
    <v-text-field
      outlined
      label="E-mail"
      :rules="emailRule"
      v-model="dataPeserta.email"
      prepend-inner-icon="mdi-gmail"      
    ></v-text-field>
    <v-text-field
      outlined
      label="No.Hp (sebagai username)"
      type="number"
      :rules="hpRule"
      counter
      v-model="dataPeserta.nohp"
      prepend-inner-icon="mdi-cellphone"      
    ></v-text-field>
    <v-text-field
      outlined
      label="Password"
      :rules="passwordRule"
      v-model="dataPeserta.password"
      counter
      :append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
      :type="showpassword ? 'text' : 'password'"
      @click:append="showpassword = !showpassword"
      prepend-inner-icon="mdi-lock"      
    ></v-text-field>
    <div class="mb-3">
      Dapatkan Paket FREE dari SOBATUTBK
    </div>
    <v-select
      label="Paket FREE"
      outlined
      :items="kelompok"
      :rules="requiredRule"
      v-model="kelompokUji"
    ></v-select>
    <v-btn 
      color="orange" 
      class="white--text"
      type="submit"
      @click.prevent="validate"
      block depressed
      :loading="isLoading"
      :disabled="!valid">
			buat akun
		</v-btn>
    <p class="text-center my-3">
      <a href="javascript:void(0)" @click="$router.push({ name: 'login-peserta' })" style="text-decoration: none;">
        Sudah punya akun
      </a>
    </p>
	</v-form>
</template>

<script>
  import { cekKodeAfiliasi } from '@/config/services'
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
  import NotifCekNomor from './NotifCekNomor'
	export default{
		data: () => ({
      valid: true,
      lazy: false,
      kelompok: ['SAINTEK', 'SOSHUM'],
      kelompokUji: '',
      dataPeserta: {
        nama_lengkap: '',
        email: '',
        nohp: '',
        password: '',
        kelompok_uji: ''
      },
			showpassword: false,	
      requiredRule: [ v => !!v || 'Tidak boleh kosong' ] ,
      msg: {
        success: 'Daftar berhasil',
        error: 'Pendaftaran gagal',
        visible: false
      },
      msg2: {
        success: 'Daftar berhasil',
        error: 'Nomor telepon sudah digunakan mohon diganti dengan yang lain',
        visible: false
      },	
      kode: ''	
		}),

    components: {
      NotifCekNomor,
      Notif
    },

    watch: {
      "kelompokUji": function (val) {
        this.dataPeserta.kelompok_uji = val.toLowerCase()        
      }
    },

    computed: {
      ...mapState('dataLogin', ['status', 'isLoading', 'statusCekNomor']),
      emailRule () {
        return [
          ...this.requiredRule,
          v => /.+@.+\..+/.test(v) || 'Data berupa e-mail'
        ]
      },
      hpRule () {
        return [
          v => (v && v.length >= 11) || 'No lebih dari 11 nomor',
          v => !!v.match(/\d+$/g) || 'No harus angka',
          v => !!v || 'Tidak boleh kosong',
        ]
      },
      passwordRule () {
        return [
          ...this.requiredRule,
          v => (v && v.length >= 6) || 'Password lebih dari 6 karakter',
        ]
      },
      nameRule () {
        return [
          ...this.requiredRule,
          v => (v && v.length >= 3) || 'Nama lebih dari 3 karakter',
        ]
      }
    },

    created () {
      this.kode = JSON.stringify(window.localStorage.getItem('kode_afiliasi'))
      // console.log(this.kode)
      if (JSON.parse(this.kode) !== null) {
        cekKodeAfiliasi (JSON.parse(this.kode))
          .then(res => {
            if (res.data.valid) {
              this.dataPeserta.kode_afiliasi = JSON.parse(this.kode)
            }
          })
      }
    },

    methods: {
      validate() {
        if (this.$refs.form.validate()) {
          // console.log(this.dataPeserta)
          this.$store.dispatch('dataLogin/postDataDaftar', this.dataPeserta)
          this.$store.dispatch('dataLogin/updateIsLoading', true)
          this.msg.visible = true
          this.msg2.visible = true
        }      
      },
      visible (val) {
        this.msg.visible = val
      },
      visible2 (val) {
        this.msg2.visible = val
      }
    },
	}
</script>