<template>
  <v-form v-model="valid" ref="form">
    <v-overlay :value="overlay" z-index="1000">
      <!-- <v-col cols="12" md="3" sm="6" class="pa-0"> -->
        <div class="d-flex">
          <v-spacer></v-spacer>
          <v-btn
            fab
            x-small
            color="red"
            @click="overlay = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
        <!-- <v-card> -->
          <v-img
            src="@/assets/simulasi.png"
            class="white--text align-end width-modal-free"
            min-width="320"
            max-width="320"
          >
            <div 
              class="pa-3" 
              style="background-color: rgba(0,0,0,0.5);">
              Fitur Simulasi Rasionalisasi dapat diakses ketika kamu sudah membeli paket premium
              <!-- <div class="d-flex my-3">
                <v-spacer></v-spacer>
                <v-btn
                  small
                  text
                  @click="overlay=false"
                  color="white">
                  Oke, saya mengerti
                </v-btn>                
              </div> -->
              <v-btn
                light
                depressed
                block class="mt-3"
                @click="$router.push({name: 'fasilitas', params: {paket: 'platinum'}})"
                color="success">
                <v-icon class="mr-3">mdi-cart</v-icon>
                Beli sekarang
              </v-btn>
            </div>
          </v-img>
        <!-- </v-card> -->
      <!-- </v-col> -->
    </v-overlay>
    <v-alert type="info" dense v-model="alert" close>
      Fitur Simulasi Rasionalisasi UTBK dapat diakses ketika kamu sudah membeli
      paket premium
    </v-alert>
    <v-autocomplete
      v-model="paket"
      :items="dataHistory.ujian"
      item-text="paket_soal"
      return-object
      :rules="requiredRule"
      autocomplete="off"
      dense
      hide-no-data
      hide-selected
      outlined
      label="Pilih paket soal"
    ></v-autocomplete>
    <p>
      Ubah target program studimu
    </p>
    <v-autocomplete
      v-model="ptn"
      :items="dataPTN"
      item-text="universitas"
      item-value="universitas"
      :loading="isLoading"
      :rules="requiredRule"
      autocomplete="off"
      dense
      hide-no-data
      hide-selected
      :disabled="paket === '' ? true:false"
      outlined
      label="Cari Perguruan tinggi"
    ></v-autocomplete>
    <v-autocomplete
      v-model="studi"
      :items="dataProdi"
      :rules="requiredRule"
      item-text="prodi"
      return-object
      dense
      hide-no-data
      hide-selected
      :disabled="ptn === '' ? true:false"
      outlined
      autocomplete="off"
      label="Cari program studi"
    ></v-autocomplete>
    <v-btn 
      depressed 
      color="primary"
      :disabled="!valid"
      :loading="isLoading"
      @click.prevent="validate">
      ubah
    </v-btn>
  </v-form>
</template>

<script>
  import { mapState } from 'vuex'
  import { flattenDeep, findIndex } from 'lodash'

  export default {
    data: () => ({  
      valid: true,
      ptn: '',
      studi: '',
      search: null,
      paket: '',
      kelompok: '',
      icon: 'mdi-magnify',
      data: {
        id_ujian: 0,
        id_ptn: 0,
        id_prodi: 0
      }, 
      requiredRule: [v => !!v || 'Tidak boleh kosong'],
      alert: false,
      overlay: false
    }),

    computed: {
      ...mapState('dataPTN', ['status', 'isLoading', 'dataPTN', 'dataProdi']),
      ...mapState('dataRanking', ['dataHistory']),
      ...mapState('dataDashboard', ['dataDashboard']),
      jumlahPaket () {
        var jadi = this.dataDashboard.map(paket => {
          return paket.paket.map(data => {
            return data
          })
        })

        return flattenDeep(jadi)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          var paketPremium = this.jumlahPaket.filter(e => {
            // console.log(e.status)
            if (e.nama_paket.indexOf('FREE') === -1) {
              return e
            }
          })
          var hasil = findIndex(paketPremium, o => o.status === true)
          if (hasil === -1) {
            this.overlay = true
            this.alert = true
          } else {
            this.$store.dispatch('dataRanking/postDataRanking', this.data)
            this.$store.dispatch('dataRanking/updateIsLoading', true)
          }
        }
      },
    },

    watch: {
      paket (val) {
        this.kelompok = val.kelompok_ujian
        this.data.id_ujian = val.id_ujian
        this.$store.dispatch('dataPTN/getDataPTN', val.kelompok_ujian)
      },
      ptn (val) {
        var data = {
          ptn: val,
          kelompok: this.kelompok.toLowerCase()
        }
        this.$store.dispatch('dataPTN/getDataProdi', data)
        this.$emit('ptn', val)
      },
      studi (val) {
        this.data.id_ptn = val.kode_ptn
        this.data.id_prodi = val.kode_prodi
      }
    }
  }
</script>