<template>
	<div class="print-pdf">
		<Navbar class="hiddenOnPrint"/>

		<div class="d-flex justify-space-between mx-auto my-3 hiddenOnPrint" style="width: 210mm">
			<v-btn 
				@click="print" 
				depressed color="primary" class="hiddenOnPrint">
				<v-icon class="mr-3">mdi-printer</v-icon>
				cetak
			</v-btn>
			<v-btn 
				@click="$router.go(-1)" 
				depressed color="success" class="hiddenOnPrint">
				kembali
			</v-btn>
		</div>

		<div class="page-a4 mx-auto pa-3">
			<div class="text-center">
				<h1>Assessment Report</h1>
				<h3>{{namaPaket}}</h3>
			</div>

			<div class="d-flex justify-center text-left">
				<!-- <div> -->
					<v-avatar size="120" color="grey lighten-4" class="my-3">
						<img
							v-if="peserta.avatar !== null"
							:src="peserta.avatar" 
							alt="profile" 
						/>
						<v-icon v-else size="80">mdi-account</v-icon>
					</v-avatar>
					<div class="d-flex align-center ml-3" style="height: 120px;">
						{{peserta.nama.toUpperCase()}} <br> 
						{{peserta.sekolah.nama}}
					</div>
				<!-- </div> -->
			</div>

			<h3>Pilihan PTN</h3>
			<v-simple-table>
		    <template v-slot:default>
		      <thead>
		        <tr>
		          <th class="text-left">Perguruan Tinggi</th>
		          <th class="text-left">Program Studi</th>
		          <th class="text-center">Kode</th>
		          <th class="text-center">Kelompok</th>
		        </tr>
		      </thead>
		      <tbody>
		        <tr v-for="(item, i) in detailAssessment.ptn_pilihan" :key="i">
		          <td>{{ item.nama }}</td>
		          <td class="text-left">{{ item.prodi }}</td>
		          <td class="text-center">{{ item.kode }}</td>
		          <td class="text-center">{{ item.kelompok }}</td>
		        </tr>
		      </tbody>
		    </template>
		  </v-simple-table>
		  <br>
		  <h3>Detail Hasil</h3>
		  <v-simple-table>
		    <template v-slot:default>
		      <thead class="white">
		        <tr>
		          <th class="text-left">Pilihan PTN</th>
		          <th class="text-left">Program Studi</th>
		          <th class="text-center">Kelompok</th>
		          <th class="text-center">Nilai Min</th>
		          <th class="text-center">DT</th>
		          <th class="text-center">IK</th>
		          <th class="text-center">Keterangan</th>
		        </tr>
		      </thead>
		      <tbody>
		        <tr v-for="(item, i) in detailAssessment.ptn_pilihan" :key="i">
		          <td>{{ item.nama }}</td>
		          <td class="text-left">{{ item.prodi }}</td>
		          <td class="text-center">{{ item.kelompok }}</td>
		          <td class="text-center">{{ item.nilai_min }}</td>
		          <td class="text-center">{{ item.daya_tampung }}</td>
		          <td class="text-center">{{ item.indeks_keketatan.value }}%</td>
		          <td class="text-center" v-if="item.memenuhi">Memenuhi</td>
		          <td class="text-center" v-else>Belum Memenuhi</td>
		        </tr>
		      </tbody>
		    </template>
		  </v-simple-table>
		  <br>
		  <v-row>
				<v-col cols="12" md="6" xs="6">					
					<h4 class="mb-3">
						NILAI CEEB Mata Uji
					</h4>					
					<div 
						v-for="(item, i) in detailAssessment.analisis_nilai.tabel"
						class="d-flex justify-space-between black--text mb-1">
						<span>{{item.materi_uji}}</span>
						<b>{{parseFloat(item.ceeb.skor).toFixed(2)}}</b>
					</div>
				</v-col>				
			</v-row>
			<v-row>
				<v-col cols="12" md="6" xs="12"  v-for="(item, i) in detailAssessment.analisis_nilai.ceeb" :key="i" class="mb-3">
					<div>
						<div class="font-weight-bold">{{item.kelompok_uji}}</div>
						<v-row no-gutters>
							<v-col cols="12" xs="4">
								<div class="d-flex align-center" style="height: 103px;">
									<div>
										<div>Nilai Akhir (NA)</div>
										<b class="headline">{{parseFloat(item.nilai_akhir).toFixed(2)}}</b>
									</div>
								</div>
							</v-col>
							<v-col cols="12" xs="8">
								<div class="d-flex align-center" style="height: 103px;">
									<div>
										<ul class="pa-0" style="list-style: none;">
											<li>
												<v-icon color="success" class="mr-3">mdi-checkbox-marked</v-icon>
												<span>{{item.detail.benar}} Soal Benar</span>
											</li>
											<li>
												<v-icon color="red" class="mr-3">mdi-close-box</v-icon>
												<span>{{item.detail.salah}} Soal Salah</span>
											</li>
											<li>
												<v-icon class="mr-3">mdi-minus-box</v-icon>
												<span>{{item.detail.kosong}} Soal tidak dijawab</span>
											</li>
										</ul>
									</div>
								</div>
							</v-col>
						</v-row>
					</div>
				</v-col>
			</v-row>
		</div>

		<div class="page-a4 mx-auto pa-3 mt-3">
			<div v-for="(item, i) in detailAssessment.analisis_nilai.tabel">
		  	<h3>Perolehan {{item.materi_uji}}</h3>
			  <v-simple-table>
					<template v-slot:default>
			      <thead class="white">
			        <tr>
			          <th class="text-left">
			          	&nbsp;
			          </th>
			          <th class="text-center">B</th>
			          <th class="text-center">S</th>
			          <th class="text-center">K</th>
			          <th class="text-center">Skor</th>
			        </tr>
			      </thead>
			      <tbody>
			        <tr v-for="(val, i) in item.detail" :key="i">
			          <td>{{ val.submateri }}</td>
			          <td class="text-center">{{ val.benar }}</td>
			          <td class="text-center">{{ val.salah }}</td>
			          <td class="text-center">{{ val.kosong }}</td>
			          <td class="text-center">{{ parseFloat(val.skor).toFixed(2) }}</td>
			        </tr>
			        <tr>
			        	<td class="font-weight-bold">Nilai CEEB</td>
			          <td class="text-center font-weight-bold">{{ item.ceeb.benar }}</td>
			          <td class="text-center font-weight-bold">{{ item.ceeb.salah }}</td>
			          <td class="text-center font-weight-bold">{{ item.ceeb.kosong }}</td>
			          <td class="text-center font-weight-bold">
			          	{{ parseFloat(item.ceeb.skor).toFixed(2) }}
			          </td>
			        </tr>
			      </tbody>
			    </template>
			  </v-simple-table>
			  <br>
		  </div>
		</div>

		<div class="page-a4 mx-auto pa-3 mt-3">
			<h3>Peringkat Tryout</h3>
			<v-simple-table>
				<template v-slot:default>
		      <thead class="white">
		        <tr>
		          <th class="text-left">Pilihan PTN</th>
		          <th class="text-left">Program Studi</th>
		          <th class="text-center">Daya Tampung</th>
		          <th class="text-center">Peringkat Sekolah</th>
		          <th class="text-center">Peringkat Nasional</th>
		          <th class="text-center">Keterangan</th>
		        </tr>
		      </thead>
		      <tbody>
		        <tr v-for="item in detailAssessment.ptn_pilihan" :key="item.ptn">
		          <td>{{ item.nama }}</td>
		          <td class="text-left">{{ item.prodi }}</td>
		          <td class="text-center">{{ item.daya_tampung }}</td>
		          <td class="text-center">{{ item.peringkat_ujian.sekolah }}</td>
		          <td class="text-center">{{ item.peringkat_ujian.nasional }}</td>
		          <td class="text-center" v-if="item.memenuhi">Memenuhi</td>
        			<td class="text-center" v-else>Belum Memenuhi</td>
		        </tr>
		      </tbody>
		    </template>
		  </v-simple-table>
		  <br>
		  <v-row>
		  	<v-col cols="12" md="6" sm="12" 
					v-for="(item, i) in detailAssessment.peringkat_tryout" 
					:key="i">
					<h3 class="mb-3">
						{{item.kelompok_uji}}
					</h3>
					<div class="black--text">
						<v-row no-gutters>
							<v-col cols="12" sm="4">							
								<center>
									<v-progress-circular 
										:value="100"
										:size="103"
										:width="10"
										:rotate="-90"
										color="yellow"
									>
										<div class="black--text title font-weight-bold">
											{{dataScore[item.kelompok_uji.toLowerCase()]}}
										</div>
									</v-progress-circular>	
								</center>						
							</v-col>
							<v-col cols="12" sm="8">
								<div class="d-flex align-center" style="height: 103px;">
									<div>
										<div>Peringkat <b>{{item.peringkat.self}}</b></div>
										<div>Dari {{item.peringkat.all}} Peserta</div>
									</div>
								</div>
							</v-col>
						</v-row>
					</div>
				</v-col>
		  </v-row>
		  <br>
		  <h3>Target dan Pencapaian</h3>
		  <v-simple-table>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-center" rowspan="2">Pilihan Ke</th>
              <th class="text-left" rowspan="2">Perguruan Tinggi Negri</th>
              <th class="text-left" rowspan="2">Program Studi</th>
              <th class="text-center" rowspan="2">Target NA</th>
              <th class="text-center" colspan="2">Pencapaian</th>
              <th class="text-center" rowspan="2">status</th>
            </tr>
            <tr>
              <th class="text-center">NA</th>
              <th class="text-center">%</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="detailAssessment.pencapaian.length === 0">
              <td colspan="9">
                <v-alert type="info" class="mt-4">
                  <div>Data tidak ditemukan silahkan coba lagi</div>
                </v-alert>
              </td>
            </tr>
            <tr v-else v-for="(item, i) in detailAssessment.pencapaian" :key="i">
              <td class="text-center">{{ item.pilihan }}</td>
              <td class="text-left">{{ item.ptn }}</td>
              <td class="text-left">{{ item.prodi }}</td>
              <td class="text-center">{{ parseFloat(item.progress.nilai_akhir.target).toFixed(2) }}</td>
              <td class="text-center">{{ item.progress.nilai_akhir.tercapai }}</td>
              <td class="text-center">{{ parseFloat(item.progress.nilai_akhir.persentase.value).toFixed(2) }}%</td>
              <td class="text-center">
                <v-icon v-if="item.progress.nilai_akhir.persentase.value >= 100" color="success">mdi-check-circle</v-icon>
                <v-icon v-else color="red">mdi-close-circle</v-icon>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
		</div>

		<div class="page-a4 mx-auto pa-3 mt-3 d-none">
			<h3>Rekomendasi PTN</h3>
			<v-simple-table>
        <template v-slot:default>
          <thead class="white">
            <tr>
              <th class="text-center" v-for="(item, i) in headers" :key="i">
                {{item.text}}
              </th>
            </tr>
          </thead>
          <tbody v-for="(item, i) in detailAssessment.rekomendasi_ptn" :key="i">            
            <tr v-if="item.alternative.length === 0">
              <td colspan="9">
                <v-alert type="info" class="mt-4">
                  <div>
                    Rekomendasi Prodi berdasarkan Nilai Akhir 
                    <i>tidak ditemukan</i>. Perolehan Nilai Akhir masih di bawah Nilai Minimal. Banyak berlatih sub-sub materi uji yang belum tercapai
                  </div>
                </v-alert>
              </td>
            </tr>
            <tr v-else v-for="(data, index) in item.alternative" :key="index">
              <td class="text-center">{{ (i*10)+1+index }}</td>
              <td class="text-left">{{ data.ptn }}</td>
              <td class="text-left">{{ data.prodi }}</td>
              <td class="text-center">{{ data.kelompok }}</td>
            </tr>            
          </tbody>
        </template>
      </v-simple-table>
		</div>
	</div>
</template>

<style scoped>
	.print-pdf{
		padding-top: 82px;
	}
	.page-a4{
		width: 210mm;
		height: 290mm;
		display: block;
		background-color: #fff;
	}

	@media print {
		.print-pdf{ padding-top: 0px; }
	}
</style>

<script>
	import {mapState} from 'vuex'
	import Navbar from '@/components/Navbar'

	export default {
		components: {
			Navbar
		},

		data: () => ({
			peserta: {},
			headers: [
        { text: 'No', value: 'calories' },
        { text: 'Universitas', value: 'nama_peserta' },
        { text: 'Program Studi', value: 'sekolah.nama' },
        { text: 'Kelompok Uji', value: 'sekolah.provinsi' },
      ],
		}),

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			this.peserta = peserta
  		var data = {
  			id_peserta: peserta.id,
  			id_ujian: parseInt(this.$route.params.id)
  		}
			this.$store.dispatch('dataAssessment/getDataAssessment', peserta.id)  		
  		this.$store.dispatch('dataAssessment/getDetailAssessment', data)
  		this.$store.dispatch('dataAssessment/postDataScoreAll', {id_ujian: data.id_ujian})
		},

		computed: {
			...mapState('dataAssessment', ['dataAssessment', 'detailAssessment', 'dataScore']),
			namaPaket () {
				var hasil = this.dataAssessment
				.filter(data => data.id_ujian === parseInt(this.$route.params.id))

				return {...hasil[0]}.paket_soal
			}
		},

		methods: {
			print () {
				window.print()
			}
		}
	}
</script>