<template>
	<div class="ml-4">
		<div 
			class="custom-control custom-radio my-2" 
			v-for="(item, index) in jawaban" 
			:key="index">
		  <input 
		  	type="radio" 
		  	name="jawaban" 
		  	:id="`jawaban${index}`"
		  	v-shortkey="[option[index]]"
		  	@shortkey="tes(index)"
		  	v-model="selected" 
		  	:value="item.huruf" 
		  	class="custom-control-input"
		  	:class="`radio${index}`" />
		  <label 
		  	:class="`custom-control-label huruf${index}`"
		  	:for="`jawaban${index}`" 
		  	v-html="item.teks"
		  ></label>
		</div>
	</div>
</template>

<style>
	.custom-control p{ margin-bottom: 0px; }
	.custom-control{
		position: relative;
    display: block;
    min-height: 1.5rem;
    padding-left: 1.5rem;
	}
	input[type=radio] {
    box-sizing: border-box;
    padding: 0;
	}
	.custom-control-input {
    position: absolute;
    z-index: -1;
    opacity: 0;
	}
	.custom-control-label {
    margin-bottom: 0;
	}
	label {
    display: inline-block;
	}
	.custom-control-label::before {
    border-radius: 50%;
	}
	.custom-control-label::before {
    position: absolute;
    top: 0rem;
    left: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.5rem;
    height: 1.5rem;
    content: "";
    font-size: 14px;
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: #fff;    
    border: 1px solid silver;
	}
	.custom-control:hover .custom-control-label::before{
    background-color: #dee2e6;    		
	}
	.huruf0::before{
    content: "A";		
	}
	.huruf1::before{
    content: "B";		
	}
	.huruf2::before{
    content: "C";		
	}
	.huruf3::before{
    content: "D";		
	}
	.huruf4::before{
    content: "E";		
	}
	.custom-control-label::after {
    position: absolute;
    top: .25rem;
    left: 0;
    display: block;
    width: 1.3rem;
    height: 1.3rem;
    content: "";
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
	}
	input[type=radio]:checked + .custom-control-label::before {
	  background-color: #599CDE;
	  color: #fff;
	}
</style>


<script>
	import { mapState } from 'vuex'
	export default {
		props: ['jawaban', 'id'],
		data: () => ({
			selected: '',
			option: ['a', 'b', 'c', 'd', 'e']
		}),
		computed: {
			...mapState('dataSoal', ['dataJawaban'])
		},
		created () {
			var index = this.$route.params.index
			// var data = this.jawabanTerpilih
			// .filter(data => data.id_soal === this.index)
			var local = JSON.parse(window.localStorage.getItem('jawaban'+parseInt(index)))
			
			if (local === null) {
				this.selected = ''				
			} else {
				var hasil = local.filter(data => data.pilihan !== undefined && data.id_soal === this.id)
				this.selected = {...hasil[hasil.length-1]}.pilihan
			}
		},
		computed: {
			jawabanTerpilih() {
				return this.$store.getters['dataSoal/getDataJawabanTerpilih']
			}
		},
		watch: {
			selected (val) {
				var id = parseInt(this.$route.params.index)
				var data = { id_soal: this.id, pilihan: val, index: id }
				this.$store.dispatch('dataSoal/updateDataJawabanTerpilih', data)
			}
		},
		methods: {
			tes (index) {
				var radio = document.querySelector('.radio'+index)
				radio.click()
			}
		}
	}
</script>