<template>
	<div class="riwayat" style="padding-top: 56px;">
		<Navbar/>

		<v-content>
			<v-container class="my-6">
				<v-btn 
					:outlined="statusTransaksi === 0 ? false:true"
					depressed dark
					class="mr-3"
					@click="statusTransaksi = 0"
					color="indigo">
					Menunggu pembayaran		
				</v-btn>
				<v-btn 
					@click="statusTransaksi = 1"
					:outlined="statusTransaksi === 1 ? false:true"
					depressed dark 
					color="indigo">
					transaksi berhasil	
				</v-btn>
				
				<div class="mt-6">
					<v-row>
						<v-col 
							v-if="dataRiwayatStatus.length === 0"
							cols="12" 
							sm="12">
							<v-alert type="info">
								Data belum ada
							</v-alert>
						</v-col>
						<v-col 
							v-else
							cols="12" 
							md="6" sm="12"
							v-for="(item, i) in dataRiwayatStatus" :key="i">
							<Invoice :item="item" />
							<v-divider></v-divider>
							<CaraPembayaran :atm="item.deskripsi_atm" :internet="item.deskripsi_internet_banking" :mobile="item.deskripsi_mobile_banking" />
						</v-col>
					</v-row>
				</div>
			</v-container>
		</v-content>

		<Footer :app="true" :absolute="true" :bottom="true" />
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Navbar from '@/components/Navbar'
	import Footer from '@/components/Footer'
	import Invoice from '@/components/Riwayat/Invoice'
	import CaraPembayaran from '@/components/Riwayat/CaraPembayaran'

	export default {
		data: () => ({
      statusTransaksi: 0,
		}),

		components: {
			Footer,
			Navbar,
			CaraPembayaran,
			Invoice
		},

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			var data = {
				peserta_id: peserta.id
			}
			this.$store.dispatch('dataAssessment/postDataRiwayat', data)
			this.$store.dispatch('dataAssessment/updateIsLoading', true)
		},

		computed: {
			...mapState('dataAssessment', ['dataRiwayat']),
			dataRiwayatStatus () {
				return this.dataRiwayat.filter(e => e.status === this.statusTransaksi)
			},
		},
	}
</script>