<template>
	<div
		class="fasilitas"
		:style="`background-image: url(${require('@/assets/biodata_bg.png')})`">
		<Navbar/>
		<v-container class="mt-12">
			<v-row>
				<v-col 
					cols="12" md="4" sm="12" 
					v-for="(data, index) in paket" :key="index">
					<v-card 
						class="mb-12"
						v-if="data.subpaket.indexOf('CAMPURAN') === -1">				
						<v-img 
							:src="require(`@/assets/banner_assesment/${index+1}.svg`)" 
							alt="sekolatik">
							<v-card-title class="d-flex">
								<div class="white--text font-weight-bold">{{data.subpaket}}</div>		
							</v-card-title>
						</v-img>
						<v-card-text class="pt-4 pb-0">
						  <div class="d-flex black--text">
						  	<div>Fasilitas</div>
						  	<v-spacer></v-spacer>
						  	<b class="title">
						  		{{Intl.NumberFormat('id-ID', { maximumSignificantDigits: 4, style: 'currency', currency: 'IDR' }).format(data.biaya)}}
						  	</b>
						  </div>
						  <v-list dense>
						  	<v-list-item 
						  		class="px-0"
						  		v-for="(item, index) in listFasilitas(data.subpaket)" :key="index">
						  		<v-list-item-icon>
						  			<v-icon color="orange">mdi-check-box-outline</v-icon>
						  		</v-list-item-icon>
						      <v-list-item-content>
						        <v-list-item-title>{{item}}</v-list-item-title>
						      </v-list-item-content>
						    </v-list-item>
						  </v-list>
						</v-card-text>
						<v-card-actions>
							<v-btn 
								block 
								color="yellow darken-2" 
								rounded large depressed
								:loading="isLoading"
								@click="show(data.id_subpaket)">
								pesan sekarang
							</v-btn>
						</v-card-actions>
					</v-card>
				</v-col>
			</v-row>

			<FormPayment :visible="visible" @hidden="hidden" />

		</v-container>	

		<Footer/>
	</div>
</template>

<style>
	.fasilitas{
		background-size: cover;
		background-position: center;
		background-attachment: fixed;
		padding-top: 56px;
	}
	.v-list-item__title{
		white-space: normal;
	}
	.v-list-item__subtitle{
		white-space: normal;
	}
</style>

<script>
	import {mapState} from 'vuex'
	import Navbar from '@/components/Navbar'
	import Footer from '@/components/Footer'
	import FormPayment from '@/components/Fasilitas/FormPayment'
	import { fasilitas } from '@/components/Fasilitas/paket'

	export default {
		data: () => ({
			visible: false,
			fasilitas: []
		}),
		components: {
			FormPayment,
			Footer,
			Navbar,
		},
		created () {
			this.$store.dispatch('dataPaket/getDataPaket')
			this.fasilitas = fasilitas
		},
		computed: {
			...mapState('dataPaket', ['dataPaket', 'isLoading']),
			paket () {
				return this.dataPaket.filter((data, index) => {
					var status = data.paket_nama.toLowerCase().indexOf(this.$route.params.paket)
					if (status !== -1) {
						return data
					}
				})
			},
		},
		methods: {
			show (id) {
				this.$store.dispatch('dataPayment/getDataPayment', id)
				this.$store.dispatch('dataPayment/updateIsLoading', true)
				this.visible = true
			},
			hidden (val) {
				this.visible = val
			},
			listFasilitas (subpaket) {
				var paket = this.fasilitas
					.filter(data => data.paket === this.$route.params.paket)
				var list = {...paket[0]}.fasilitas.filter(data => {
						var status = subpaket.toLowerCase().indexOf(data.subpaket)
						if (status !== -1) { return data }
					})
				return {...list[0]}.list
			}
		}
	}
</script>