<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent
  >
    <v-card color="red" dark>
      <v-card-text class="pt-6 white--text">
        Mohon maaf waktu pengerjaan soal telah habis sehingga anda tidak dapat lagi melanjutkan pengerjaan soal seluruh jawaban anda telah tersimpan.
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="white"
          class="red--text"
          depressed
          :loading="isLoading"
          @click="lihatHasil"
        >
          baik saya menerti
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    props: ['visible'],    
    data () {
      return {
        dialog: false,
      }
    },
    computed: {
      ...mapState('dataSoal', ['isLoading'])
    },
    watch: {
      visible (val) {
        this.dialog = val
      }
    },
    methods: {
      lihatHasil () {
        var index = this.$route.params.index
        this.$store.dispatch('dataSoal/postStatusCompleted', parseInt(index))        
        this.$router.push({name: 'upload-jawaban'})
      }
    }
  }
</script>