<template>
	<div>
		<FormKartuKredit v-if="kartuKredit" @kartu="kartu" @next="next"/>
		<v-card v-else>
			<v-card-title>
				Pilih Metode Pembayaran
			</v-card-title>

			<v-card-text class="black--text d-flex">
				<v-row>
					<v-col cols="12" md="6" sm="12">
						<!-- <v-btn 
							outlined 
							color="primary" 
							disabled block 
							@click="kartuKredit=true">
							bayar kartu kredit
						</v-btn> -->
						<v-select
							v-model="metode_bank"
							outlined
							dense
							class="mt-7"
							return-object							
							label="Virtual Account"
							:items="dataPayment.metode[1].bank[0]"
							item-text="nama_bank"
						></v-select>	
						<v-select
							v-model="metode_mitra"
							disabled
							outlined
							return-object			
							dense
							label="Confencence Store"
							:items="dataPayment.metode[2].mitra[0][0]"
							item-text="nama_mitra"
						></v-select>	
						<!-- <v-select
							v-model="metode_ewallet"
							outlined
							return-object			
							dense
							label="E-wallet"
							:items="[dataPayment.metode[3].mitra]"
							item-text="nama_mitra"
						></v-select>	 -->					
					</v-col>
					<v-col cols="12" md="6" class="d-none d-md-flex">
						<img src="@/assets/atm.png" alt="atm" height="100%" />
					</v-col>
				</v-row>
			</v-card-text>
		</v-card>
		<div class="pt-4 d-flex">
			<v-spacer></v-spacer>
			<v-btn color="primary" text @click="next('kembali')">
	    	kembali
	    </v-btn>
			<!-- <v-btn color="primary" depressed class="ml-3" @click="next">
	    	lanjutkan pembayaran
	    </v-btn> -->
		</div>
	</div>
</template>


<script>
	import FormKartuKredit from './FormKartuKredit'

	export default {
		props: ['dataPayment'],
		components: {
			FormKartuKredit
		},
		data: () => ({
			akunVirtual: [],
			store: [],
			metode_mitra: {},
			metode_bank: {},
			metode_ewallet: {},
			kartuKredit: false
		}),
		watch: {
			metode_bank (val) {
				if (val !== '') {
					val.metode_pembayaran = '02'
					this.$store.dispatch('dataPayment/updateDataMetode', val)
					this.$emit('next', 3)
				}
			},
			metode_mitra (val) {
				if (val !== '') {
					val.metode_pembayaran = '03'
					this.$store.dispatch('dataPayment/updateDataMetode', val)
					this.$emit('next', 3)
				}
			},
			metode_ewallet (val) {
				if (val !== '') {
					val.metode_pembayaran = '05'
					this.$store.dispatch('dataPayment/updateDataMetode', val)
					this.$emit('next', 3)
				}
			}
		},
		methods: {
			next (jenis) {				
				if (jenis === 'kembali') {
					this.step = 1
				} else {
					this.step = 3					
				}
				this.$emit('next', this.step)
			},
			kartu (val) {
				this.kartuKredit = val
			}
		}
	}
</script>