import {
	getDataSoal,
	getDataSoal2,
	postDataJawaban,
	postDataJawaban2,
	getDataPaketSoal,
	postDataUjian,
	postDataUjian2,
	postDataScore,
	postStatusCompleted,
	postFinishUjian,
	pauseUjian
} from '@/config/services'
import { uniqBy, reverse } from 'lodash'
import router from '@/router/index'
import moment from 'moment'

var dataSoal = {
	namespaced: true,
	state: {
		dataSoal: [],
		isLoading: false,
		status: null,
		isLoadingUploadJawaban: false,
		statusUploadJawaban: null,
		page: 1,
		dataJawaban: {
			id_sesi: 0,
			jawaban: [],
			id_peserta: 0
		},
		dataHasil: [],
		dataPaketSoal: [],
		dataUjian: {},
		dataScore: {},
		materi_uji: '',
		submateri: '',
		durasiSubmateri: 0
	},
	mutations: {
		updateDataUjian (state, payload) {
			state.dataUjian = payload
		},
		updateDataSoal (state, payload) {
			state.dataSoal = payload
		},
		updateDataPaketSoal (state, payload) {
			state.dataPaketSoal = payload
		},
		updateDataHasil (state, payload) {
			state.dataHasil.push(payload)
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoadingUploadJawaban (state, payload) {
			state.isLoadingUploadJawaban = payload
		},
		updateStatusUploadJawaban (state, payload) {
			state.statusUploadJawaban = payload
		},
		updatePage (state, payload) {
			state.page = payload
		},
		updateMateriUji (state, payload) {
			state.materi_uji = payload
		},
		updateSubmateri (state, payload) {
			state.submateri = payload
		},
		updateDataScore (state, payload) {
			state.dataScore = payload
		},
		updateDataJawabanIdSesi (state, payload) {
			state.dataJawaban.id_sesi = payload
		},
		updateDataJawabanTerpilih (state, payload) {
			if (payload.pilihan !== undefined) {
				var uniq = {
					id_soal: payload.id_soal,
					pilihan: payload.pilihan
				}
				state.dataJawaban.jawaban.push(uniq)
			}
		},
		updateDurasiSubmateri(state, payload) {
			state.durasiSubmateri = payload
		}
	},
	getters: {
		getDataJawaban: state => state.dataJawaban,
		getDataJawabanTerpilih: state => state.dataJawaban.jawaban,
		getDataHasil: state => state.dataHasil
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateIsLoadingUploadJawaban (context, payload) {
			context.commit('updateIsLoadingUploadJawaban', payload)
		},
		updateStatus (context, payload) {
			context.commit('updateStatus', payload)
		},
		updatePage (context, payload) {
			context.commit('updatePage', payload)
		},
		updateDataJawabanIdSesi (context, payload) {
			context.commit('updateDataJawabanIdSesi', payload)
		},
		updateDurasiSubmateri (context, payload) {
			let durasi_submateri = JSON.parse(window.localStorage.getItem('durasi_submateri'))
	  	let indexSubmateri = parseInt(payload)
	  	let durasiSubmateri = durasi_submateri[indexSubmateri]

	  	let time = moment().add(durasiSubmateri, 's')
	  	// if (durasi_submateri === null) {	  		
	  	// 	time = moment().add(durasiSubmateri, 'm')	  
	  	// 	window.localStorage.setItem('durasi_submateri_'+indexSubmateri, durasiSubmateri)		
	  	// } else {
	  	// 	time = moment().add(durasi_submateri, 's')	  		
	  	// }

			context.commit('updateDurasiSubmateri', moment(time).format('X'))
		},
		saveDurasiSubmateri(context, payload) {
			let durasi_submateri = JSON.parse(window.localStorage.getItem('durasi_submateri'))
			durasi_submateri[payload.index] = payload.durasi
			window.localStorage.setItem('durasi_submateri', JSON.stringify(durasi_submateri))
		},
		updateDataJawabanTerpilih (context, payload) {			
			context.commit('updateDataJawabanTerpilih', payload)
			if (payload.pilihan !== undefined) {
				var uniq = {
					id_soal: payload.id_soal,
					pilihan: payload.pilihan
				}
				var jawaban = JSON.parse(window.localStorage.getItem('jawaban'+payload.index))
				if (jawaban === null) {
					window.localStorage.setItem('jawaban'+payload.index, JSON.stringify([uniq]))					
				} else {
					jawaban.push(uniq)
					window.localStorage.setItem('jawaban'+payload.index, JSON.stringify(jawaban))					
				}
			}
		},
		postDataUjian2 (context, payload) {
			postDataUjian2 (payload)
				.then(res => {
					// console.log(res)
					if (res.status === 201) {
						window.localStorage.setItem('info_ujian', JSON.stringify(res.data.data))
						router.push({ name: 'download-soal' })
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)					
				})
		},
		getDataPaketSoal (context, payload) {
			getDataPaketSoal (payload)
				.then(res => {
					if (res.status === 200) {						
						context.commit('updateDataPaketSoal', res.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
		},
		getDataSoal2 (context, payload) {
			getDataSoal2 (payload)
				.then(res => {
					// console.log(res)
					if (res.status === 200) {	
						context.commit('updateMateriUji', res.data.nama)

						context.commit('updateSubmateri', res.data.submateri[0].nama)
						let durasi_submateri = res.data.submateri.map((submateri, i) => {
							return submateri.durasi_ujian*60
						})

						let durasiSubmateri = JSON.parse(window.localStorage.getItem('durasi_submateri'))

						if (durasiSubmateri === null) {
							window.localStorage.setItem('durasi_submateri', JSON.stringify(durasi_submateri))	
						}


						window.localStorage.setItem('soal_ujian', JSON.stringify(res.data))
						context.dispatch('updateStatus', true)
						context.dispatch('updateIsLoading', false)
						router.push({ name: 'soal', params: { index: 0 } })						
					}
				})
				.catch(err => {
					context.dispatch('updateStatus', false)
					context.dispatch('updateIsLoading', false)	
				})
		},
		saveDataJawaban (context, payload) {
			var soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
			var data = JSON.parse(window.localStorage.getItem('jawaban'+payload.id))

			reverse(data)
			var uniq = uniqBy(data, 'id_soal')
			// console.log(uniq)
			var waktu_mulai = JSON.parse(window.localStorage.getItem('durasi_ujian_mulai_'+payload.id))
			var waktu_selesai = moment().format('YYYY-MM-DD hh:mm:ss')

			var format_mulai = moment(waktu_mulai).format('X')
			var format_selesai = moment(waktu_selesai).format('X')
			var hasil = format_selesai - format_mulai

			var hasil_sebelumnya = JSON.parse(window.localStorage.getItem('durasi_ujian_'+payload.id))
			if (hasil_sebelumnya !== null) {
				var hasil_gabung = hasil + hasil_sebelumnya
			} else {
				var hasil_gabung = hasil
			}

			var jawaban = {
				"kategori_id": soal_ujian.submateri[payload.id].id_kategori_submateri,
				"durasi_ujian": hasil_gabung,
				"jawaban": uniq
			}

			var submisi = JSON.parse(window.localStorage.getItem('submisi'))
			if (submisi === null) {
				window.localStorage.setItem('submisi', JSON.stringify([jawaban]))					
			} else {
				submisi[payload.id] = jawaban
				window.localStorage.setItem('submisi', JSON.stringify(submisi))					
			}

			let newId = 0
			if (payload.posisi === 'next') {
				newId = payload.id + 1
			} else if (payload.posisi === 'prev') {
				newId = payload.id - 1
			}

			router.push({ name: 'soal', params: { index: newId } })
			context.commit('updateSubmateri', soal_ujian.submateri[newId].nama)

			var waktu = moment().format('YYYY-MM-DD hh:mm:ss')
			window.localStorage.setItem('durasi_ujian_mulai_'+newId, JSON.stringify(waktu))
			window.localStorage.setItem('durasi_ujian_'+payload.id, JSON.stringify(hasil_gabung))
		},
		postDataJawaban2 (context, payload) {
			var submisi = window.localStorage.getItem('submisi')
			var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))

			var form = new FormData()
			form.append("ujian_id", info_ujian.id)
			form.append("detail_id", info_ujian.detail_id)
			form.append("submisi", submisi)
			// console.log(form)
			postDataJawaban2 (form)
				.then(res => {
					// console.log(res)
					if (res.status === 201) {
						window.localStorage.setItem('score', JSON.stringify(res.data.data))
						context.commit('updateDataHasil', res.data)
						context.dispatch('updateIsLoadingUploadJawaban', false)
						context.commit('updateStatusUploadJawaban', true)									
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoadingUploadJawaban', false)
					context.commit('updateStatusUploadJawaban', false)
				})			
		},
		getDataScore (context, payload) {
			var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))
			var soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
			var data = { id_sub: info_ujian.detail_id }
			postDataScore (data) 
				.then(res => {
				// console.log(res)
					if (res.status === 200) {
						context.commit('updateDataScore', res.data)	
						window.localStorage.removeItem('submisi')
						soal_ujian.submateri.map((data, index) => {
							window.localStorage.removeItem('jawaban'+index)
						})					
					}					
				})
		},
		postStatusCompleted (context, payload) {
			var soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
			var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))
			var data = JSON.parse(window.localStorage.getItem('jawaban'+payload))

			reverse(data)
			var uniq = uniqBy(data, 'id_soal')
			// console.log(uniq)
			var waktu_mulai = JSON.parse(window.localStorage.getItem('durasi_ujian_mulai_'+payload))
			var waktu_selesai = moment().format('YYYY-MM-DD hh:mm:ss')

			var format_mulai = moment(waktu_mulai).format('X')
			var format_selesai = moment(waktu_selesai).format('X')
			var hasil = format_selesai - format_mulai

			var hasil_sebelumnya = JSON.parse(window.localStorage.getItem('durasi_ujian_'+payload))
			if (hasil_sebelumnya !== null) {
				var hasil_gabung = hasil + hasil_sebelumnya
			} else {
				var hasil_gabung = hasil
			}

			var jawaban = {
				"kategori_id": soal_ujian.submateri[payload].id_kategori_submateri,
				"durasi_ujian": hasil_gabung,
				"jawaban": uniq
			}

			var submisi = JSON.parse(window.localStorage.getItem('submisi'))
			if (submisi === null) {
				window.localStorage.setItem('submisi', JSON.stringify([jawaban]))					
			} else {
				submisi[payload.id] = jawaban
				window.localStorage.setItem('submisi', JSON.stringify(submisi))					
			}

			var form = new FormData()
			form.append("ujian_id", info_ujian.id)
			form.append("detail_id", info_ujian.detail_id)
			form.append("submisi", JSON.stringify(submisi))
			postDataJawaban2 (form)
				.then(res => {
					if (res.status === 201) {							
						var completed = {
							completed: 1
						}
						postStatusCompleted (info_ujian.detail_id, completed)
							.then(res => {
								if (res.status === 200) {
									context.dispatch('updateIsLoadingUploadJawaban', false)																		
									context.commit('updateStatusUploadJawaban', true)																		
								}
							})
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoadingUploadJawaban', false)
					context.commit('updateStatusUploadJawaban', false)
					var completed = {
						completed: 1
					}
					postStatusCompleted (info_ujian.detail_id, completed)
							.then(res => {
								if (res.status === 200) {
									context.dispatch('updateIsLoadingUploadJawaban', false)
									context.commit('updateStatusUploadJawaban', true)
								}
							})
				})
		},
		pauseUjian (context, payload) {
			context.dispatch('saveDurasiSubmateri', payload)
			router.push({ name: 'tes' })
		}
	}
}

export default dataSoal