<template>
	<div>
		<v-toolbar class="px-9 bb mb-7">
			<v-toolbar-title>
				<img src="@/assets/logo.png" alt="logo" class="logo" height="40px">
			</v-toolbar-title>
			<v-spacer></v-spacer>
			<v-btn
				depressed
				color="success"
				@click="$router.push({name: 'analisis'})">
				selesai
			</v-btn>
		</v-toolbar>

		<Pertanyaan/>
		<NomorSoal/>
	</div>
</template>

<script>
	import Pertanyaan from '@/components/Pembahasan/Pertanyaan'
	import NomorSoal from '@/components/Pembahasan/NomorSoal'
	export default{
		components: {
			NomorSoal,
			Pertanyaan
		},
		created () {
			var id = parseInt(this.$route.params.id)
			var data = JSON.parse(window.localStorage.getItem('paramAssessment'))
			data.id_kategori_submateri = id
			
			this.$store.dispatch('dataAssessment/getDataPembahasan', data)
			this.$store.dispatch('dataAssessment/updateIsLoading', true)

			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			var idSoal = window.localStorage.getItem('id_paket_soal')
			var dataPaket = {
				id_peserta: peserta.id,
				id_paket_soal: idSoal
			}
			this.$store.dispatch('dataSoal/getDataPaketSoal', dataPaket)
			this.$store.dispatch('dataDashboard/getDataDashboard', peserta.id)			
		}
	}
</script>