<template>
	<v-card style="border-radius: 3px 3px 0 0 !important">
		<v-card-title>
			<v-icon class="mr-3">mdi-calendar</v-icon>
			{{ tgl }}
		</v-card-title>
		<v-divider></v-divider>
		<v-card-text class="black--text">
			<Notif :status="status" :msg="msg" @visible="visible" />					
			<v-row>
				<v-col cols="12" sm="6">
					<div class="title">{{ item.nama_paket }}</div>
					<div class="subtitle-1">{{ item.kode_pembayaran }}</div>
				</v-col>
				<v-col cols="12" sm="6">
					<div class="subtitle-1">Total Pembayaran</div>
					<div class="title font-weight-bold orange--text">
						{{Intl.NumberFormat('id-ID', { maximumSignificantDigits: 4, style: 'currency', currency: 'IDR' }).format(item.biaya)}}
					</div>
				</v-col>
			</v-row>
		</v-card-text>
		<v-divider></v-divider>
		<v-card-text class="py-0">
			<v-row>
				<v-col cols="12" sm="6" style="border-right: 1px solid silver;">
					<div class="subtitle-1">Metode Pembayaran</div>
					<div 
						class="subtitle-2 black--text" 
						v-if="item.bank_kode === null">
						{{ item.mitra_kode }}
					</div>
					<div 
						class="subtitle-2 black--text" 
						v-else>
						{{ item.bank_nm }}
					</div>
				</v-col>
				<v-col cols="12" sm="6">
					<div class="subtitle-1">Batas Waktu Pembayaran</div>
					<div v-if="item.status === 0" class="subtitle-2 red--text">
						<!-- {{ setStartTime(i) }} -->
						{{time.h}} Jam : {{time.m}} Menit : {{time.s}} Detik
					</div>	
					<div v-else-if="item.status === 1" class="subtitle-2 red--text">
						0 Jam : 0 Menit : 0 Detik
					</div>
				</v-col>
			</v-row>
		</v-card-text>
		<v-divider></v-divider>
		<v-card-actions class="px-4">
			<div v-if="item.mitra_no === null">
				<div class="title">Virtual Akun</div>
				<!-- <div class="subtitle-1">999990988080</div> -->
				<div class="subtitle-1">{{ item.bank_no }}</div>
			</div>
			<div v-else>
				<div class="title">Nomor Pembayaran</div>
				<!-- <div class="subtitle-1">999990988080</div> -->
				<div class="subtitle-1">{{ item.mitra_no }}</div>
			</div>
			<v-spacer></v-spacer>
			<v-btn 									
				v-if="item.status === 0"
				depressed
				color="orange white--text"
				:disabled="time.h === 0?true:false"
				:loading="isLoading">
				<span v-if="time.h === 0">waktu habis</span>
				<span v-else>Menunggu pembayaran</span>
			</v-btn>
			<v-btn 
				v-else-if="item.status === 1"
				depressed
				color="success">
				Berhasil
			</v-btn>
		</v-card-actions>
	</v-card>
</template>

<script>
	import { mapState } from 'vuex'
	import moment from 'moment'
	import Notif from '@/components/Notif'

// @click="konfirmasi(item.biaya, item.kode_pembayaran, item.kode_transaksi)"
	export default {
		props: ['item'],
		data: () => ({
			time: {
				h: 0,
				m: 0,
				s: 0
			},
			actualTime: moment().format('X'),
			waktu1: 0,
			msg: {
        success: 'Konfirasi berhasil selamat mengerjakan',
        error: 'Maaf konfirmasi anda gagal',
        visible: false
      },
      statusTransaksi: 0
		}),

		components: {
			Notif
		},

		created () {
			this.compute()
			this.setStartTime()
			this.addOneSecondToActualTimeEverySecond()
		},

		computed: {		
			...mapState('dataPayment', ['status', 'isLoading']),
			tgl () {
				return moment(this.item.waktu_pesan).format('DD-MM-YYYY')
			},
		},

		methods: {
			addOneSecondToActualTimeEverySecond () {
		      var component = this
		      component.actualTime = moment().format('X')
		      setTimeout(function(){
		      		component.getDiffInSeconds()
		      		component.compute()
		          component.addOneSecondToActualTimeEverySecond()
		      }, 1000);
		  },
		  getDiffInSeconds () {
		    return this.waktu1 - this.actualTime
		  },
		  setStartTime () {
		  // 	var date = this.item.filter((e, index) => {
				// 	if (index === i) {
				// 		return e
				// 	}
				// })
		  	// console.log(i)
				// return moment(date[0].batas_waktu).format('DD-MM-YYYY')
				this.waktu1 = moment(this.item.batas_waktu).format('X')
		  },
		  compute () {
		    var duration = moment.duration(this.getDiffInSeconds(), "seconds")
		    // console.log(duration)
		    if (duration > 0) {
		    	this.time.h = duration.hours() < 10 ? '0'+duration.hours() : duration.hours()
			    this.time.m = duration.minutes() < 10 ? '0'+duration.minutes() : duration.minutes()
			    this.time.s = duration.seconds() < 10 ? '0'+duration.seconds() : duration.seconds()
		    }
		  },						
			konfirmasi (biaya, noRef, transId) {
				var data = {
					referenceNo: noRef,
					biaya: biaya,
					trans_id: transId
				}

				this.$store.dispatch('dataPayment/postCekPayment', data)
				this.$store.dispatch('dataPayment/updateIsLoading', true)
				this.msg.visible = true
			},
			visible (val) {
        this.msg.visible = val
      }
		}
	}
</script>