<template>
	<div>
		<v-card>
		<v-card-title>
			<v-list class="mt-0" style="width: 100%">
				<v-list-item>
          <v-list-item-content>
          	<div class="d-flex justify-space-between">
          		<div>{{ dataProgress.kelompok_uji }}</div>
          		<div>{{ dataProgress.current }}%</div>
          	</div>
          	<v-progress-linear :value="dataProgress.current" height="10"></v-progress-linear>
          </v-list-item-content>
        </v-list-item>
			</v-list>
		</v-card-title>
	</v-card>
  <Loader type="table" v-if="isLoading" />
	<v-simple-table v-else class="elevation-6 mt-6">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">Paket</th>
          <th class="text-center" v-for="(val, i) in materiUji" :key="i">
            {{val}}
          </th>
          <th class="text-center">Report</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, i) in dataProgressPaket" :key="i">
          <td>{{ item.nama }}</td>
          <td class="text-center" v-for="(val, i) in materiUji" :key="i">
          	<v-icon 
              color="success" 
              v-if="item[val] === true">
              mdi-check-circle
            </v-icon>
          	<v-icon 
              color="red" 
              v-else-if="item[val] === false">
              mdi-close-circle
            </v-icon>
            <v-icon 
              v-else>
              mdi-minus-circle
            </v-icon>
          </td>
          <td>
          	<v-btn 
              color="primary" 
              small 
              depressed
              @click="result(item.id_paket_soal)"
              :disabled="disable(item.nama)">
          		<v-icon small class="mr-3">mdi-file-outline</v-icon>
          		result
          	</v-btn>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import { flattenDeep, findIndex } from 'lodash'
  import Loader from '@/components/Loader'

	export default {
		data: () => ({}),

    components: {
      Loader
    },

		computed: {
			...mapState('dataDashboard', ['dataDashboard', 'isLoading']),		
			dataProgress () {
        var data = {}
				this.dataDashboard.map(e => {
          data = {
            kelompok_uji: e.progress_tryout.summary.kelompok_uji,
            current: e.progress_tryout.summary.stats.progress.current
          }
        })

        return data
			},
      materiUji () {
        // var materi_uji = ['Tes Potensi Skolastik (TPS)', 'Tes Kompetensi Akademik (TKA SAINTEK)', 'Tes Kompetensi Akademik (TKA SOSHUM)']
        var materi_uji = ['Tes Potensi Skolastik (TPS)']
        // this.dataDashboard.map(e => {
        //   e.progress_tryout.detail.map(val => {
        //     val.subpaket.paket_soal.map(data => {
        //       if (materi_uji.length <= data.materi_uji.length) {
        //         materi_uji = data.materi_uji
        //       }
        //     })
        //   })
        // })
        return materi_uji
      },
      dataProgressPaket () {
        var dataPaket = []
        this.dataDashboard.map(e => {
          e.progress_tryout.detail.map((val, index) => {
            val.subpaket.paket_soal.map((data, i) => {
              var obj = {}
              obj.id_paket_soal = data.id
              obj.nama = data.nama

              data.materi_uji.map((d, j) => {
                obj[d.nama] = d.completed
              })

              // dataPaket[index] = obj
              dataPaket.push(obj)
            })
          })
        })

        return dataPaket
      }
		},

    methods: {
      disable (nama) {
        var hasil = null
        this.dataDashboard.map(e => {
          e.progress_tryout.detail.map((val, index) => {
            var data = val.subpaket.paket_soal.filter(paket => paket.nama === nama)
            // console.log(data)
            if (data.length > 0) {
              hasil = findIndex(data[0].materi_uji, (com) => com.completed === false)
            }
          })
        })

        if (hasil !== -1) {
          return true
        } else {
          return false
        }
      },
      result (id_paket_soal) {
        window.localStorage.setItem('id_paket_soal', id_paket_soal)        
        window.localStorage.setItem('id_paket_soal_pembahasan', id_paket_soal)
        this.$router.push({name: 'analisis'})
      }
    }
	}
</script>