import { getDataPTN, getDataProdi, cekPilihanPTN } from '@/config/services'

var dataPTN = {
	namespaced: true,
	state: {
		isLoading: false,
		status: null,
		dataPTN: [],
		dataProdi: [],
		dataProdi2: [],
		pilihanPTN: []
	},
	mutations: {
		updateDataPTN (state, payload) {
			state.dataPTN = payload
		},
		updateDataProdi (state, payload) {
			state.dataProdi = payload
		},
		updateDataProdi2 (state, payload) {
			state.dataProdi2 = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updatePilihanPTN (state, payload) {
			state.pilihanPTN.push(payload)
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateStatus (context, payload) {
			context.commit('updateStatus', payload)
		},
		updateDataProdi (context, payload) {
			context.commit('updateDataProdi', payload)
		},
		getDataPTN (context, payload) {
			getDataPTN (payload)
				.then(res => {
					if (res.status === 200) {						
						context.commit('updateDataPTN', res.data)
						context.dispatch('updateIsLoading', false)
						context.dispatch('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		cekPilihanPTN (context, payload) {
			cekPilihanPTN (payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updatePilihanPTN', res.data.data)
						context.dispatch('updateIsLoading', false)
						context.dispatch('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		getDataProdi (context, payload) {
			getDataProdi (payload)
				.then(res => {
					if (res.status === 200) {						
						context.commit('updateDataProdi', res.data)
						context.dispatch('updateIsLoading', false)
						context.dispatch('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		getDataProdi2 (context, payload) {
			getDataProdi (payload)
				.then(res => {
					if (res.status === 200) {						
						context.commit('updateDataProdi2', res.data)
						context.dispatch('updateIsLoading', false)
						context.dispatch('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataPTN