<template>
	<div>
		<v-card>
			<v-card-title>
				<div class="subtitle-1">Batas Waktu Pembayaran</div>
				<v-spacer></v-spacer>
				<v-btn outlined color="red">
					{{time.h}} Jam : {{time.m}} Menit : {{time.s}} Detik
				</v-btn>
			</v-card-title>

			<v-card-text class="black--text d-flex">
				<v-row>
					<v-col cols="12" md="4" sm="12">
						<p>Transfer Ke Virtual Account</p>
						<img :src="require('@/assets/metode/'+dataMetode.image)" alt="logo" />
					</v-col>
					<v-col cols="12" md="8" sm="12">						
						<div>Nomor Virtual Account</div>
						<div class="title mb-3" v-if="status">
							{{detailPayment.vacctNo}}
						</div>
						<div class="mb-3" v-else>
							No virtual account akan otomatis muncul ketika 
							sudah konfirmasi
						</div>
    				<Notif :status="status" :msg="msg" @visible="visible" />						
						<!-- <div>
							<div class="mb-2">Cara Pembayaran</div>
							<span v-html="dataMetode.deskripsi">
							</span>
						</div> -->
					</v-col>
				</v-row>
			</v-card-text>
		</v-card>

		<CaraPembayaran :atm="dataMetode.deskripsi" :internet="dataMetode.deskripsi_2" :mobile="dataMetode.deskripsi_3" />

		<div class="pt-4 d-flex">
			<v-spacer></v-spacer>			
	    <v-btn 
	    	v-if="status"
	    	color="primary" 
	    	depressed class="ml-3" 
	    	@click="selesai">
	    	selesai
	    </v-btn>
			<span v-else>
				<v-btn color="primary" text @click="next('kembali')">
		    	kembali
		    </v-btn>
				<v-btn 
					color="primary" 
					:loading="isLoading" 
					depressed class="ml-3" 
					@click="konfirmasi">
		    	simpan
		    </v-btn>
			</span>
		</div>
	</div>
</template>


<script>
	import moment from 'moment'
	import {mapState} from 'vuex'
	import Notif from '@/components/Notif'
	import CaraPembayaran from './CaraPembayaran'
	export default {
		props: ['dataPayment'],
		data: () => ({
			time: {
				h: 0,
				m: 0,
				s: 0
			},
			actualTime: moment().format('X'),
			waktu1: 0,
			msg: {
        success: 'Transaksi berhasil silahkan bayar sebelum batas waktunya',
        error: 'Maaf transaksi gagal coba ulangi lagi dari awal',
        visible: false
      },
		}),
		components: {
			Notif,
			CaraPembayaran
		},
		computed: {
			...mapState('dataPayment', ['dataMetode', 'isLoading', 'status', 'detailPayment'])
		},
		mounted () {
			this.setStartTime()	
			this.compute()
			this.addOneSecondToActualTimeEverySecond()
		},
		methods: {
			addOneSecondToActualTimeEverySecond () {
		      var component = this
		      component.actualTime = moment().format('X')
		      setTimeout(function(){
		      		component.getDiffInSeconds()
		      		component.compute()
		          component.addOneSecondToActualTimeEverySecond()
		      }, 1000);
		  },
		  getDiffInSeconds () {
		    return this.waktu1 - this.actualTime
		  },
		  setStartTime () {
		  	var detailPaketSoal = JSON.parse(window.localStorage.getItem('detailPaketSoal'))
		  	var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))

		  	var time = moment().add(1, 'd').format('YYYY-MM-DD hh:mm:ss')
				this.waktu1 = moment(time).format('X')
		  },
		  compute () {
		    var duration = moment.duration(this.getDiffInSeconds(), "seconds")
		    if (duration > 0) {
		    	this.time.h = duration.hours() < 10 ? '0'+duration.hours() : duration.hours()
			    this.time.m = duration.minutes() < 10 ? '0'+duration.minutes() : duration.minutes()
			    this.time.s = duration.seconds() < 10 ? '0'+duration.seconds() : duration.seconds()
		    }
		  },
			next (jenis) {				
				if (jenis === 'kembali') {
					this.step = 2
				}
				this.$emit('next', this.step)
			},
			konfirmasi () {
				var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
				var date = moment()
				var data = {
					peserta_id: peserta.id,
					subpaket_tryout: this.dataPayment.paket.id_subpaket,
					metode_pembayaran: this.dataMetode.metode_pembayaran,
					biaya: this.dataPayment.paket.biaya,
					kode_pembayaran: this.dataPayment.kode_pembayaran,
					batas_waktu: date.add(1, 'd').format('YYYY-MM-DD hh:mm:ss'),
					created_at: moment().format('YYYY-MM-DD hh:mm:ss'),
					updated_at: moment().format('YYYY-MM-DD hh:mm:ss'),
					bank_code: this.dataMetode.kode
				}
				// console.log(data)
				this.$store.dispatch('dataPayment/postDataPayment', data)
				this.$store.dispatch('dataPayment/updateIsLoading', true)
        this.msg.visible = true				
			},
			visible (val) {
        this.msg.visible = val
      },
      selesai () {
      	this.$router.push({name: 'riwayat'})
				this.$store.dispatch('dataPayment/updateStatus', null)      	
      }
		}
	}
</script>