<template>
	<div class="white" style="padding-top: 56px;">
		<Navbar/>
		<!--  -->
		<section 
			class="banner d-none d-md-flex" 
			:style="`background-image: url(${require('@/assets/banner.webp')})`">		
		</section>
		<!--  -->
		<v-container>
			<v-row>
				<v-col cols="12" md="8">
					<Menu/>
					<SelamatDatang />
				</v-col>
				<v-col cols="12" md="4" style="position: relative;">
					<Profil/>
				</v-col>
			</v-row>
		</v-container>
		<!--  -->
		<v-container>
			<v-btn color="purple" dark x-large depressed tile class="mb-4">
				PAKET TRYOUT
				<v-icon class="ml-3">mdi-information</v-icon>
			</v-btn>
			<v-row>
				<v-col cols="12" md="8">
					<PaketTryout/>
				</v-col>
				<v-col cols="12" md="4">
					<Konfirmasi/>
				</v-col>
			</v-row>
		</v-container>
		<!--  -->
		<v-container>
			<div 
				class="d-sm-flex pa-4 white--text text-left justify-center"
				:style="`background-image: url(${require('@/assets/purple.png')})`"
				style="background-size: cover;font-size: 15px;">
				<template v-if="paket === -1">
					<div class="mr-md-12">
						Dapatkan fasilitas <i>SIMULASI RASIONALISASI UTBK</i> hanya teruntuk kamu sobat utbk yang membeli fasilitas paket premium (Silver, Gold, Platinum) Bandingkan nilai akhir kamu dengan standar nilai PTN di seluruh Indonesia.
					</div>
					<v-btn depressed style="background-color: rgba(255,255,255,0.5);" @click="$router.push({name: 'simulasi'})">
						SIMULASI RASIONALISASI UTBK
					</v-btn>
				</template>	
				<template v-else>
					<div class="mr-md-12">
						Kamu adalah peserta yang hebat, Silahkan klik simulasi rangking untuk mendapatkan review nilai akhir kamu dengan standar nilai PTN di seluruh indonesia. Selamat mencoba ya sobat.
					</div>
					<v-btn depressed style="background-color: rgba(255,255,255,0.5);" @click="$router.push({name: 'simulasi'})">
						SIMULASI RASIONALISASI UTBK
					</v-btn>
				</template>							
			</div>
		</v-container>
		<!--  -->
		<v-container>			
			<v-row>
				<v-col cols="12" md="8">
					<v-btn color="success" dark x-large depressed tile class="mb-4">
						PROGRESS TRYOUT
						<v-icon class="ml-3">mdi-information</v-icon>
					</v-btn>
					<Progress/>
				</v-col>
				<v-col cols="12" md="4">
					<v-btn color="teal" dark x-large depressed block tile class="mb-4">
						10 PESERTA TES TERBAIK
						<v-icon class="ml-3">mdi-information</v-icon>
					</v-btn>
					<Terbaik/>
				</v-col>
			</v-row>
		</v-container>
		<!--  -->
		<v-container class="mb-12">
			<v-btn color="blue" dark x-large depressed tile class="mb-4">
				BONUS & FASILITAS
				<v-icon class="ml-3">mdi-information</v-icon>
			</v-btn>
			<Fasilitas/>
		</v-container>
		<!--  -->
		<Footer/>
	</div>
</template>

<style scoped>
	.banner{
		width: 100%;
		height: 200px;
		background-size: cover;
		background-position: center;
	}
</style>

<script>
	import { mapState } from 'vuex'
	import { flattenDeep, findIndex } from 'lodash'
	import Navbar from '@/components/Navbar'
	import SelamatDatang from '@/components/Dashboard/SelamatDatang'
	import Menu from '@/components/Dashboard/Menu'
	import PaketTryout from '@/components/Dashboard/PaketTryout'
	import Profil from '@/components/Dashboard/Profil'
	import Konfirmasi from '@/components/Dashboard/Konfirmasi'
	import Progress from '@/components/Dashboard/Progress'
	import Terbaik from '@/components/Dashboard/Terbaik'
	import Fasilitas from '@/components/Dashboard/Fasilitas'
	import Footer from '@/components/Footer'
	export default {
		components: {
			Footer,
			Fasilitas,
			Terbaik,
			Progress,
			Konfirmasi,
			Profil,
			PaketTryout,
			Menu,
			SelamatDatang,
			Navbar,
		},

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			var data = {
				peserta_id: peserta.id
			}
			this.$store.dispatch('dataAssessment/postDataRiwayat', data)
			this.$store.dispatch('dataDashboard/getDataDashboard', peserta.id)
		},

		computed: {
			...mapState('dataDashboard', ['dataDashboard']),
			jumlahPaket () {
        var jadi = this.dataDashboard.map(paket => {
          return paket.paket.map(data => {
            return data
          })
        })

        return flattenDeep(jadi)
      },
			paket () {
				var paketPremium = this.jumlahPaket.filter(e => {
          // console.log(e.status)
          if (e.nama_paket.indexOf('FREE') === -1) {
            return e
          }
        })
        var hasil = findIndex(paketPremium, o => o.status === true)
        return hasil
			}
		}
	}
</script>