<template>
	<div>
		<div class="ml-12 mb-3">
			<div class="d-flex justify-space-between">
				<div>
					<span class="mr-7">Soal No.{{page}}</span>
				</div>
				<b class="d-none d-md-block">
					Pembahasan - {{ nama }}
				</b>
				<div>&nbsp;</div>
			</div>
			<b class="d-md-none">
				{{ nama }}
			</b>
		</div>
		<v-card flat class="mx-12 ba pa-7 scroll" max-height="65vh" height="65vh">
			<v-skeleton-loader 
				v-if="isLoading"
				type="paragraph"
			></v-skeleton-loader>
			<template v-else-if="dataPembahasan.length === 0">
				<v-card-text class="title">
					Maaf pembahasan belum ada
				</v-card-text>
			</template>
			<template v-else v-for="(pembahasan, index) in dataPembahasan">
				<span :key="index" v-if="index+1 === page">
					<v-card-text class="black--text">
						<div v-html="pembahasan.pertanyaan"></div>
					</v-card-text>
					<v-card-actions>
						<Jawaban :jawaban="pembahasan.jawaban" :terjawab="pembahasan.jawaban_peserta" />
					</v-card-actions>
					<v-card-text class="black--text">
						<p class="title">Pembahasan</p>
						<div v-html="pembahasan.pembahasan"></div>
					</v-card-text>
				</span>
			</template>		
		</v-card>
	</div>
</template>

<style scoped>
	.ba{ box-shadow: 0 0 0 1px #cfcfcf; }
	.scroll{ overflow: auto; }
	@media only screen and (max-width: 366px) {
		.v-application .mx-12{
			margin-right: 8px !important;
    	margin-left: 8px !important;
		}
		.v-application .pa-7{
			padding: 8px !important;
		}
	}
</style>

<script>
	import { mapState } from 'vuex'
	import Jawaban from './Jawaban'

	export default{
		data: () => ({}),
		components: {
			Jawaban
		},
		computed: {
			...mapState('dataAssessment', ['dataPembahasan', 'page', 'isLoading']),
			...mapState('dataSoal', ['dataPaketSoal']),
			nama () {
				var id = parseInt(this.$route.params.id)
				var hasil = []
				this.dataPaketSoal.map(data => {
					return data.submateri.map((sub, index, self) => {
						if (sub.id_kategori_submateri === id) {
							hasil = sub.nama
						}
					})
				})

				return hasil
			}
		}
	}
</script>