<template>
	<v-card>
		<v-card-title>
			Kartu Kredit atau Debit
		</v-card-title>

		<v-card-text class="black--text d-flex">
			<v-form v-model="valid" ref="form">
				<v-row>
					<v-col cols="12" md="12" sm="12">
							<v-text-field
								label="Nomor Kartu"
								dense
								outlined
							></v-text-field>
							<div class="d-flex">
								<span>Masa Berlaku</span>
								<v-text-field
									label="mm"
									dense
									outlined
									class="mx-4"
								></v-text-field>
								<v-text-field
									label="yy"
									dense
									outlined
								></v-text-field>
							</div>
							<v-text-field
								label="CVV"
								dense
								outlined
							></v-text-field>
					</v-col>
					<!-- <v-col cols="12" md="4" class="d-none d-md-flex">
						<img src="@/assets/atm.png" alt="atm" width="100%" />
					</v-col> -->
				</v-row>
				<div class="d-flex">
					<v-btn text color="primary" class="mr-3" @click="metode">
						kembali
					</v-btn>
					<v-btn 
						color="primary" 
						depressed :disabled="!valid" 
						@click.prevent="validate">
						simpan dan lanjutkan
					</v-btn>
				</div>
			</v-form>		
		</v-card-text>
	</v-card>
</template>

<script>
	export default {
		data: () => ({
			valid: true
		}),
		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					this.$emit('next', 3)
				}
			},
			metode () {
				this.$emit('kartu', false)
			}
		}
	}
</script>