<template>
	<v-row class="my-6">
		<v-col cols="12" md="4" sm="12">
			<div class="text-center">
				<div class="subtitle-2 font-weight-bold">NILAI UTBK ANDA</div>
				<div class="orange--text display-3 font-weight-bold">
					{{dataRanking.nilai_utbk.skor_akhir}}
				</div>
			</div>
		</v-col>
		<v-col cols="12" md="8" sm="12">
			<table>
				<tr v-for="(item, index) in desc" :key="index">
					<td class="text-capitalize title d-none d-md-flex">{{item.title}}</td>
					<td class="pl-6">
						<div class="text-capitalize d-md-none d-sm-flex">{{item.title}}</div>
						<div class="text-uppercase title">{{item.text}}</div>
					</td>
				</tr>
			</table>
		</v-col>
	</v-row>
</template>

<script>
	import {mapState} from 'vuex'

	export default {
		computed: {
			...mapState('dataRanking', ['dataRanking']),
			desc () {
				var data = [
					{ title: 'perguruan tinggi', text: this.dataRanking.ptn.nama },	
					{ title: 'program studi', text: this.dataRanking.ptn.prodi },
					{ title: 'kelompok ujian', text: this.dataRanking.nilai_utbk.kelompok }
				]

				return data
			}
		}
	}
</script>