<template>
	<div>
		<v-alert type="info" v-if="paket === -1">			
			Dapatkan fasilitas premium dengan kelengkapan fitur yang akan membantu sobat untuk mengikuti tes dan review kemampuan.
		</v-alert>
		<v-alert class="text-center" color="red" dark v-else-if="dataRiwayatStatus.length > 0 && time.h > 0">
			<v-btn 
				@click="$router.push({name: 'riwayat'})" 
				rounded outlined 
				color="white"
				class="mb-3">
				konfirmasi pembayaran
			</v-btn>
			<p>
				Ayo segera selesaikan pembayarannya sebelum waktunya habis
			</p>
			<div class="title">
				{{ setStartTime(0) }}
				{{time.h}} Jam : {{time.m}} Menit : {{time.s}} Detik				
			</div>
		</v-alert>
		<v-alert type="info" v-else-if="time.h < 0">
			Maaf batas waktu untuk transaksi sudah habis, silahkan
			melakukan transaksi lagi.
		</v-alert>
		<v-alert type="success" v-else>			
			Selamat mengikuti tes kamu adalah peserta terhebat yang akan menuai kemenangan seusai berjuang. Selamat mencoba ya sobat.
		</v-alert>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import moment from 'moment'
	import { flattenDeep, findIndex } from 'lodash'

	export default {
		data: () => ({
			time: {
				h: 0,
				m: 0,
				s: 0
			},
			actualTime: moment().format('X'),
			waktu1: 0,
			expired: false
		}),

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			var data = {
				peserta_id: peserta.id
			}
			this.$store.dispatch('dataAssessment/postDataRiwayat', data)
			this.$store.dispatch('dataAssessment/updateIsLoading', true)
			// this.setStartTime()	
			this.compute()
			this.addOneSecondToActualTimeEverySecond()
		},

		computed: {
			...mapState('dataAssessment', ['dataRiwayat']),
			...mapState('dataDashboard', ['dataDashboard']),		
			jumlahPaket () {
				var jadi = this.dataDashboard.map(paket => {
					return paket.paket.map(data => {
						return data
					})
				})

				return flattenDeep(jadi)
			},
			paket () {
				var paketPremium = this.jumlahPaket.filter(e => {
          // console.log(e.status)
          if (e.nama_paket.indexOf('FREE') === -1) {
            return e
          }
        })
        var hasil = findIndex(paketPremium, o => o.status === true)
        return hasil
			},
			dataRiwayatStatus () {
				return this.dataRiwayat.filter(e => e.status === 0)
			}		
		},

		methods: {
			addOneSecondToActualTimeEverySecond () {
		      var component = this
		      component.actualTime = moment().format('X')
		      setTimeout(function(){
		      		component.getDiffInSeconds()
		      		component.compute()
		          component.addOneSecondToActualTimeEverySecond()
		      }, 1000);
		  },
		  getDiffInSeconds () {
		    return this.waktu1 - this.actualTime
		  },
		  setStartTime (i) {
		  	var date = this.dataRiwayatStatus.filter((e, index) => {
					if (index === i) {
						return e
					}
				})

				// return moment(date[0].batas_waktu).format('DD-MM-YYYY')
				this.waktu1 = moment(date[0].batas_waktu).format('X')
		  },
		  compute () {
		    var duration = moment.duration(this.getDiffInSeconds(), "seconds")
		    if (duration > 0) {
		    	this.time.h = duration.hours() < 10 ? '0'+duration.hours() : duration.hours()
			    this.time.m = duration.minutes() < 10 ? '0'+duration.minutes() : duration.minutes()
			    this.time.s = duration.seconds() < 10 ? '0'+duration.seconds() : duration.seconds()
		    } else {
		    	this.expired = true
		    }
		  }
		}
	}
</script>