import { 
	postDataLogin, 
	postDataDaftar,
	postCekAkun
} from '@/config/services'
import router from '@/router/index'

var dataLogin = {
	namespaced: true,
	state: {
		isLoading: false,
		status: null,
		statusCekNomor: null
	},
	mutations: {
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateStatusCekNomor (state, payload) {
			state.statusCekNomor = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		postDataLogin (context, payload) {
			postDataLogin (payload)
				.then(res => {
					if (res.data.status === 200) {
						window.localStorage.setItem('dataPeserta', JSON.stringify(res.data.data))
						window.localStorage.setItem('token', res.data.data.token)
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						// router.push({name: 'dashboard'})
						window.location.href="/peserta"
					} else {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', false)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		postDataDaftar (context, payload) {
			var data = {
				nohp: payload.nohp
			}
			postCekAkun(data)
				.then(res => {
					if (res.status === 200) {
						if (res.data.tersedia) {
							postDataDaftar (payload)
								.then(res => {
									if (res.status === 201) {
										window.localStorage.setItem('dataPeserta', JSON.stringify(res.data.data))
										window.localStorage.setItem('token', res.data.data.token)
										context.dispatch('updateIsLoading', false)
										context.commit('updateStatus', true)
										// router.push({name: 'dashboard'})
										window.location.href="/peserta"										
									} else {
										context.dispatch('updateIsLoading', false)
										context.commit('updateStatus', false)
									}
								})
								.catch(err => {
									context.dispatch('updateIsLoading', false)
									context.commit('updateStatus', false)
								})
						} else {
							context.dispatch('updateIsLoading', false)
							context.commit('updateStatusCekNomor', false)
						}
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataLogin