<template>
	<v-form v-model="valid" ref="form" class="pa-3">
    <Notif :status="status" :msg="msg" @visible="visible" />
		<v-text-field
      outlined
      v-model="dataLogin.nohp"      
      label="Username (No. Hp atau E-mail)"
      :rules="hpRule"
      counter
      prepend-inner-icon="mdi-cellphone"      
    ></v-text-field>
    <v-text-field
      outlined
      v-model="dataLogin.password"
      label="Password"
      counter
      :rules="passwordRule"
      :append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
      :type="showpassword ? 'text' : 'password'"
      @click:append="showpassword = !showpassword"
      prepend-inner-icon="mdi-lock"      
    ></v-text-field>
    <div class="d-flex mb-4">
    	<v-spacer></v-spacer>
    	<a href="javascript:void(0)" style="text-decoration: none;">Lupa Password ?</a>
    </div>
    <v-btn 
      color="orange" 
      class="white--text"
      block depressed
      @click.prevent="validate"
      :disabled="!valid"
      :loading="isLoading"
      type="submit">
			login
		</v-btn>
		<v-btn 
      color="orange" 
      @click="$router.push({ name: 'signup' })" 
      outlined block class="mt-4">
			daftar sekarang
		</v-btn>
	</v-form>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

	export default{
		data: () => ({
      valid: true,
      dataLogin: {
        nohp: '',
        password: ''
      },
      msg: {
        success: 'Login berhasil',
        error: 'Login gagal',
        visible: false
      },
			showpassword: false,			
      requiredRule: [ v => !!v || 'Tidak boleh kosong' ]    
		}),

    components: {
      Notif
    },

    computed: {
      ...mapState('dataLogin', ['status', 'isLoading']),
      hpRule () {
        return [
          v => !!v.match(/\d+$/g) || 'No handphone harus angka',
          v => (v && v.length >= 10) || 'No lebih dari 11 nomor',
          ...this.requiredRule,
        ]
      },
      passwordRule () {
        return [
          ...this.requiredRule,
          v => (v && v.length >= 6) || 'Password lebih dari 6 karakter',
        ]
      },
      // kode () {
      //   return JSON.parse(window.localStorage.getItem('kode_afiliasi'))
      // }
    },

    methods: {
      validate() {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataLogin/postDataLogin', this.dataLogin)
          this.$store.dispatch('dataLogin/updateIsLoading', true)
          this.msg.visible = true
        }      
      },
      visible (val) {
        this.msg.visible = val
      }
    },
	}
</script>