<template>
	<v-row>
		<template v-if="dataPaket.length === 0">
			<v-col cols="12" md="6">
				<Loader type="card" />
			</v-col>
		</template>
		<v-col v-else cols="12" md="6" v-for="(item, index) in dataPaket" :key="index">
			<div>
				<div style="position: relative;">
					<img :src="require('@/assets/'+item.img)" alt="free" width="100%" />
					<div class="text-paket">
						<p class="font-weight-bold display-1 text-uppercase">
							{{item.name}}
						</p>
						<!-- 4/25 Sudah Dikerjakan -->
					</div>
				</div>
				<div class="d-flex">
					<v-spacer></v-spacer>
					<v-btn 
						depressed 
						:disabled="dataRiwayatStatus.length > 0?false:false"
						color="primary"
						@click="pesan(item.name)"
						v-if="item.status === 0">
						pesan
					</v-btn>
					<v-btn 
						depressed
						color="orange"
						dark
						v-else-if="item.status === 2">
						menunggu pembayaran
					</v-btn>
					<v-btn 
						depressed
						color="orange"
						dark
						v-else-if="item.status === 3">
						menunggu aktivasi
					</v-btn>
					<PilihPaket :idPaket="item.id" :idSubPaket="item.id_subpaket_tryout" v-else-if="item.status === 1" />
					<!-- <v-btn depressed color="success" v-else @click="$router.push({name: 'tes'})">
						mulai
					</v-btn> -->
				</div>
			</div>
		</v-col>
	</v-row>
</template>

<style scoped>
	.text-paket{
		position: absolute;
		bottom: 10px;
		right: 10px;
		color: #fff;
	}
</style>

<script>
	import { mapState } from 'vuex'
	import { flattenDeep } from 'lodash'
	import Loader from '@/components/Loader'
	import PilihPaket from '@/components/Dashboard/PilihPaket'

	export default {
		data: () => ({
			id_paket_soal: null
		}),
		components: {
			PilihPaket,
			Loader
		},
		created () {
			this.id_paket_soal = window.localStorage.getItem('id_paket_soal')			
		},
		computed: {
			...mapState('dataAssessment', ['dataRiwayat']),
			...mapState('dataDashboard', ['dataDashboard']),		
			dataPaket () {
				var paket1 = [
					{ name: 'free', status: 0, img: 'free.png' },
					{ name: 'silver', status: 0, img: 'silver.png' },
					{ name: 'gold', status: 0, img: 'gold.png' },
					{ name: 'platinum', status: 0, img: 'platinum.png' }
				]
				var jadi = this.dataDashboard.map((paket, index) => {
					paket.paket.map(val => {
						paket1.map((data, index) => {
							var status = val.nama_paket.toLowerCase().indexOf(data.name)
							if (status !== -1 && val.status === true && val.is_activated === true && val.is_paid === true) {								
								data.status = 1
								data.id = val.id_paket
								data.id_subpaket_tryout = val.subpaket.id
							} else if (status !== -1 && val.status === false && val.is_activated === false && val.is_paid === false) {
								data.status = 2
							} else if (status !== -1 && val.status === false && val.is_activated === false && val.is_paid === true) {
								data.status = 3
							}

							return data
						})
					})
					return paket1
				})

				return flattenDeep(jadi)
			},	
			dataRiwayatStatus () {
				return this.dataRiwayat.filter(e => e.status === 0)
			}
		},

		methods: {			
			pesan (name) {
				var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
				if (peserta.sekolah.tahun_kelulusan === null) {
					this.$router.push({name: 'biodata'})
				} else {
					this.$router.push({name: 'fasilitas', params: {paket: name}})
				}
			}
		}
	}
</script>