<template>
	<v-card color="grey lighten-5">
		<v-card-title class="pb-0" v-if="clicked(dataDashboard[0].bonus_fasilitas, begin, end).length !== 0">
			<v-spacer></v-spacer>
			<v-btn 
				v-if="banyak === false"
				@click="lebihBanyak"
				color="success" 
				outlined>
				Tampilkan lebih banyak
			</v-btn>
			<v-btn 
				v-else
				@click="lebihSedikit"
				color="success" 
				outlined>
				Tampilkan lebih sedikit
			</v-btn>
		</v-card-title>
		<v-card-text>
				<v-alert type="info" v-if="clicked(dataDashboard[0].bonus_fasilitas, begin, end).length === 0">
					Maaf bonus dan fasilitas untuk saat ini belum tersedia
				</v-alert>
				<v-row v-else>
					<v-col 
						cols="12" 
						md="4" 
						sm="12"
						v-for="(item, i) in clicked(dataDashboard[0].bonus_fasilitas, begin, end)"
						:key="i">
						<v-card flat>
							<v-img
		            :src="item.file_gambar"
		            class="white--text align-end"
		            height="200px">
		            <v-card-title style="background-color: rgba(0,0,0,0.5)">
		            	{{ item.nama_fasilitas }}
		            	<v-spacer></v-spacer>
				      		<v-btn icon class="white" v-if="item.has_access !== true">
				      			<v-icon color="primary">mdi-lock</v-icon>
				      		</v-btn>
				      		<a :href="item.file_url" target="_blank" v-else style="text-decoration: none;">
				      			<v-btn icon class="white">
					      			<v-icon color="primary">mdi-download</v-icon>
					      		</v-btn> 
				      		</a>          	
		            </v-card-title>
		          </v-img>
				      <!-- <v-card-title>
				      	{{ item.nama_fasilitas }}
				      	<v-icon class="mr-3" color="white">mdi-lock</v-icon>
				      </v-card-title> -->
						</v-card>
					</v-col>
				</v-row>
			<div class="text-center" v-if="clicked(dataDashboard[0].bonus_fasilitas, begin, end).length !== 0">
		    <v-pagination
		    	v-show="banyak === false"
		      v-model="page"
		      :length="jumlahPage"
		    ></v-pagination>
		  </div>
		</v-card-text>
	</v-card>
</template>

<script>
	import { mapState } from 'vuex'
	export default{
		data: () => ({
			page: 1,
			jumlahPage: 0,
			begin: 0,
			end: 3,
			tampilan: [{
				text: 'Tampilkan lebih banyak',
				banyak: true
			},{
				text: 'Tampilkan lebih sedikit',
				banyak: false
			}],
			banyak: false
		}),

		computed: {
			...mapState('dataDashboard', ['dataDashboard'])			
		},

		methods: {
			clicked(jumlah, begin, end) {
				var page = jumlah.length/3
				this.jumlahPage = Math.ceil(page)
				if (jumlah.length > 0) {
					return jumlah.slice(begin, end)
				} else {
					return []
				}
			},
			lebihBanyak () {
				this.begin = 0
				this.end = this.dataDashboard[0].bonus_fasilitas.length
				this.banyak = true
			},
			lebihSedikit () {
				this.begin = 0
				this.end = 3
				this.banyak = false
			}
		},

		watch: {
			page (val) {
				this.begin = 3 * (val-1)
				this.end = 3 * val
			}
		}
	}
</script>