<template>
	<v-card-text>
 		<Loader v-if="isLoading" type="list-item-avatar-two-line" />
 		<v-list v-else two-line class="mt-0" style="width: 100%">
 			<template v-if="dataHasil.length === 0">
 				<v-divider></v-divider>
 				<v-list-item>
          <v-list-item-content>
            <v-list-item-title>
            	Data peserta belum ada
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
 			</template>
			<template v-else v-for="(item, i) in dataHasil">
				<v-divider
          :key="i"
        ></v-divider>
				<v-list-item>
					<v-list-item-avatar class="grey lighten-3">
						{{ i+1 }}
					</v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
            	<v-tooltip bottom>
					      <template v-slot:activator="{ on }">
					      	<span v-on="on">{{ namaPeserta(i) }}</span>						        
					      </template>
					      <span>{{ item.nama_peserta }}</span>
					    </v-tooltip>	            	
            </v-list-item-title>
            <v-list-item-subtitle>
            	<!-- <v-icon color="primary" class="mr-2">mdi-home-variant-outline</v-icon> -->
            	{{ item.sekolah.nama }}
            </v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-action>
	          <b>{{ parseFloat(item.nilai_akhir).toFixed(2) }}</b>
	        </v-list-item-action>
        </v-list-item>
			</template>
		</v-list>
 	</v-card-text>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'

	export default {
		data: () => ({
			kelompok: '',
		}),

		created () {
			this.getPeringkat('SAINTEK')
		},

		components: {
			Loader
		},

		computed: {
			...mapState('dataAssessment', ['isLoading', 'dataPeringkat']),
      dataHasil () {    
      	var hasil = []    
        Object.keys(this.dataPeringkat).map((key, index) => {
        	hasil[index] = this.dataPeringkat[key]
        })
        return hasil
      }
		},

		created () {
			this.getPeringkat('saintek')
		},

		methods: {
			getPeringkat (kelompok) {
				var data = {
	        nama: '',
	        kelompok: kelompok,
	        id_ptn: 0,
	        id_prodi: 0,
	        page: 1
	      }
				this.kelompok = kelompok
				this.$store.dispatch('dataAssessment/getDataPeringkat', data)
	      this.$store.dispatch('dataAssessment/updateIsLoading', true)
			},
			namaPeserta (index) {
				var data = this.dataHasil.filter((e, i) => i === index)
				var nama = data[0].nama_peserta.split(' ')
				var hasil = ''
				nama.map(nama => {
					if (nama.length >= 3) {
						// console.log(nama)
						hasil = nama.toUpperCase()
					}
				})
				return hasil
				// if (nama[0].length >= 4) {
				// 	return nama[0].toUpperCase()
				// } else {
				// 	return nama[0].toUpperCase()					
				// }
			}
		}
	}
</script>