<template>
	<div class="assessment white">
		<Navbar class="hiddenOnPrint"/>

		<v-container>
			<PaketTryout/>
		</v-container>

		<v-container>
			<DetailHasil/>
		</v-container>

		<v-container>
			<Navigasi class="hiddenOnPrint" />
		</v-container>

		<v-container>
			<router-view></router-view>
		</v-container>

		<Footer/>
	</div>
</template>

<style>
	.assessment{
		padding-top: 56px;
	}
	@media print {
		.hiddenOnPrint {
			display: none;
		}
		.assessment{ padding-top: 0px; }
	}
</style>

<script>
	import { mapState } from 'vuex'
	import Navbar from '@/components/Navbar'
	import Footer from '@/components/Footer'
	import PaketTryout from '@/components/Assessment/PaketTryout'
	import DetailHasil from '@/components/Assessment/DetailHasil'
	import Navigasi from '@/components/Assessment/Navigasi'

	import axios from 'axios'

	export default {
		props: ['id', 'token'],

		components: {
			Navigasi,
			DetailHasil,
			PaketTryout,
			Footer,
			Navbar
		},

		created () {
			if (this.id === undefined && this.token === undefined) {
				var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
				this.$store.dispatch('dataAssessment/getDataAssessment', peserta.id)
				this.$store.dispatch('dataDashboard/getDataDashboard', peserta.id)
			} else {
				const http = axios.create({
					baseURL: 'https://app.sobatutbk.com/api/v1/',
					headers: {
						'Authorization': 'Bearer '+this.token
					}
				})
				
				this.getDataAssessment(http, this.id)
				this.getDataDashboard(http, this.id)
			}
		},

		methods: {
			getDataAssessment (http, payload) {
				http.get('peserta/'+payload+'/assessment/')
					.then(res => {
						if (res.status === 200) {
							this.$store.dispatch('dataAssessment/updateIsLoading', false)
							this.$store.commit('dataAssessment/updateStatus', true)
							this.$store.commit('dataAssessment/updateDataAssessment', res.data)

				  		var data = {
				  			id_peserta: payload,
				  			id_ujian: res.data[0].id_ujian
				  		}
							this.getDetailAssessment(http, data)
							this.postDataScoreAll(http, {id_ujian: data.id_ujian})
						}
					})
					.catch(err => {
						this.$store.dispatch('dataAssessment/updateIsLoading', false)
						this.$store.commit('dataAssessment/updateStatus', false)
					})
			},
			getDetailAssessment(http, payload) {
				http.get('peserta/'+payload.id_peserta+'/assessment/'+payload.id_ujian)
					.then(res => {
						if (res.status === 200) {
							this.$store.dispatch('dataAssessment/updateIsLoading', false)
							this.$store.commit('dataAssessment/updateStatus', true)
							this.$store.commit('dataAssessment/updateDetailAssessment', res.data.data)
						}
					})
					.catch(err => {
						this.$store.dispatch('dataAssessment/updateIsLoading', false)
						this.$store.commit('dataAssessment/updateStatus', false)
					})
			},
			postDataScoreAll(http, payload) {
				http.post('ujian/finalize-all', payload)
					.then(res => {
						if (res.status === 200) {
							this.$store.dispatch('dataAssessment/updateIsLoading', false)
							this.$store.commit('dataAssessment/updateStatus', true)
							this.$store.commit('dataAssessment/updateDataScore', res.data.skor)
						}
					})
					.catch(err => {
						this.$store.dispatch('dataAssessment/updateIsLoading', false)
						this.$store.commit('dataAssessment/updateStatus', false)
					})
			},
			getDataDashboard (http, payload) {
				http.get('peserta/'+payload+'/dashboard')
					.then(res => {
						if (res.status === 200) {
							var arr = new Array(res.data)
							this.$store.commit('dataDashboard/updateDataDashboard', arr)
							this.$store.dispatch('dataDashboard/updateIsLoading', false)
							this.$store.commit('dataDashboard/updateStatus', true)
						}
					})
					.catch(err => {
						this.$store.dispatch('dataDashboard/updateIsLoading', false)
						this.$store.commit('dataDashboard/updateStatus', false)
					})
			}
		}
	}
</script>