import {
	getDataProv,
	getDataKab,
	postDataBiodata,
	getDataSekolah
} from '@/config/services'
import router from '@/router/index'

var dataBiodata = {
	namespaced: true,
	state: {
		dataProv: [],
		dataKab: [],
		dataBiodata: {},
		status: null,
		isLoading: false,
		dataSekolah: []
	},
	mutations: {
		updateDataProv (state, payload) {
			state.dataProv = payload
		},
		updateDataKab (state, payload) {
			state.dataKab = payload
		},
		updateDataBiodata (state, payload) {
			state.dataBiodata = payload
		},
		updateDataSekolah (state, payload) {
			state.dataSekolah = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataProv (context, payload) {
			getDataProv ()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataProv', res.data.data)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataKab (context, payload) {
			getDataKab (payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKab', res.data.data)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataSekolah (context, payload) {
			getDataSekolah (payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataSekolah', res.data.data)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataBiodata (context, payload) {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			postDataBiodata (peserta.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataBiodata', res.data.data)
						window.localStorage.setItem('dataPeserta' ,JSON.stringify(res.data.data))
						context.dispatch('updateIsLoading', false)
						router.go(-1)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataBiodata