<template>
	<v-card class="mb-6">
		<v-card-title>
			Pilihan Kedua
		</v-card-title>
		<v-card-text>
			<v-row>
        <v-col cols="12" lg="12" md="12" sm="12" class="pa-1">
          <v-autocomplete
            v-model="ptn2"
            :items="dataPTN"
            item-text="universitas"
            item-value="universitas"
            :loading="isLoading"
            :rules="requiredRule"
            dense
            autocomplete="off"
            hide-no-data
            hide-selected
            class="mr-2"
            outlined
            label="Cari Perguruan tinggi"
          ></v-autocomplete>
        </v-col>
        <!--  -->
        <v-col cols="12" lg="12" md="12" sm="12" class="pa-1">
          <v-autocomplete
            v-model="studi2"
            :items="dataProdi2"
            :rules="requiredRule"
            item-text="prodi"
            autocomplete="off"
            return-object
            dense
            hide-no-data
            hide-selected
            :disabled="ptn2 === '' ? true:false"
            class="mr-2"
            outlined
            label="Cari program studi"
          ></v-autocomplete>
        </v-col>
      </v-row>
		</v-card-text>		
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'

	export default {
    props: ['kelompok'],
		data: () => ({	
      ptn2: '',
      studi2: '',
      search2: null,
      icon: 'mdi-magnify',
      requiredRule: [v => !!v || 'Tidak boleh kosong']
		}),

    computed: {
      ...mapState('dataPTN', ['status', 'isLoading', 'dataPTN', 'dataProdi2']),
    },

    watch: {
      ptn2 (val) {
        var data = {
          ptn: val,
          kelompok: this.kelompok
        }
        this.$store.dispatch('dataPTN/getDataProdi2', data)
      },
      studi2 (val) {
        val.pilihan_ke = 2
        this.$emit('prodi', val)
      }
    }
	}
</script>