import { 
	getDataAssessment, 
	getDetailAssessment,
	getDataPembahasan,
	postDataScoreAll,
	getDataPeringkat,
	postDataRiwayat
} from '@/config/services'

var dataAssessment= {
	namespaced: true,
	state: {
		dataAssessment: [],
		detailAssessment: [],
		dataPembahasan: [],
		dataScore: [],
		isLoading: false,
		status: null,
		page: 1,
		dataPeringkat: [],
		dataRiwayat: [],
		pagination: {}
	},
	mutations: {
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updatePage (state, payload) {
			state.page = payload
		},
		updateDetailAssessment(state, payload) {
			state.detailAssessment= payload
		},
		updateDataAssessment(state, payload) {
			state.dataAssessment= payload
		},
		updateDataPembahasan(state, payload) {
			state.dataPembahasan= payload
		},
		updateDataScore(state, payload) {
			state.dataScore = payload
		},
		updateDataPeringkat(state, payload) {
			state.dataPeringkat = payload
		},
		updateDataRiwayat(state, payload) {
			state.dataRiwayat = payload
		},
		updatePagination(state, payload) {
			state.pagination = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updatePage (context, payload) {
			context.commit('updatePage', payload)
		},
		getDataAssessment(context, payload) {
			getDataAssessment(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDataAssessment', res.data)

						var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			  		var data = {
			  			id_peserta: peserta.id,
			  			id_ujian: res.data[0].id_ujian
			  		}
						context.dispatch('getDetailAssessment', data)
						context.dispatch('postDataScoreAll', {id_ujian: data.id_ujian})
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		getDataPembahasan(context, payload) {
			getDataPembahasan(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDataPembahasan', res.data.data)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		getDetailAssessment(context, payload) {
			getDetailAssessment(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDetailAssessment', res.data.data)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		postDataScoreAll(context, payload) {
			postDataScoreAll(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDataScore', res.data)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		getDataPeringkat(context, payload) {
			getDataPeringkat(payload)
				.then(res => {
					if (res.status === 200) {
						// console.log(res.data)
						var pagination = {
							current: res.data.current_page,
							last_page: res.data.last_page,
							...payload
							// next_page_url: res.data.next_page_url,
							// prev_page_url: res.data.prev_page_url
						}
						context.commit('updatePagination', pagination)
						context.commit('updateDataPeringkat', res.data.data)
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		postDataRiwayat(context, payload) {
			postDataRiwayat(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataRiwayat', res.data.data)
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
	}
}

export default dataAssessment