<template>
	<div
		class="login d-flex align-center justify-center py-7"
		:style="{'background-image': isMobile === false ? `url(${require('@/assets/login_bg.png')})` : 'none'}">
		<v-col cols="12" lg="4" md="8" sm="12">
			<v-sheet color="white" class="pa-6 elevation-5" style="border-radius: 3px;">
				<center>
					<img src="@/assets/logo.png" alt="logo" height="70px" class="mb-4" />
				</center>		
				<template v-if="$route.name === 'login-peserta'">
					<div class="px-3">
						<v-btn color="primary" block depressed>
							<v-icon class="mr-3">mdi-facebook</v-icon>
							Masuk dengan facebook
						</v-btn>
					</div>
					<p class="text-center my-3 grey--text">ATAU</p>	
				</template>
				<v-row>
					<v-col cols="12" md="6" class="px-6" v-for="(data, index) in nav" :key="index">
						<v-btn 
							block depressed
							color="orange"
							dark
							:text="actived(data.name)"
							@click="select(data.name)">
							<!-- <v-icon class="mr-3">{{data.icon}}</v-icon> -->
							login {{data.name}}
						</v-btn>
					</v-col>	
				</v-row>	
				<router-view></router-view>
			</v-sheet>
		</v-col>		
	</div>
</template>

<style scoped>
	.login{
		background-size: cover;
		background-attachment: fixed;
		background-position: center;
		/*height: 100vh;
		max-height: 100vh;*/
	}
</style>

<script>
	export default {
		data: () => ({
			selected: 'peserta',
			nav: [
				{ name: 'peserta', icon: 'mdi-face' },
				{ name: 'sekolah', icon: 'mdi-google-classroom' }
			],
			isMobile: false
		}),

		mounted () {
			this.cekMobile()
		},

		methods: {
			actived (name) {
				if (this.selected === name) {
					return false
				} else {
					return true
				}
			},
			select (name) {
				this.selected = name
				if (name === 'sekolah') {
					window.location.href='https://app.suksesutbk.com/login/sekolah'
				}
				this.$router.push({name: 'login-'+name})
			},
			cekMobile () {
				var vm = this
				setInterval(function () {
					if (window.innerWidth < 960) {
						vm.isMobile = true
					} else {
						vm.isMobile = false
					}
				}, 1000)
			}
		}
	}
</script>