import { getDataPaket, getDetailPaket } from '@/config/services'

var dataPaket = {
	namespaced: true,
	state: {
		isLoading: false,
		status: null,
		dataPaket: [],
		detailPaket: []
	},
	mutations: {
		updateDataPaket (state, payload) {
			state.dataPaket = payload
		},
		updateDetailPaket (state, payload) {
			state.detailPaket = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateStatus (context, payload) {
			context.commit('updateStatus', payload)
		},
		updateDetailPaket (context, payload) {
			context.commit('updateDetailPaket', payload)
		},
		getDataPaket (context, payload) {
			getDataPaket ()
				.then(res => {
					if (res.status === 200) {						
						context.commit('updateDataPaket', res.data.data)
						context.dispatch('updateIsLoading', false)
						context.dispatch('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		getDetailPaket (context, payload) {
			getDetailPaket (payload)
				.then(res => {
					if (res.status === 200) {						
						context.commit('updateDetailPaket', res.data.data)
						context.dispatch('updateIsLoading', false)
						context.dispatch('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataPaket