<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent
  >
    <v-card color="warning" dark>
      <v-card-text class="pt-6 white--text">
        Mohon maaf waktu pengerjaan soal sub materi telah habis sehingga anda tidak dapat lagi melanjutkan pengerjaan soal. <b>Silahkan pilih sub materi yang lain.</b>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        Pilih Materi
        <v-col cols="12" sm="6">
          <v-select
            outlned
            dense
            color="white"
            :items="submateri"
            item-text="text"
            item-value="index"
            v-model="selectedSubmateri"
          ></v-select>
        </v-col>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    props: ['visible'],    
    data () {
      return {
        dialog: false,
        selectedSubmateri: ''
      }
    },
    computed: {
      submateri() {
        const soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
        return soal_ujian.submateri.map((submateri, i) => {
          return {
            text: submateri.nama,
            index: i
          }
        })
      }
    },
    watch: {
      visible (val) {
        this.dialog = val
      },
      selectedSubmateri (val) {
        var data = {
          id: parseInt(val),
          posisi: 'select'
        }
        this.$router.push({name: 'soal', params: {index: val}})
        this.$store.dispatch('dataSoal/updateDurasiSubmateri', data.id)
        this.$store.dispatch('dataSoal/updatePage', 1)
      }
    }
  }
</script>