<template>
	<v-dialog
    v-model="dialog"
    width="600"
    persistent
  >
    <v-card class="grey lighten-4">
    	<v-card-title>
    		<v-spacer></v-spacer>
        <v-btn icon color="primary" @click="hidden">
          <v-icon>mdi-close</v-icon>
        </v-btn>
    	</v-card-title>
      <v-card-text class="pa-0">
			  <v-stepper v-model="step" class="elevation-0 grey lighten-4">
			    <v-stepper-header class="elevation-0">
			      <v-stepper-step :complete="step > 1" step="1"></v-stepper-step>

			      <v-divider></v-divider>

			      <v-stepper-step :complete="step > 2" step="2"></v-stepper-step>

			      <v-divider></v-divider>

			      <v-stepper-step :complete="step > 3" step="3"></v-stepper-step>
			    </v-stepper-header>

			    <v-stepper-items>
			      <v-stepper-content step="1">
			        <Deskripsi :dataPayment="dataPayment" @next="next" />
			      </v-stepper-content>

			      <v-stepper-content step="2">
			        <Pembayaran :dataPayment="dataPayment" />
			        <Metode :dataPayment="dataPayment" @next="next" />
			      </v-stepper-content>

			      <v-stepper-content step="3">
			        <Pembayaran :dataPayment="dataPayment" />			        
			        <Konfirmasi :dataPayment="dataPayment" @next="next" />			        
			      </v-stepper-content>
			    </v-stepper-items>
			  </v-stepper>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<style>
	.v-stepper__step{ padding: 0 24px; }
</style>

<script>
	import { mapState } from 'vuex'
	import { flattenDeep } from 'lodash'
	import Deskripsi from './Deskripsi'
	import Pembayaran from './Pembayaran'
	import Metode from './Metode'
	import Konfirmasi from './Konfirmasi'
	export default {
		props: ['id', 'visible', 'index'],
		data: () => ({
			dialog: false,
			step: 1
		}),

		components: {
			Konfirmasi,
			Metode,
			Pembayaran,
			Deskripsi
		},

		// created () {
		// 	this.$store.dispatch('dataPayment/getDataPayment', this.id)
		// 	this.$store.dispatch('dataPayment/updateIsLoading', true)
		// },

		watch: {
			visible (val) {
				this.dialog = val
			}
		},

		computed: {
			...mapState('dataPayment', ['dataPayment']),
			// detailPayment () {
			// 	var detail = this.dataPayment.filter((data, index) => index === this.index)
			// 	console.log(flattenDeep(detail))
			// Deep	return flattenDeep(detail)
			// }
		},

		methods: {
			next (val) {
				this.step = val
			},
			hidden () {
				this.$emit('hidden', false)
			}
		}
	}
</script>