<template>
	<v-app-bar 
		class="px-9"
		fixed>
		<v-toolbar-title @click="$router.push({name: 'dashboard'})">
			<img src="@/assets/logo.png" alt="logo" height="40px">
		</v-toolbar-title>
		<ul 
			class="navbar-nav d-none d-sm-none d-md-flex"
			v-if="unit === undefined">
			<li>
				<a href="javascript:void(0)" @click="$router.push({name: 'dashboard'})">
					Beranda
				</a>
			</li>
			<li>
				<a href="javascript:void(0)" @click="$router.push({name: 'fitur'})">
					Fitur
				</a>
			</li>
			<li>
				<a href="javascript:void(0)" @click="$router.push({name: 'how-soal'})">
					Cara Pemakaian
				</a>
			</li>
			<li>
				<a href="javascript:void(0)" @click="$router.push({name: 'riwayat'})">
					Riwayat Transaksi
				</a>
			</li>
			<li><a href="javascript:void(0)" @click="keluar">Keluar</a></li>
		</ul>
		<v-spacer></v-spacer>

    <v-app-bar-nav-icon @click="show" class="d-sm-flex d-md-none"></v-app-bar-nav-icon>	
	</v-app-bar>
</template>

<style scoped>
	.navbar-nav{
		margin-left: 24px;
		display: flex;
		padding: 0px;
		list-style: none;
	}
	.navbar-nav li a{
		margin: 0 8px;
		text-decoration: none;
		color: #000;
	}
	@media only screen and (max-width: 366px) {
		.v-application .px-9{
			padding-right: 0px !important;
    	padding-left: 0px !important;
		}
	}
</style>

<script>
	export default{	
		data: () => ({
			unit: ''
		}),

		created () {
			this.unit = this.$route.params.id
		},
		methods: {
			keluar () {
				window.localStorage.removeItem('token')
				window.localStorage.removeItem('dataPeserta')
				window.localStorage.removeItem('id_paket_soal')
				window.localStorage.removeItem('pilihan_ptn')
				window.localStorage.removeItem('detailPaketSoal')
				window.localStorage.removeItem('info_ujian')
				window.localStorage.removeItem('id_materi_uji')
				this.$router.push({name:'login-peserta'})
			},
			show () {
				this.$store.dispatch('updateDrawer', true)
			}
		}
	}
</script>