<template>
  <v-dialog
    v-model="dialog"
    fullscreen 
    hide-overlay 
    transition="dialog-bottom-transition">
    <v-card color="red" dark tile class="d-flex justify-center align-center">
      <v-card width="500" flat color="red">
        <v-card-text class="pt-6 white--text subtitle-1 text-center">
          <v-icon color="white" size="100">mdi-wifi-off</v-icon> <br>
          Koneksi internet terputus, silahkan periksa koneksi anda.
        </v-card-text>
      </v-card>
    </v-card>
  </v-dialog>
</template>

<script>
  import {mapState} from 'vuex'
  export default {   
    data () {
      return {
        dialog: false,
      }
    },

    mounted () {
      this.cekConnection()
    },

    methods: {
      cekConnection () {
        var vm = this
        setInterval(function(){
          var connection = navigator.onLine
          // if (connection !== dialog) {
          //   this.$store.dispatch('updateConnection', connection)
          // }
          vm.dialog = !connection
        }, 1000)
      }
    }
  }
</script>