<template>
  <v-dialog
    v-model="dialog"
    fullscreen 
    hide-overlay 
    transition="dialog-bottom-transition">
    <v-card :color="statusUploadJawaban === false?'red':'primary'" dark tile class="d-flex justify-center align-center">
      <v-card width="500" flat :color="statusUploadJawaban === false?'red':'primary'">
        <v-card-text class="pt-6 white--text subtitle-1 text-center">
          <template v-if="isLoadingUploadJawaban === true">
            <v-progress-circular
              :size="80"
              width="10"
              color="white"
              indeterminate
            ></v-progress-circular>
            <div class="title mt-3">
              Sedang proses upload jawaban, mohon tunggu sebentar.
            </div>
          </template>
          <template v-else-if="statusUploadJawaban === false">
            <v-icon color="white" size="80">mdi-close-circle</v-icon>
            <div class="title mt-3">
              Proses upload jawaban gagal, mohon dilihat lagi koneksi atau jawaban anda.
            </div>
            <v-btn
              depressed
              color="white"
              @click="$router.go(-1)"
              class="primary--text mt-3">
              kembali ke halaman soal
            </v-btn>
          </template>
          <template v-else-if="statusUploadJawaban === true">
            <v-icon color="white" size="80">mdi-check</v-icon>
            <div class="title mt-3">
              Proses upload jawaban berhasil
            </div>
            <v-btn
              depressed
              color="white"
              @click="$router.push({name: 'hasil-soal'})"
              class="primary--text mt-3">
              pergi ke halaman hasil
            </v-btn>
          </template>
        </v-card-text>
      </v-card>
    </v-card>
  </v-dialog>
</template>

<script>
  import {mapState} from 'vuex'
  export default {   
    data () {
      return {
        dialog: true,
      }
    },

    created () {
      this.$store.dispatch('dataSoal/updateIsLoadingUploadJawaban', true)
    },

    computed: {
      ...mapState('dataSoal', ['statusUploadJawaban', 'isLoadingUploadJawaban'])
    },    
  }
</script>