<template>
  <div class="small">
  	<v-row>
  		<v-col cols="12" md="4" sm="12" 
        v-for="(item, i) in detailAssessment.analisis_nilai.tabel"
        :key="i">
  			<v-card elevation="6">
          <v-card-title v-html="namaMateriUji(item.materi_uji)"></v-card-title>
  				<v-card-text>
    				<LineChart :chartData="datacollection(item)"/> 					
  				</v-card-text>
  			</v-card>
  		</v-col>
  	</v-row>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import LineChart from './LineChart.js'

  export default {
    components: {
      LineChart
    },
    computed: {
      ...mapState('dataAssessment', ['detailAssessment']),      
    },
    methods: {
      namaMateriUji (name) {
        var nama = name.split(' ')
        if (nama[4] === undefined) {
          return nama[0]+' '+nama[1]+' '+nama[2]+' '+nama[3]
        } else {
          return nama[0]+' '+nama[1]+' '+nama[2]+'<br>'+nama[3]+' '+nama[4]
        }
      },
      datacollection (item) {
        var dataSet = {
          labels: [item.materi_uji],
          datasets: [{
            label: 'one',
            backgroundColor: 'red',
            data: [10]
          },{
            label: 'two',
            backgroundColor: 'yellow',
            data: [10]
          }]
        }

        var color = ['#313D65', '#F08519', '#1976D2', '#EB306F', '#3F51B5'] 
        // console.log(item.detail)
        item.detail.map((val, j) => {
          var obj = {
            label: '',
            backgroundColor: '',
            data: []
          }
          obj.backgroundColor = color[j]
          obj.label = val.submateri          
          obj.data[0] = val.skor
          dataSet.datasets[j] = obj
        })

        

        return dataSet
      },
    }
  }
</script>