<template>
	<div>    
    <v-btn 
      v-if="isDone"
      depressed 
      color="success" 
      @click="$router.push({ name: 'analisis' })">
      cek hasil
    </v-btn> 
    <!-- <v-btn 
      depressed 
      color="success" 
      @click="visible">
      Mulai
    </v-btn>  -->
    <v-btn 
      v-else
      depressed 
      color="success" 
      @click="visible">
      mulai
    </v-btn>    
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card>
        <v-card-title class="headline text-capitalize">
          pilih sub paket Materi
        </v-card-title>

        <v-card-text v-for="(item, i) in dataSubPaket" :key="i" class="py-0">
            {{ status(item.subpaket.nama) }}
          <span v-for="(paket, index) in item.subpaket.paket_soal" :key="index">
            <PilihPTN :subpaket="paket" :img="index+1" />
          </span>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dialog=false">
            kembali
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog> 
  </div>
</template>

<style scoped>
	img{ width: 100%; }
	a{ margin: 4px 0;display: block; }
</style>

<script>
  import { mapState } from 'vuex'
  import { findIndex } from 'lodash'
	import PilihPTN from './PilihPTN'
	export default {
    props: ['idPaket', 'idSubPaket'],
		data: () => ({
			dialog: false,
      textBtn: '',
      isDone: null
		}),

		components: {
			PilihPTN
		},

    computed: {
      ...mapState('dataDashboard', ['dataDashboard']),
      dataSubPaket () {
        var data = {...this.dataDashboard[0]}
        return data.paket.filter(e => e.id_paket === this.idPaket)
      }
    },
    methods: {
      visible () {
        var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
        window.localStorage.setItem('id_subpaket_tryout', this.idSubPaket)
        if (peserta.sekolah.tahun_kelulusan !== null) {
          this.dialog = true
        } else {
          this.$router.push({name: 'biodata'})
        }
      },
      status (nama) {
        var hasil = null
        // var mulai = null
        this.dataDashboard.map(e => {
          e.progress_tryout.detail.map((val, index) => {
            var data = val.subpaket.nama === nama
            if (data) {
              val.subpaket.paket_soal.map(materi => {
                hasil = findIndex(materi.materi_uji, (com) => com.completed === false)
              })
            }
          })
        })

        if (hasil === -1) {
          this.isDone =  true
        } else {
          this.isDone = false
        }
      }
    }
	}
</script>