<template>
  <div class="sidebar" :class="{'d-block': drawer}">
    <v-list 
      class="mt-0"
      max-width="80%"
      transition="slide-x-transition">
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title @click="$router.push({name: 'dashboard'})">
            <img src="@/assets/logo.png" alt="logo" height="30px">               
          </v-list-item-title>
        </v-list-item-content>
        <v-list-item-icon>
          <v-btn icon small @click="hide">
            <v-icon>mdi-close</v-icon>
          </v-btn>  
        </v-list-item-icon>
      </v-list-item>

      <v-list-item-group>
        <v-list-item
          v-for="(item, i) in items"
          :key="i"
          @click="link(item.link)">
          <v-list-item-icon>
            <v-icon v-text="item.icon" color="primary"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item @click="keluar">
          <v-list-item-icon>
            <v-icon color="primary">mdi-close</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>Keluar</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<style>
  .sidebar{
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
    display: none;
  }
  .sidebar .v-list{
    border-radius: 0px;
    height: 100vh;
  }
</style>

<script>
  import {mapState} from 'vuex'
  export default {
    data () {
      return {
        items: [
          { title: 'Beranda', icon: 'mdi-view-dashboard', link: 'dashboard' },
          { title: 'Fitur', icon: 'mdi-tools', link: 'fitur' },
          { title: 'Cara Pemakaian  ', icon: 'mdi-book-open-variant', link: 'how-soal' },
          { title: 'Riwayat Transaksi', icon: 'mdi-file-document', link: 'riwayat' },
        ],
      }
    },
    computed: {
      ...mapState(['drawer'])
    },
    methods: {
      hide () {
        this.$store.dispatch('updateDrawer', false)
      },
      link (nameLink) {
        this.$router.push({name: nameLink})
        this.$store.dispatch('updateDrawer', false)
      },
      keluar () {
        window.localStorage.removeItem('token')
        window.localStorage.removeItem('dataPeserta')
        window.localStorage.removeItem('id_paket_soal')
        window.localStorage.removeItem('pilihan_ptn')
        window.localStorage.removeItem('detailPaketSoal')
        window.localStorage.removeItem('info_ujian')
        window.localStorage.removeItem('id_materi_uji')
        this.$router.push({name:'login-peserta'})
      },
    }
  }
</script>