<template>
	<div>
		<v-row v-for="(item, i) in detailAssessment.analisis_nilai.tabel" :key="i">
			<v-col cols="12" md="4" sm="12">
				<center>
					<v-progress-circular 
						:value="value"
						:size="240"
						:width="20"
						:rotate="-90"
						color="yellow"
					>
						<div class="display-1 black--text font-weight-bold">
							{{parseFloat(item.ceeb.skor).toFixed(2)}}
						</div>
					</v-progress-circular>
				</center>
				<p class="text-center text-capitalize title mt-3">
					total nilai {{item.materi_uji}}
				</p>
			</v-col>
			<v-col cols="12" md="8" sm="12">
				<v-card>
					<v-card-text>
						<v-simple-table>
							<template v-slot:default>
					      <thead class="white">
					        <tr>
					          <th class="text-left">
					          	<div class="d-none d-md-flex">analisis perolehan {{item.materi_uji}}</div>
					          </th>
					          <th class="text-center">b</th>
					          <th class="text-center">s</th>
					          <th class="text-center">k</th>
					          <th class="text-center">skor</th>
					        </tr>
					      </thead>
					      <tbody>
					        <tr v-for="(val, i) in item.detail" :key="i">
					          <td>{{ val.submateri }}</td>
					          <td class="text-center">{{ val.benar }}</td>
					          <td class="text-center">{{ val.salah }}</td>
					          <td class="text-center">{{ val.kosong }}</td>
					          <td class="text-center">{{ parseFloat(val.skor).toFixed(2) }}</td>
					        </tr>
					        <tr>
					        	<td class="font-weight-bold">Nilai CEEB</td>
					          <td class="text-center font-weight-bold">{{ item.ceeb.benar }}</td>
					          <td class="text-center font-weight-bold">{{ item.ceeb.salah }}</td>
					          <td class="text-center font-weight-bold">{{ item.ceeb.kosong }}</td>
					          <td class="text-center font-weight-bold">
					          	{{ parseFloat(item.ceeb.skor).toFixed(2) }}
					          </td>
					        </tr>
					      </tbody>
					    </template>
					  </v-simple-table>
					</v-card-text>
				</v-card>
			</v-col>
		</v-row>
	</div>
</template>

<style scoped>
	thead tr th {
		font-weight: bold !important;
		color: #000 !important;
		text-transform: uppercase;
		background-color: #f4f4f4;
	}
	table tr:nth-child(even){ background-color: #f4f4f4; }
</style>

<script>
	import {mapState} from 'vuex'
	export default{
		data () {
      return {
        interval: {},
        value: 0,
        
      }
    },
    computed: {
    	...mapState('dataAssessment', ['detailAssessment'])
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
		mounted () {
      this.interval = setInterval(() => {
        if (this.value === 100) {
          return (this.value = 0)
        }
        this.value += 10
      }, 1000)
    },
	}
</script>