import http from './http'
import http2 from './http2'

// login
export function postDataLogin(data) {
	return http.post('login/peserta', data)
}

export function postDataDaftar(data) {
	return http.post('register/peserta', data)
}

export function cekKodeAfiliasi(data) {
	return http.get('cari/kode-afiliasi?kode_afiliasi='+data)
}

// dashboard
export function getDataDashboard(id) {
	return http.get('peserta/'+id+'/dashboard')
}

// soal
export function getDataSoal(data) {
	return http.get('ujian/submateri/soal?id_kategori_submateri='+data)
}

export function getDataSoal2(data) {
	return http2.get('ujian/soal?idu='+data.id_ujian+'&idm='+data.id_materi_uji)
}

export function postDataJawaban(data) {
	return http.post('ujian/submit', data)
}

export function postDataJawaban2(data) {
	return http2.post('ujian/submit', data)
}

export function postFinishUjian(data) {
	return http2.post('ujian/finish', data)
}

export function getDataPaketSoal(data) {
	return http.get('peserta/'+data.id_peserta+'/paket-soal/'+data.id_paket_soal)
}

export function postDataUjian(data) {
	return http.post('ujian/request', data)
}

export function postDataUjian2(data) {
	return http2.post('ujian/start', data)
}

export function postDataScore(data) {
	return http.post('ujian/finalize', data)
}

export function postDataScoreAll(data) {
	return http.post('ujian/finalize-all', data)
}

export function postStatusCompleted(id, data) {
	return http.post('ujian/sub/'+id+'/status', data)
}

export function pauseUjian(data) {
	return http2.post('ujian/pause', data)
}

// ptn
export function getDataPTN(data) {
	var kelompok = data.toUpperCase()
	return http.get('cari/kelompok/ptn?kelompok='+kelompok)
}

export function getDataProdi(data) {
	if (data.kelompok === 'campuran') {
		return http.get('cari/ptn?nama='+data.ptn)
	} else {		
		return http.get('cari/ptn?kelompok='+data.kelompok+'&nama='+data.ptn)
	}
}

export function cekPilihanPTN(data) {
	return http.post('ujian/cek-pilihan-ptn', data)
}

// paket
export function getDataPaket() {
	return http.get('paket/list')
}

export function getDetailPaket(id) {
	return http.get('paket/'+id+'/detail')
}

// Payment
export function postDataPayment(id, data) {
	return http.post('paket/'+id+'/checkout', data)
}

export function getDataPayment(id) {
	return http.get('paket/'+id+'/checkout')
}

export function postCekPayment(data) {
	return http.post('cek-payment')
}

export function postDataRiwayat(data) {
	return http.post('payment', data)
}

// assessment
export function getDataAssessment(data) {
	return http.get('peserta/'+data+'/assessment/')
}

export function getDetailAssessment(data) {
	return http.get('peserta/'+data.id_peserta+'/assessment/'+data.id_ujian)
}

export function getDataPembahasan(data) {
	return http.get('ujian/submateri/pembahasan/?id_ujian='+data.id_ujian+'&id_sub='+data.id_sub+'&id_kategori_submateri='+data.id_kategori_submateri)
}

export function getDataPeringkat(data) {
	if (data.id_paket_soal === undefined) {
		return http.get('cari/peringkat?nama='+data.nama+'&kelompok='+data.kelompok+'&id_ptn='+data.id_ptn+'&id_prodi='+data.id_prodi+'&page='+data.page)
	} else {
		return http.get('cari/peringkat?nama='+data.nama+'&kelompok='+data.kelompok+'&id_ptn='+data.id_ptn+'&id_prodi='+data.id_prodi+'&id_paket_soal='+data.id_paket_soal+'&page='+data.page)
	}
	// return http.get('cari/peringkat?nama='+data.nama)
}

// ranking
export function getDataHistory(data) {
	return http.get('peserta/'+data+'/history-ujian')
}

export function postDataRanking(data) {
	return http.post('simulasi-rangking', data)
}

// biodata
export function getDataProv() {
	return http.get('cari/provinsi')
}

export function getDataKab(data) {
	return http.get('cari/kabupaten/?provinsi='+data)
}

export function getDataSekolah(data) {
	return http.get('cari/sekolah/?provinsi='+data.provinsi+'&kabupaten='+data.kabupaten)
}

export function postDataBiodata(id, data) {
	return http.post('peserta/'+id, data)
}

export function postCekAkun(data) {
	return http.post('cek-akun', data)
}