<template>
	<div>
    <a 
      @click="pilihPTN"
      class="paket-soal"
      :style="`background-image: url(${require('@/assets/subpaket/'+img+'.svg')});`">
      {{subpaket.nama}}
    </a>
    <v-dialog
      v-model="dialog2"
      width="600"
    >
      <!-- <template v-slot:activator="{ on }">
        <a 
          v-on="on"
          class="paket-soal"
          :style="`background-image: url(${require('@/assets/subpaket/'+img+'.svg')});`">
          {{subpaket.nama}}
        </a>
      </template> -->

      <v-card>
        <v-card-title class="headline text-capitalize text-center">
          pilih perguruan tinggi negeri
        </v-card-title>

        <v-card-text>
          <FormPTN @keluar="keluar" :subpaket="subpaket"/>
        </v-card-text>
      </v-card>
    </v-dialog> 
  </div>
</template>

<style scoped>
	img{ width: 100%; }
	.paket-soal{
    background-size: cover;
    display: flex;
    justify-content: flex-end;
    padding-right: 16px;
    align-items: center;
    color: #fff;
    margin: 8px 0;
    font-size: 24px;
    font-weight: 900;
    height: 120px;
  }

  @media only screen and (max-width: 366px) {
    .paket-soal{ 
      height: 100px;
      font-size: 20px;
    }
  }
</style>

<script>
  import {mapState} from 'vuex'
  import {flatten} from 'lodash'
  import FormPTN from './PilihPTN/FormPTN'
	export default {
    props: ['subpaket', 'img'],
		data: () => ({
			dialog2: false
		}),

    components: {
      FormPTN
    },

    computed: {
      ...mapState('dataPTN', ['pilihanPTN'])
    },

    created () {
      var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
      var data = {
        id_peserta: peserta.id,
        id_paket_soal: this.subpaket.id
      }
      this.$store.dispatch('dataPTN/cekPilihanPTN', data)
    },

    methods: {
      keluar (val) {
        this.dialog2 = val
      },
      pilihPTN () {
        var hasil = flatten(this.pilihanPTN).filter(data => {
          return data.id_paket_soal === this.subpaket.id
        })
        // console.log(hasil)

        if (hasil.length === 0) {
          this.dialog2 = true
        } else {
          var ptn = []
          hasil.map((e, i) => {
            ptn[i] = {
              id_prodi: e.kode_prodi,
              id_ptn: e.kode_ptn
            }
          })
          // console.log(ptn)
          window.localStorage.setItem('pilihan_ptn', JSON.stringify(ptn))
          window.localStorage.setItem('id_paket_soal', this.subpaket.id)        
          window.localStorage.setItem('id_paket_soal_pembahasan', this.subpaket.id)
          this.$router.push({name: 'tes'})
        }
      }
    }
	}
</script>