<template>
	<v-row>
		<v-col cols="12" md="6" sm="12">
			<v-card>				
				<v-card-title>
					NILAI CEEB Mata Uji
				</v-card-title>
				<v-card-text>
					<div 
						v-for="(item, i) in detailAssessment.analisis_nilai.tabel"
						class="d-flex justify-space-between black--text mb-3">
						<span>{{item.materi_uji}}</span>
						<b>{{parseFloat(item.ceeb.skor).toFixed(2)}}</b>
					</div>
				</v-card-text>
			</v-card>
		</v-col>
		<v-col cols="12" md="6" sm="12">
			<v-card v-for="(item, i) in detailAssessment.analisis_nilai.ceeb" :key="i" class="mb-3">
				<v-card-text class="black--text">
					<v-row no-gutters>
						<v-col cols="12" sm="3">							
							<center>
								<div class="font-weight-bold">{{item.kelompok_uji}}</div>
								<v-progress-circular 
									:value="value"
									:size="103"
									:width="10"
									:rotate="-90"
									color="yellow"
								>
									<div class="black--text title font-weight-bold">
										{{parseFloat(item.nilai_akhir).toFixed(2)}}
									</div>
								</v-progress-circular>	
							</center>						
						</v-col>
						<v-col cols="12" sm="3">
							<div class="d-flex align-center" style="height: 103px;">
								<div>
									<div>Nilai Akhir (NA)</div>
									<b class="headline">{{parseFloat(item.nilai_akhir).toFixed(2)}}</b>
								</div>
							</div>
						</v-col>
						<v-col cols="12" sm="6">
							<div class="d-flex align-center" style="height: 103px;">
								<div>
									<ul class="pa-0" style="list-style: none;">
										<li>
											<v-icon color="success" class="mr-3">mdi-checkbox-marked</v-icon>
											<span>{{item.detail.benar}} Soal Benar</span>
										</li>
										<li>
											<v-icon color="red" class="mr-3">mdi-close-box</v-icon>
											<span>{{item.detail.salah}} Soal Salah</span>
										</li>
										<li>
											<v-icon class="mr-3">mdi-minus-box</v-icon>
											<span>{{item.detail.kosong}} Soal tidak dijawab</span>
										</li>
									</ul>
								</div>
							</div>
						</v-col>
					</v-row>
				</v-card-text>
			</v-card>
		</v-col>
	</v-row>
</template>

<script>
	import {mapState} from 'vuex'
	export default{
		data () {
      return {
        interval: {},
        value: 0,
      }
    },
    computed: {
    	...mapState('dataAssessment', ['detailAssessment'])
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
		mounted () {
      this.interval = setInterval(() => {
        if (this.value === 100) {
          return (this.value = 0)
        }
        this.value += 10
      }, 1000)
    },
	}
</script>