<template>
	<v-footer absolute class="white bt">		
		<div class="content-footer">
			<div class="pagination">
				<v-btn 
					depressed
					outlined
					color="orange"
					class="px-2 mr-2"
					:disabled="disablePrevMateri"
					:loading="isLoading"
					@click="prevMateri">					
					<v-icon class="mr-2">mdi-chevron-left</v-icon>	
					{{textPrev}}			
				</v-btn>
				<v-btn 
					depressed
					dark
					color="orange"
					class="pa-0 mr-2"
					:class="{'d-none': hiddenPrev}"
					v-shortkey="['arrowleft']"
					@shortkey="clicked(page-1, 'prev')"
					@click="clicked(page-1)">					
					<v-icon>mdi-chevron-left</v-icon>					
				</v-btn>
				<template v-for="(item, index) in dataPembahasan">
					<!-- <span v-for="(val, i) in local" :key="i+100"> -->
						<v-btn
							depressed 
							:outlined="actived(index+1)"
							color="primary" 
							class="pa-0 mx-1"
							:key="index"
							@click="clicked(index+1)">
							{{ index+1 }}
						</v-btn>
					<!-- </span>					 -->
				</template>
				<v-btn 
					depressed
					dark
					color="orange"
					class="pa-0 ml-2"
					:class="{'d-none': hiddenNext}"
					v-shortkey="['arrowright']"
					@shortkey="clicked(page+1, 'next')"
					@click="clicked(page+1)">					
					<v-icon>mdi-chevron-right</v-icon>					
				</v-btn>
				<v-btn 
					depressed
					color="orange"
					class="px-2 ml-2"
					outlined
					:disabled="disableNextMateri"
					:loading="isLoading"
					@click="nextMateri">					
					{{textNext}}				
					<v-icon class="ml-2">mdi-chevron-right</v-icon>	
				</v-btn>				
			</div>
		</div>
	</v-footer>
</template>

<style scoped>	
	.v-btn:not(.v-btn--round).v-size--default{
		min-width: 36px !important;
	}
	.bt{ box-shadow: 0 0 0 1px #cfcfcf; }
	.content-footer{
		margin: 0 auto;
		max-width: 100%;
		overflow: auto;
		display: flex;
	}
	.pagination{
		display: flex;
		list-style: none;
		padding: 0px !important;
	}
	.hidden{ display: none; }
</style>

<script>
	import { mapState } from 'vuex'
	import { uniqBy, remove, flattenDeep, findIndex } from 'lodash'

	export default{
		data: () =>({
			page: 1,
			hiddenPrev: true,
			hiddenNext: true,
			hiddenDone: true,
			disableNextMateri: false,
			disablePrevMateri: false,
			id: 0,
			index: 0,
			textNext: 'Review selanjutnya',
			textPrev: 'Review sebelumnya',
		}),

		created () {
			this.clicked(this.page)
			this.id = parseInt(this.$route.params.id)
		},

		watch: {
			dataPembahasan (val) {
				this.clicked(this.page)
			} 
		},

		computed: {
			...mapState('dataAssessment', ['dataPembahasan', 'isLoading']),
			...mapState('dataSoal', ['dataPaketSoal']),
			...mapState('dataDashboard', ['dataDashboard']),		
			jumlahPaket () {
				var jadi = this.dataDashboard.map(paket => {
					return paket.paket.map(data => {
						return data
					})
				})

				return flattenDeep(jadi)
			},
			submateri () {
				var hasil = []
				this.dataPaketSoal.map(data => {
					// console.log()
					data.submateri.map((sub, index, self) => {
						if (sub.id_kategori_submateri === this.id) {
							hasil = self
							this.index = index
						}
					})
				})

				return hasil
			},
			disable () {
				var paketPremium = this.jumlahPaket.filter(e => {
          // console.log(e.status)
          if (e.nama_paket.indexOf('FREE') === -1) {
            return e
          }
        })
        var hasil = findIndex(paketPremium, o => o.status === true)
				if (hasil === -1) {
					return true
				} else {
					return false
				}			
			}
		},

		methods: {
			clicked (pageNum, jenis = '') {
				if (this.hiddenPrev === false && jenis === 'prev') {
					this.page = pageNum
					this.$store.dispatch('dataAssessment/updatePage', pageNum)
				} else if (this.hiddenNext === false && jenis === 'next') {
					this.page = pageNum
					this.$store.dispatch('dataAssessment/updatePage', pageNum)
				} else if (jenis === '') {					
					this.page = pageNum
					this.$store.dispatch('dataAssessment/updatePage', pageNum)				
				}

				if (this.dataPembahasan.length === 0) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else if (this.dataPembahasan.length === 0) {
					// this.hiddenNextMateri = true
					this.hiddenPrev = true					
					this.hiddenDone = false
					this.hiddenNext = true	
				}else if (this.page === 1 && this.dataPembahasan.length > 0) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = false			
				} else if (this.page === this.dataPembahasan.length){
					// this.hiddenNextMateri = true
					this.hiddenPrev = false					
					this.hiddenDone = false
					this.hiddenNext = true	
				} else if (this.page === this.dataPembahasan.length){
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else{
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = false	
				}
			},
			actived (page) {
				if (page === this.page) {
					return false
				}else{
					return true
				}
			},
			nextMateri () {
				// console.log(this.dataPaketSoal)
				var id = this.submateri
					.filter((data, index) => {
						if (data.id_kategori_submateri !== this.id && index > this.index) {
							return data
						}
					})
				if (this.index === this.submateri.length-1 || this.disable === true) {
					this.textNext = 'Materi uji pada paket ini sudah selesai'
					this.disableNextMateri = true
				} else {
					this.$router.push({name: 'pembahasan', params: {id: id[0].id_kategori_submateri}})
					window.location.reload(true)
				}
			},
			prevMateri () {
				var id = this.submateri
					.filter((data, index) => {
						if (data.id_kategori_submateri !== this.id && index < this.index) {
							return data
						}
					})
				var posisi = id.length-1
				if (this.index === 0) {
					this.textPrev = 'Materi uji pada paket ini sudah selesai'
					this.disablePrevMateri = true
				} else {					
					this.$router.push({name: 'pembahasan', params: {id: id[posisi].id_kategori_submateri}})
					window.location.reload(true)
				}
			}
		}
	}
</script>