<template>
	<v-form ref="form" v-model="valid">
		<CardPilihanPTN1 @prodi="prodi" :kelompok="kelompokSelected" />
    <CardPilihanPTN2 @prodi="prodi" :kelompok="kelompokSelected" />
		<!--  -->
		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn text color="primary" @click="keluar">
				kembali
			</v-btn>
			<v-btn 
				depressed 
				color="primary" 
				class="ml-3"
				:disabled="!valid"
				@click.prevent="validate">
				simpan
			</v-btn>
		</div>
	</v-form>
</template>

<script>
  import CardPilihanPTN1 from './CardPilihanPTN1'
  import CardPilihanPTN2 from './CardPilihanPTN2'
	export default {
    props: ['subpaket'],
		data: () => ({	
			valid: true,	
			pilihan_ptn: []	
    }),

    components: {
      CardPilihanPTN1,
      CardPilihanPTN2
    },

    created() {
    	var jenis = ['saintek', 'soshum', 'campuran']
        var jadi = jenis.filter(data => {
          var status = this.subpaket.nama.toLowerCase().indexOf(data)
          if (status !== -1) {
            return data
          } 
        })

      this.$store.dispatch('dataPTN/getDataPTN', jadi[0])
    },

    computed: {
      kelompokSelected() {
        var jenis = ['saintek', 'soshum', 'campuran']
        var jadi = jenis.filter(data => {
          var status = this.subpaket.nama.toLowerCase().indexOf(data)
          if (status !== -1) {
            return data
          } 
        })

        return jadi[0]
      },
    },

		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					window.localStorage.setItem('pilihan_ptn', JSON.stringify(this.pilihan_ptn))
					// console.log(this.subpaket.id)
        	window.localStorage.setItem('id_paket_soal', this.subpaket.id)				
        	window.localStorage.setItem('id_paket_soal_pembahasan', this.subpaket.id)				
          this.$router.push({ name: 'tes' })
        }
			},
			prodi (val) {				
				this.pilihan_ptn[val.pilihan_ke-1] = {"id_prodi": val.kode_prodi, "id_ptn": val.kode_ptn}
			},
			keluar () {
				this.$emit('keluar', false)
			}
		}
	}
</script>