<template>
  <v-dialog
    v-model="dialog"
    fullscreen 
    hide-overlay 
    transition="dialog-bottom-transition">
    <v-card :color="status === false?'red':'success'" dark tile class="d-flex justify-center align-center">
      <v-card width="500" flat :color="status === false?'red':'success'">
        <v-card-text class="pt-6 white--text subtitle-1 text-center">
          <template v-if="status !== false">
            <v-progress-circular
              :size="80"
              width="10"
              color="white"
              indeterminate
            ></v-progress-circular>
            <div class="title mt-3">
              Sedang proses download soal, mohon tunggu sebentar.
            </div>
          </template>
          <template v-else-if="status === false">
            <v-icon color="white" size="80">mdi-close-circle</v-icon>
            <div class="title mt-3">
              Proses download soal gagal, mohon dilihat lagi koneksi anda.
            </div>
          </template>
        </v-card-text>
      </v-card>
    </v-card>
  </v-dialog>
</template>

<script>
  import {mapState} from 'vuex'
  export default {   
    data () {
      return {
        dialog: true,
      }
    },

    created () {
      var detailPaketSoal = JSON.parse(window.localStorage.getItem('detailPaketSoal'))
      var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))
      
      this.$store.dispatch('dataSoal/getDataSoal2', {
        id_ujian: info_ujian.id,
        id_materi_uji: detailPaketSoal.id_materi_uji
      })
      this.$store.dispatch('dataSoal/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataSoal', ['status'])
    },    
  }
</script>