<template>
	<div>
		<v-card>
			<v-card-title>
				Peringkat Berdasarkan
			</v-card-title>
      <v-overlay :value="overlay" z-index="1000">
          <div class="d-flex">
            <v-spacer></v-spacer>
            <v-btn
              fab
              x-small
              color="red"
              @click="overlay = false"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </div>
          <v-img
            src="@/assets/peringkat.png"
            class="white--text align-end"
            min-width="320"
            max-width="320"
          >
            <div 
              class="pa-3" 
              style="background-color: rgba(0,0,0,0.5);">
              Fitur Peringkat dapat diakses ketika kamu sudah membeli paket premium
              <!-- <div class="d-flex my-3">
                <v-spacer></v-spacer>
                <v-btn
                  small
                  text
                  @click="overlay=false"
                  color="white">
                  Oke, saya mengerti
                </v-btn>                
              </div> -->
              <v-btn
                light
                depressed
                block class="mt-3"
                @click="$router.push({name: 'fasilitas', params: {paket: 'platinum'}})"
                color="success">
                <v-icon class="mr-3">mdi-cart</v-icon>
                Beli sekarang
              </v-btn>
            </div>
          </v-img>
      </v-overlay>
      <v-alert type="info" dense v-model="alert" close class="mx-4">
        Fitur Peringkat dapat diakses ketika kamu sudah membeli
        paket premium
      </v-alert>
			<v-form v-model="valid" ref="form">
				<v-card-text class="py-0">
					<v-row>
						<!-- <v-col cols="12" md="6" sm="12">
							<v-text-field
                outlined
                dense
                label="Nama" 
                v-model="dataParams.nama"    
              ></v-text-field>
						</v-col> -->
            <v-col cols="12" md="4" sm="12">
              <v-select
                label="Kelompok Ujian"
                :items="kelompok_uji"
                v-model="dataParams.kelompok"
                outlined
                dense
              ></v-select>
            </v-col>
            <v-col cols="12" md="4" sm="12">
              <v-autocomplete
                v-model="ptn"
                :items="dataPTN"
                item-text="universitas"
                item-value="universitas"
                :loading="isLoading"
                autocomplete="off"
                dense
                hide-no-data
                hide-selected
                :disabled="dataParams.kelompok === '' ? true:false"
                outlined
                label="Cari Perguruan tinggi"
              ></v-autocomplete>
            </v-col>
						<v-col cols="12" md="4" sm="12">
							<v-autocomplete
                v-model="studi"
                :items="dataProdi"
                item-text="prodi"                
                return-object
                dense
                hide-no-data
                hide-selected
                :disabled="ptn === '' ? true:false"
                outlined
                label="Cari program studi"
              ></v-autocomplete>
						</v-col>
					</v-row>
				</v-card-text>
				<v-card-actions class="pa-3">
					<v-spacer></v-spacer>
					<v-btn color="primary" :loading="isLoading" block depressed @click.prevent="validate">
						Filter
					</v-btn>
					<v-spacer></v-spacer>
				</v-card-actions>
			</v-form>
		</v-card>

		<TabelPeringkat :dataPeringkat="dataPeringkat" @free="free"/>
	</div>
</template>

<script>
  import {mapState} from 'vuex'
  import {flattenDeep, findIndex} from 'lodash'
  import TabelPeringkat from './TabelPeringkat'
	export default {
		data: () => ({
      valid: true,
      ptn: '',
      studi: '',
      dataParams: {
        nama: '',
        kelompok: '',
        id_ptn: 0,
        id_prodi: 0
      },
      kelompok_uji: ['SAINTEK', 'SOSHUM'],
      overlay: false,
      alert: false
		}),
    components: {
      TabelPeringkat
    },
    computed: {
      ...mapState('dataPTN', ['isLoading', 'dataPTN', 'dataProdi']),      
      ...mapState('dataAssessment', ['isLoading', 'dataPeringkat']),
      ...mapState('dataDashboard', ['dataDashboard']),
      jumlahPaket () {
        var jadi = this.dataDashboard.map(paket => {
          return paket.paket.map(data => {
            return data
          })
        })

        return flattenDeep(jadi)
      }
    },
    methods: {
      free (val) {
        this.overlay = val
        this.alert = val
      },
      validate () {
        if (this.$refs.form.validate()) {
          var paketPremium = this.jumlahPaket.filter(e => {
            // console.log(e.status)
            if (e.nama_paket.indexOf('FREE') === -1) {
              return e
            }
          })
          var hasil = findIndex(paketPremium, o => o.status === true)
          if (this.$route.params.id !== undefined && this.$route.params.token !== undefined) {
            var id_paket_soal = JSON.parse(window.localStorage.getItem('id_paket_soal'))
            this.dataParams.id_paket_soal = id_paket_soal          
            this.$store.dispatch('dataAssessment/getDataPeringkat', this.dataParams)
            this.$store.dispatch('dataAssessment/updateIsLoading', true)
          }else if (hasil === -1) {
            this.overlay = true
            this.alert = true
          } else {
            var id_paket_soal = JSON.parse(window.localStorage.getItem('id_paket_soal'))
            this.dataParams.id_paket_soal = id_paket_soal          
            this.$store.dispatch('dataAssessment/getDataPeringkat', this.dataParams)
            this.$store.dispatch('dataAssessment/updateIsLoading', true)
          }
        }
      }
    },
    watch: {
      "dataParams.kelompok": function (val) {
        this.$store.dispatch('dataPTN/getDataPTN', val)
      },
      ptn (val) {
        var data = {
          ptn: val,
          kelompok: this.dataParams.kelompok.toLowerCase()
        }
        this.$store.dispatch('dataPTN/getDataProdi', data)
      },
      studi (val) {
        this.dataParams.id_ptn = val.kode_ptn
        this.dataParams.id_prodi = val.kode_prodi
      }
    }
	}
</script>