<template>
	<v-card class="profil elevation-5">
		<v-card-title class="text-center flex-column pb-0">
			<v-avatar size="150" color="grey lighten-4">
				<v-icon v-if="avatar === null" size="100">mdi-account</v-icon>
				<img 
					v-else
					:src="avatar" 
					alt="profile" 
				/>
			</v-avatar>
			<p class="mt-3">{{nama}}</p>
		</v-card-title>
		<v-card-text class="py-0">
			<v-list class="mt-0">
				<v-list-item
          v-for="(item, i) in items"
          :key="i"
        >
          <v-list-item-icon>
            <v-icon color="primary">
            	{{item.icon}}
            </v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>
            	<span v-if="item.text === null">
            		-
            	</span>
            	<span v-else>
            		{{item.text}}
            	</span>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
			</v-list>
		</v-card-text>
		<v-card-actions class="pa-0">
			<v-btn 
				block depressed 
				large color="primary" 
				@click="$router.push({name: 'biodata'})">
				<v-icon>mdi-pencils</v-icon>
				Edit profil
			</v-btn>
		</v-card-actions>
	</v-card>
</template>

<style scoped>
	.profil{
		position: absolute;
		top: -80px;
		width: 100%;
	}

	@media only screen and (max-width: 960px){
		.profil{ position: relative;top: 0; }
	}
</style>

<script>
	export default {
		data: () => ({
			avatar: '',
			nama: ''
		}),
		created () {
				var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
				this.avatar = peserta.avatar
				this.nama = peserta.nama
		},
		computed: {
			items () {
				var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
				var data = [
					{ icon: 'mdi-home-variant-outline', text: peserta.sekolah.nama },
					{ icon: 'mdi-cellphone', text: peserta.nohp },
					{ icon: 'mdi-map-marker', text: peserta.sekolah.kabupaten },
				]

				return data
			}
		}
	}
</script>