import Vue from 'vue'
import VueRouter from 'vue-router'

import Soal from '@/views/Soal.vue'
import HasilSoal from '@/views/HasilSoal.vue'

import Login from '@/views/Login.vue'
import FormLogin from '@/components/Login/FormLogin'
import FormSignup from '@/components/Login/FormSignup'

import Dashboard from '@/views/Dashboard'

import Biodata from '@/views/Biodata'
import Tes from '@/views/Tes'
import DownloadSoal from '@/views/DownloadSoal'
import UploadJawaban from '@/views/UploadJawaban'
import Fasilitas from '@/views/Fasilitas'

import Assessment from '@/views/Assessment'
import AnalisisNilai from '@/components/Assessment/AnalisisNilai'
import NilaiAkhir from '@/components/Assessment/NilaiAkhir'
import GrafikNilai from '@/components/Assessment/GrafikNilai'
import Peringkat from '@/components/Assessment/Peringkat'
import Rekomendasi from '@/components/Assessment/Rekomendasi'

import SimulasiRanking from '@/views/SimulasiRanking'
import Pembahasan from '@/views/Pembahasan'
import Riwayat from '@/views/Riwayat'
import Fitur from '@/views/Fitur'
import PrintPDF from '@/views/PrintPDF'

import Pemakaian from '@/views/Pemakaian'
import HowtoSoal from '@/components/Pemakaian/Soal'
import HowtoAssessment from '@/components/Pemakaian/Assessment'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login-peserta/:kode?',
    component: Login,
    props: true,
    children: [
      {
        path: '/signup/:kode?',
        name: 'signup',
        component: FormSignup,
        meta: {title: 'Signup'}
      },
      {
        path: '/login-peserta/:kode?',
        name: 'login-peserta',
        component: FormLogin,
        props: true,
        meta: {title: 'Login'}
      }
    ]
  },
  {
    path: '/hasil',
    name: 'hasil-soal',
    component: HasilSoal,
    meta: { requiresAuth: true, title: 'Hasil Soal' },
  },
  {
    path: '/soal/:index',
    name: 'soal',
    component: Soal,
    meta: { requiresAuth: true, title: 'Soal' },
    props: true
  },
  {
    path: '/pembahasan/:id',
    name: 'pembahasan',
    component: Pembahasan,
    props: true,
    meta: { requiresAuth: true, title: 'Pembahasan Soal' },
  },
  {
    path: '/print/:id',
    name: 'print',
    component: PrintPDF,
    props: true,
    meta: { requiresAuth: true, title: 'Cetak Ke PDF' },
  },
  {
    path: '/fitur',
    name: 'fitur',
    component: Fitur,
    props: true,
    meta: { requiresAuth: true, title: 'Fitur' },
  },
  {
    path: '/riwayat',
    name: 'riwayat',
    component: Riwayat,
    props: true,
    meta: { requiresAuth: true, title: 'Riwayat Transaksi' },
  },
  {
    path: '/pemakaian',
    component: Pemakaian,    
    meta: { requiresAuth: true, title: 'Cara Pemakaian' },
    children: [
      {
        path: '/pemakaian',
        component: HowtoSoal,
        name: 'how-soal'
      },
      {
        path: '/pemakaian/assessment',
        component: HowtoAssessment,
        name: 'how-assessment'
      }
    ]
  },
  {
    path: '/simulasi',
    name: 'simulasi',
    component: SimulasiRanking,
    meta: { requiresAuth: true, title: 'Simulasi Rasionalisasi' },
  },
  {
    path: '/assessment/:id?/:token?',
    component: Assessment,
    // meta: { requiresAuth: true, title: 'Assessment' },
    children: [
      {
        path: '/assessment/:id?/:token?/rekomendasi',
        component: Rekomendasi,
        name: 'rekomendasi'
      },
      {
        path: '/assessment/:id?/:token?/peringkat',
        component: Peringkat,
        name: 'peringkat'
      },
      {
        path: '/assessment/:id?/:token?/grafik',
        component: GrafikNilai,
        name: 'grafik'
      },
      {
        path: '/assessment/:id?/:token?',
        component: AnalisisNilai,
        name: 'analisis',
        props: true
      }
    ],
    props: true 
  },  
  {
    path: '/fasilitas/:paket',
    name: 'fasilitas',
    component: Fasilitas,
    meta: { requiresAuth: true, title: 'Fasilitas' },
    props: true
  },
  {
    path: '/tes',
    name: 'tes',
    component: Tes,
    meta: { requiresAuth: true, title: 'Tes' },
  },
  {
    path: '/download-soal',
    name: 'download-soal',
    component: DownloadSoal,
    meta: { requiresAuth: true, title: 'Proses Download...' },
  },

  {
    path: '/upload-jawaban',
    name: 'upload-jawaban',
    component: UploadJawaban,
    meta: { requiresAuth: true, title: 'Proses Upload...' },
  },
  {
    path: '/biodata',
    name: 'biodata',
    component: Biodata,
    meta: { requiresAuth: true, title: 'Biodata' },
  },
  {
    path: '/:kode?',
    component: Dashboard,
    name: 'dashboard',
    meta: { requiresAuth: true, title: 'Dashboard' },
    // props: true 
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  // console.log(to.params.kode)
  // window.localStorage.setItem('kode_afiliasi', JSON.stringify(to.params.kode))
  const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);

  // Find the nearest route element with meta tags.
  const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
  const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

  // If a route with a title was found, set the document (page) title to that value.
  if(nearestWithTitle) document.title = nearestWithTitle.meta.title;
  
  const requiresAuth = to.matched.some(route => route.meta.requiresAuth)
  const isLoggedIn = window.localStorage.getItem('dataPeserta')
  
  // var kode = to.params.kode

  if (requiresAuth && !isLoggedIn){ 
    next({ name: 'login-peserta' })
  }else if(isLoggedIn && to.name === 'login-peserta') {
    next({ name: 'dashboard' })
  }else if (requiresAuth && isLoggedIn) {
    next()
  }else {
    next()
  }
})

export default router
