import { 
	postDataPayment, 
	getDataPayment,
	postCekPayment
} from '@/config/services'

var dataPayment = {
	namespaced: true,
	state: {
		dataPayment: {},
		isLoading: false,
		status: null,
		dataMetode: {},
		detailPayment: {},
	},
	mutations: {
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateDataPayment (state, payload) {
			state.dataPayment = payload
		},
		updateDataMetode (state, payload) {
			state.dataMetode = payload
		},
		updateDetailPayment (state, payload) {
			state.detailPayment = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateStatus (context, payload) {
			context.commit('updateStatus', payload)
		},
		updateDataMetode (context, payload) {
			context.commit('updateDataMetode', payload)
		},
		getDataPayment (context, payload) {
			getDataPayment (payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						// context.dispatch('updateStatus', true)
						context.commit('updateDataPayment', res.data.data)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		postDataPayment (context, payload) {
			postDataPayment (payload.subpaket_tryout, payload)
				.then(res => {
					if (res.status === 200) {													
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDetailPayment', res.data.data)
					} else {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', false)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		postCekPayment (context, payload) {
			postCekPayment (payload)
				.then(res => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', true)
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataPayment