<template>
	<v-footer absolute class="white bt">	
		<v-snackbar
      v-model="snackbar"
      color="primary"
      top
    >
      Silahkan pilih jawaban untuk 
      melanjutkan materi selanjutnya
      <v-btn
        color="white"
        icon
        @click="snackbar = false"
      >
        <v-icon small>mdi-close</v-icon>
      </v-btn>
    </v-snackbar>	
		<div class="content-footer">
			<div class="pagination">
				<v-btn 
					depressed
					outlined
					dark
					color="orange"
					class="px-2 mr-2"
					:class="{ 'hidden': hiddenPrevMateri }"
					:loading="isLoading"
		      v-shortkey="['ctrl', 'arrowleft']"
					@shortkey="prevMateri"					
					@click="prevMateri">					
					<v-icon class="mr-2">mdi-chevron-left</v-icon>	
					materi sebelumnya				
				</v-btn>
				<v-btn 
					depressed
					dark
					color="orange"
					class="pa-0 mr-2"
					:class="{'d-none': hiddenPrev}"
					v-shortkey="['arrowleft']"
					@shortkey="prevSoal(page-1)"				
					@click="prevSoal(page-1)">					
					<v-icon>mdi-chevron-left</v-icon>					
				</v-btn>
				<template v-for="(item, index) in dataSoal">
					<!-- <span v-for="(val, i) in local" :key="i+100"> -->
						<v-btn
							depressed 
							:outlined="actived(index+1, item.id)"
							color="primary" 
							class="pa-0 mx-1"
							:key="index"
							@click="clicked(index+1)">
							{{ index+1 }}
						</v-btn>
					<!-- </span>					 -->
				</template>
				<v-btn 
					depressed
					dark
					color="orange"
					class="pa-0 ml-2"
					:class="{'d-none': hiddenNext}"					
		      v-shortkey="['arrowright']"
					@shortkey="nextSoal(page+1)"				
					@click="nextSoal(page+1)">					
					<v-icon>mdi-chevron-right</v-icon>					
				</v-btn>
				<v-btn 
					depressed
					dark
					color="orange"
					class="px-2 ml-2"
					outlined
					:class="{ 'hidden': hiddenNextMateri }"
					:loading="isLoading"
		      v-shortkey="['ctrl', 'arrowright']"
					@shortkey="nextMateri"				
					@click="nextMateri">					
					materi selanjutnya				
					<v-icon class="ml-2">mdi-chevron-right</v-icon>	
				</v-btn>
			</div>
			<v-btn 
				class="px-2 ml-2" 
				color="success"
				:class="{ 'hidden': hiddenDone }"
				:loading="isLoading"
				@click="isDone">
				Selesai
			</v-btn>
		</div>
	</v-footer>
</template>

<style scoped>	
	.v-btn:not(.v-btn--round).v-size--default{
		min-width: 36px !important;
	}
	.bt{ box-shadow: 0 0 0 1px #cfcfcf; }
	.content-footer{
		margin: 0 auto;
		max-width: 100%;
		overflow: auto;
		display: flex;
	}
	.pagination{
		display: flex;
		list-style: none;
		padding: 0px !important;
	}
	.hidden{ display: none; }
</style>

<script>
	import { mapState } from 'vuex'
	import { uniqBy } from 'lodash'
	import moment from 'moment'

	export default{
		data: () =>({
			page: 1,
			hiddenPrev: true,
			hiddenNext: true,
			hiddenDone: true,
			hiddenNextMateri: null,
			hiddenPrevMateri: null,
			local: [],
			snackbar: false
		}),

		created () {
			this.clicked(this.page)
			var id = this.$route.params.index
			this.local = JSON.parse(window.localStorage.getItem('jawaban'+parseInt(id)))
		},

		watch: {
			dataSoal (val) {
				this.clicked(this.page)
			} 
		},

		computed: {
			...mapState('dataSoal', ['isLoading']),
			dataSoal () {
				var detailSoal = JSON.parse(window.localStorage.getItem('soal_ujian'))
				return detailSoal.submateri[this.$route.params.index].soal
			},
			jawabanTerpilih() {
				return this.$store.getters['dataSoal/getDataJawabanTerpilih']
			}
		},

		methods: {
			clicked (pageNum) {
				// if (this.hiddenPrev === false) {
					this.page = pageNum
					this.$store.dispatch('dataSoal/updatePage', pageNum)
				// }

				var materi = JSON.parse(window.localStorage.getItem('detailPaketSoal'))
				var posisiMateri = materi.submateri.length - 1
				var index = this.$route.params.index

				if (parseInt(index) === 0) {
					this.hiddenPrevMateri = true
				} else {
					this.hiddenPrevMateri = false					
				}

				if (parseInt(index) === posisiMateri) {
					this.hiddenNextMateri = true
				} else {
					this.hiddenNextMateri = false					
				}

				if (this.dataSoal.length === 0 && posisiMateri > parseInt(index)) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else if (this.dataSoal.length === 0 && posisiMateri === parseInt(index)) {
					// this.hiddenNextMateri = true
					this.hiddenPrev = true					
					this.hiddenDone = false
					this.hiddenNext = true	
				}else if (this.page === 1 && this.dataSoal.length > 0) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = false			
				} else if (this.page === this.dataSoal.length && posisiMateri === parseInt(index)){
					// this.hiddenNextMateri = true
					this.hiddenPrev = false					
					this.hiddenDone = false
					this.hiddenNext = true	
				} else if (this.page === this.dataSoal.length && posisiMateri > parseInt(index)){
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else{
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = false	
				}
			},
			prevSoal (pageNum) {
				if (this.hiddenPrev === false) {
					this.page = pageNum
					this.$store.dispatch('dataSoal/updatePage', pageNum)
				}

				var materi = JSON.parse(window.localStorage.getItem('detailPaketSoal'))
				var posisiMateri = materi.submateri.length - 1
				var index = this.$route.params.index

				if (parseInt(index) === 0) {
					this.hiddenPrevMateri = true
				} else {
					this.hiddenPrevMateri = false					
				}

				if (parseInt(index) === posisiMateri) {
					this.hiddenNextMateri = true
				} else {
					this.hiddenNextMateri = false					
				}

				if (this.dataSoal.length === 0 && posisiMateri > parseInt(index)) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else if (this.dataSoal.length === 0 && posisiMateri === parseInt(index)) {
					// this.hiddenNextMateri = true
					this.hiddenPrev = true					
					this.hiddenDone = false
					this.hiddenNext = true	
				}else if (this.page === 1 && this.dataSoal.length > 0) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = false			
				} else if (this.page === this.dataSoal.length && posisiMateri === parseInt(index)){
					// this.hiddenNextMateri = true
					this.hiddenPrev = false					
					this.hiddenDone = false
					this.hiddenNext = true	
				} else if (this.page === this.dataSoal.length && posisiMateri > parseInt(index)){
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else{
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = false	
				}
			},
			nextSoal (pageNum) {
				if (this.hiddenNext === false) {
					this.page = pageNum
					this.$store.dispatch('dataSoal/updatePage', pageNum)
				}

				var materi = JSON.parse(window.localStorage.getItem('detailPaketSoal'))
				var posisiMateri = materi.submateri.length - 1
				var index = this.$route.params.index

				if (parseInt(index) === 0) {
					this.hiddenPrevMateri = true
				} else {
					this.hiddenPrevMateri = false					
				}

				if (parseInt(index) === posisiMateri) {
					this.hiddenNextMateri = true
				} else {
					this.hiddenNextMateri = false					
				}

				if (this.dataSoal.length === 0 && posisiMateri > parseInt(index)) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else if (this.dataSoal.length === 0 && posisiMateri === parseInt(index)) {
					// this.hiddenNextMateri = true
					this.hiddenPrev = true					
					this.hiddenDone = false
					this.hiddenNext = true	
				}else if (this.page === 1 && this.dataSoal.length > 0) {
					// this.hiddenNextMateri = false
					this.hiddenPrev = true					
					this.hiddenDone = true
					this.hiddenNext = false			
				} else if (this.page === this.dataSoal.length && posisiMateri === parseInt(index)){
					// this.hiddenNextMateri = true
					this.hiddenPrev = false					
					this.hiddenDone = false
					this.hiddenNext = true	
				} else if (this.page === this.dataSoal.length && posisiMateri > parseInt(index)){
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = true	
				} else{
					// this.hiddenNextMateri = false
					this.hiddenPrev = false					
					this.hiddenDone = true
					this.hiddenNext = false	
				}
			},
			actived (page, id) {
				var active = []
				if (this.jawabanTerpilih.length > 0) {
					active = this.jawabanTerpilih.filter(data => data.id_soal === id)
				} else if (this.local === null){
					active = []
				}else if (this.local.length > 0) {
					active = this.local.filter(data => data.id_soal === id)
				}

				if (page === this.page || active.length > 0) {
					return false
				}else{
					return true
				}
			},
			nextMateri () {
				if (this.hiddenNextMateri === false) {
					var id = this.$route.params.index
					var data = {
						id: parseInt(id),
						posisi: 'next'
					}
					if (JSON.parse(window.localStorage.getItem('jawaban'+parseInt(id))) === null) {
						this.snackbar = true
					} else {
						this.$store.dispatch('dataSoal/saveDataJawaban', data)	
						this.page = 1			
						this.$store.dispatch('dataSoal/updatePage', this.page)
						this.$store.dispatch('dataSoal/updateDurasiSubmateri', data.id+1)
					}
				}
			},
			prevMateri () {
				if (this.hiddenPrevMateri === false) {
					var soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
					var id = this.$route.params.index
					var newId = parseInt(id) - 1
					var data = {
						id: parseInt(id),
						posisi: 'prev'
					}
					this.$store.dispatch('dataSoal/saveDataJawaban', data)

					this.page = 1			
					this.$store.dispatch('dataSoal/updatePage', this.page)

					var waktu = moment().format('YYYY-MM-DD hh:mm:ss')
					window.localStorage.setItem('durasi_ujian_mulai_'+newId, JSON.stringify(waktu))

					this.$store.dispatch('dataSoal/updateDurasiSubmateri', newId)					
				}
			},
			isDone () {
				var index = this.$route.params.index				
				var data = {
					id: parseInt(index),
					posisi: 'done'
				}
				if (JSON.parse(window.localStorage.getItem('jawaban'+parseInt(index))) === null) {
						this.snackbar = true
					} else {
						this.$store.dispatch('dataSoal/saveDataJawaban', data)
						this.$store.dispatch('dataSoal/postDataJawaban2', data)
						this.$router.push({name: 'upload-jawaban'})				
					}
				// this.$store.dispatch('dataSoal/postDataJawaban', data)				
			}
		}
	}
</script>