<template>
	<v-card class="my-6">
		<v-card-title>
			Rekomendasi Prodi
		</v-card-title>    
		<v-card-text>        
      <v-alert type="info" v-model="alert">
        Fitur Rekomendasi PTN dapat diakses ketika kamu sudah membeli paket premium
      </v-alert>           
      <!-- <div v-if="cekPaketFree(true)"> -->
        <v-overlay 
          v-if="cekPaketFree"
          :value="overlay"
          z-index="1000">
            <div class="d-flex">
              <v-spacer></v-spacer>
              <v-btn
                fab
                x-small
                color="red"
                @click="overlay=false">
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </div>
            <v-img
              src="@/assets/rekomendasi.png"
              class="white--text align-end"
              min-width="320"
              max-width="320"
            >
              <div 
                class="pa-3" 
                style="background-color: rgba(0,0,0,0.5);">
                Fitur Rekomendasi PTN dapat diakses ketika kamu sudah membeli paket premium
                <!-- <div class="d-flex my-3">
                  <v-spacer></v-spacer>
                  <v-btn
                    small
                    text
                    @click="overlay=false"
                    color="white">
                    Oke, saya mengerti
                  </v-btn>                
                </div> -->
                <v-btn
                  light class="mt-3"
                  depressed
                  block
                  @click="$router.push({name: 'fasilitas', params: {paket: 'platinum'}})"
                  color="success">
                  <v-icon class="mr-3">mdi-cart</v-icon>
                  Beli sekarang
                </v-btn>
              </div>
            </v-img>
        </v-overlay>
      <!-- </div> -->
      <v-simple-table v-else>
        <template v-slot:default>
          <thead class="white">
            <tr>
              <th class="text-center" v-for="(item, i) in headers" :key="i">
                {{item.text}}
              </th>
            </tr>
          </thead>
          <tbody v-for="(item, i) in detailAssessment.rekomendasi_ptn" :key="i">
            <template v-if="i === page-1">
              <tr v-if="item.alternative.length === 0">
                <td colspan="9">
                  <v-alert type="info" class="mt-4">
                    <div>
                      Rekomendasi Prodi berdasarkan Nilai Akhir 
                      <i>tidak ditemukan</i>. Perolehan Nilai Akhir masih di bawah Nilai Minimal. Banyak berlatih sub-sub materi uji yang belum tercapai
                    </div>
                  </v-alert>
                </td>
              </tr>
              <tr v-else v-for="(data, index) in item.alternative" :key="index">
                <td class="text-center">{{ (i*10)+1+index }}</td>
                <td class="text-left">{{ data.ptn }}</td>
                <td class="text-left">{{ data.prodi }}</td>
                <td class="text-center">{{ data.kelompok }}</td>
              </tr>
            </template>
          </tbody>
        </template>
      </v-simple-table>
		</v-card-text>
    <v-card-actions>
      <v-pagination
        v-model="page"
        :length="detailAssessment.rekomendasi_ptn.length"
      ></v-pagination>
    </v-card-actions>
	</v-card>
</template>

<script>
	import {mapState} from 'vuex'
  import {flattenDeep, findIndex} from 'lodash'
	export default {
		data: () => ({
			headers: [
        { text: 'No', value: 'calories' },
        { text: 'Universitas', value: 'nama_peserta' },
        { text: 'Program Studi', value: 'sekolah.nama' },
        { text: 'Kelompok Uji', value: 'sekolah.provinsi' },
      ],
      page: 1,
      overlay: false,
      alert: false
		}),

    // methods: {
      
    // },

    computed: {
      ...mapState('dataAssessment', ['detailAssessment']),
      ...mapState('dataDashboard', ['dataDashboard']),
      jumlahPaket () {
        var jadi = this.dataDashboard.map(paket => {
          return paket.paket.map(data => {
            return data
          })
        })

        return flattenDeep(jadi)
      },
      cekPaketFree () {
        var paketPremium = this.jumlahPaket.filter(e => {
          // console.log(e.status)
          if (e.nama_paket.indexOf('FREE') === -1) {
            return e
          }
        })
        var hasil = findIndex(paketPremium, o => o.status === true)
        // console.log(hasil)        
        if (this.$route.params.id !== undefined && this.$route.params.token !== undefined) {
          return false
        }else if (hasil === -1) {
          this.alert = true
          this.overlay = true
          return true
        } else {
          return false
        }              
      },
    },

    // created () {
    //   this.cekPaketFree()
    // }
	}
</script>