<template>
	<v-form v-model="valid" ref="form">
		<v-row>
			<!-- <v-col cols="12" lg="3" md="4" sm="12" class="pa-1">
				<v-avatar color="grey lighten-4" size="100">
					<v-icon v-if="data.avatar === null" large>mdi-account</v-icon>
					<img 
						v-else
						:src="data.avatar" 
						alt="profile" 
					/>
				</v-avatar>
			</v-col>
			<v-col cols="12" lg="9" md="8" sm="12" class="pa-1 d-flex align-center" style="height: 100px;">
				<div style="width: 100%">
					<v-file-input 
						accept="image/jpeg" 
						label="Upload gambar"
						show-size
						counter
						@change="uploadGambar"
						class="mr-2"
					></v-file-input>
					<v-alert v-model="msgSize" type="error" dense dismissible>
						Maksimal gambar 500 KB
					</v-alert>
				</div>
			</v-col> -->
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Nama Lengkap</span>
			</v-col>
			<v-col cols="12" lg="9" md="8" sm="12" class="pa-1">
				<v-text-field
          label="Nama Lengkap"
          class="mr-2"
          outlined
          dense
          v-model="data.nama_lengkap"
          :rules="requiredRule"
        ></v-text-field>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">E-mail</span>
			</v-col>
			<v-col cols="12" lg="9" md="8" sm="12" class="pa-1">
				<v-text-field
          label="E-mail"
          outlined
          class="mr-2"
          dense
          v-model="data.email"
          :rules="emailRule"
        ></v-text-field>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">No. Hp</span>
			</v-col>
			<v-col cols="12" lg="3" md="8" sm="12" class="pa-1">
				<v-text-field
          label="No. Hp"
          outlined
          counter
          dense
          v-model="data.nohp"
          :rules="hpRule"
        ></v-text-field>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">No. WhatsApp</span>
			</v-col>
			<v-col cols="12" lg="3" md="8" sm="12" class="pa-1">
				<v-text-field
          label="No. WhatsApp"
          class="mr-2"
          outlined
          counter
          dense
          v-model="data.nohp_wa"
          :rules="hpRule"
        ></v-text-field>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Instagram</span>
			</v-col>
			<v-col cols="12" lg="3" md="8" sm="12" class="pa-1">
				<v-text-field
          label="Instagram"
          outlined
          dense
          v-model="data.sosmed_ig"
          :rules="requiredRule"
        ></v-text-field>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Facebook</span>
			</v-col>
			<v-col cols="12" lg="3" md="8" sm="12" class="pa-1">
				<v-text-field
          label="Facebook"
          class="mr-2"
          outlined
          dense
          v-model="data.sosmed_fb"
          :rules="requiredRule"
        ></v-text-field>
			</v-col>
		</v-row>
			<!--  -->
		<v-divider class="mb-3"></v-divider>
		<div class="title mb-3 black--text">Informasi Asal Sekolah</div>
		<v-row>
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Provinsi</span>
			</v-col>
			<v-col cols="12" lg="3" md="8" sm="12" class="pa-1">
				<v-autocomplete
          label="Provinsi"
          outlined
          dense
          :items="dataProv"
          item-text="nama"
          return-object
          v-model="provinsi"
        ></v-autocomplete>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Kabupaten</span>
			</v-col>
			<v-col cols="12" lg="3" md="8" sm="12" class="pa-1">
				<v-autocomplete
          label="Kabupaten"
          class="mr-2"
          outlined
          dense
          :items="dataKab"
          item-text="nama"
          return-object
          :disabled="provinsi === undefined ? true:false"
          v-model="kabupaten"
        ></v-autocomplete>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Nama Sekolah</span>
			</v-col>
			<v-col cols="12" lg="9" md="8" sm="12" class="pa-1">
				<v-autocomplete
          label="Nama Sekolah"
          class="mr-2"
          outlined
          dense
          :search-input.sync="sekolah"
          :disabled="kabupaten === undefined ? true:false"          
          :loading="isLoading"
          hide-selected
          hide-no-data
          :items="dataSekolah"
          item-text="nama"
          item-value="id"
          v-model="data.id_sekolah"
          :rules="requiredRule"
        ></v-autocomplete>
			</v-col>
		</v-row>
			<!--  -->
		<v-row>
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-3">
				<span class="font-weight-bold">Tahun Kelulusan</span>
			</v-col>
			<v-col cols="12" lg="9" md="8" sm="12" class="pa-1">
				<div style="width: 200px;">
					<v-select
	          label="Tahun lulus"
	          outlined
	          dense
	          :items="tahun"
          	v-model="data.tahun_kelulusan"
	          :rules="tahunRule"
	        ></v-select>
				</div>
			</v-col>
			<!--  -->
			<v-col cols="12" lg="3" md="4" sm="12" class="pa-1"></v-col>
			<v-col cols="12" lg="9" md="8" sm="12" class="pa-1">
				<v-checkbox
		      v-model="checkbox"
		      :rules="[v => !!v || 'Anda harus mennyetujuinya']"
		      label="Saya menerima kebijakan yang diberikan oleh Sobat UTBK"
		      class="mt-0"
		      required
		    ></v-checkbox>
			</v-col>
		</v-row>
		<center>
			<v-btn 
			:disabled="!valid"
			type="submit"
			@click.prevent="validate" 
			:loading="isLoading"
			color="primary" class="mt-3">
				Simpan dan lanjutkan
			</v-btn>
		</center>
	</v-form>
</template>

<script>
	import { mapState } from 'vuex'
	export default {
		data: () => ({
			valid: true,
			checkbox: false,
			sekolah: '',
			provinsi: {},
			kabupaten: {},
			data: {
				nama_lengkap: '',
				nohp: '',
				nohp_wa: '',
				sosmed_ig: '',
				sosmed_fb: '',
				id_sekolah: 0,
				tahun_kelulusan: 0,
				avatar: '',
				email: ''
			},
			img: '',
			requiredRule: [v => !!v || 'Tidak boleh kosong'],
			msgSize: false
		}),

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			this.data.nama_lengkap = peserta.nama
			this.data.nohp = peserta.nohp
			this.data.email = peserta.sosmed.email
			this.data.avatar = peserta.avatar
			this.data.nohp_wa = peserta.sosmed.whatsapp
			this.data.sosmed_ig = peserta.sosmed.instagram
			this.data.sosmed_fb = peserta.sosmed.facebook
			this.data.tahun_kelulusan = peserta.sekolah.tahun_kelulusan	
			this.data.id_sekolah = peserta.sekolah.id	
			this.$store.dispatch('dataBiodata/getDataProv')
			this.$store.dispatch('dataBiodata/updateIsLoading', true)		
		},

		watch: {
			"provinsi": function (val) {
				this.$store.dispatch('dataBiodata/getDataKab', val)
				this.$store.dispatch('dataBiodata/updateIsLoading', true)									
			},
			"kabupaten": function (val) {		
				var data = {
					kabupaten: val,
					provinsi: this.provinsi
				}		
				this.$store.dispatch('dataBiodata/getDataSekolah', data)
				this.$store.dispatch('dataBiodata/updateIsLoading', true)									
			}
		},

		methods: {
			uploadGambar (val) {
				// console.log(val.size)
				if (val.size > 512000) {
					this.msgSize = true
				} else {
					this.msgSize = false
					var vm = this
					var reader = new FileReader()
	        reader.readAsDataURL(val)
	        reader.onload = function () {
	          vm.img = reader.result
	        };
				}				
			},
			validate() {
        if (this.$refs.form.validate()) {        	
        	this.data.avatar = this.img
          this.$store.dispatch('dataBiodata/postDataBiodata', this.data)
          this.$store.dispatch('dataBiodata/updateIsLoading', true)
        }      
      }
		},

		computed: {
			...mapState('dataBiodata', ['dataProv', 'dataKab', 'dataSekolah', 'isLoading']),
			tahun () {
				var now = new Date().getUTCFullYear()
				var hasil = [now, now-1, now-2]
				return hasil
			},
			emailRule () {
        return [
          ...this.requiredRule,
          v => /.+@.+\..+/.test(v) || 'Data berupa e-mail'
        ]
      },
      hpRule () {
        return [
          v => (v && v.length >= 10) || 'No lebih dari 11 nomor',
          ...this.requiredRule,
        ]
      },
      tahunRule () {
        return [
          ...this.requiredRule,
        ]
      },
		}
	}
</script>