<template>
	<div>
		<v-toolbar flat class="px-9 bb mb-7">
			<v-toolbar-title>
				<img src="@/assets/logo.png" alt="logo" class="logo">
			</v-toolbar-title>
			<v-spacer></v-spacer>
			<!-- <span class="mr-3">Sisa Waktu</span> -->
			<v-btn
				depressed
				small
				rounded
				:loading="isLoading"
				class="mr-3"
				color="warning"
				@click="pause">
				<v-icon small class="mr-2">mdi-pause</v-icon>
				pause
			</v-btn>
			<v-chip outlined large class="py-3">
				{{time.h}}:{{time.m}}:{{time.s}}
			</v-chip>
		</v-toolbar>

		<TimeOut :visible="visible"/>
		<TimeOutSubmateri :visible="visible2"/>
	</div>
</template>

<style scoped>
	.bb{ box-shadow: 0 0 0 1px #cfcfcf; }
	.logo{ height: 40px; }
	.v-chip.v-size--large{ height: 40px; }

	@media only screen and (max-width: 366px) {
		.v-application .px-9{
			padding-right: 0px !important;
    	padding-left: 0px !important;
		}
		.v-application .mb-7{
			margin-bottom: 16px !important;
		}
		.v-application .py-3{
			padding-top: 4px !important;
    	padding-bottom: 4px !important;
		}
		.logo{ height: 30px; }
	}
</style>

<script>
	import moment from 'moment'
	import TimeOut from '@/components/Soal/TimeOut'
	import TimeOutSubmateri from '@/components/Soal/TimeOutSubmateri'
	import { mapState } from 'vuex'
	import { findIndex } from 'lodash'

	export default {
		data: () => ({
			time: {
				h: 0,
				m: 0,
				s: 0
			},
			actualTime: moment().format('X'),
			lamaUjian: null,
			waktu1: 0,
			visible: false,
			visible2: false
		}),
		created() {			
			this.$store.dispatch('dataSoal/updateDurasiSubmateri', this.$route.params.index)
		},
		mounted () {
			// this.setStartTime()	
			this.compute()
			this.addOneSecondToActualTimeEverySecond()
		},
		components: {
			TimeOutSubmateri,
			TimeOut
		},
		computed: {
			...mapState('dataSoal', ['status', 'isLoading', 'durasiSubmateri'])
		},
		methods: {
			addOneSecondToActualTimeEverySecond () {
		      var component = this
		      component.actualTime = moment().format('X')
		      setTimeout(function(){
		      		component.getDiffInSeconds()
							// this.$store.dispatch('updateWaktu', this.waktu1)
		      		component.compute()
		          component.addOneSecondToActualTimeEverySecond()
		      }, 1000);
		  },
		  getDiffInSeconds () {
		    return this.durasiSubmateri - this.actualTime
		  },
		  // setStartTime () {
				// this.waktu1 = moment(this.durasiSubmateri).format('X')	
		  // },
		  compute () {
		    var duration = moment.duration(this.getDiffInSeconds(), "seconds")
		    if (duration > 1) {
		    	this.time.h = duration.hours() < 10 ? '0'+duration.hours() : duration.hours()
			    this.time.m = duration.minutes() < 10 ? '0'+duration.minutes() : duration.minutes()
			    this.time.s = duration.seconds() < 10 ? '0'+duration.seconds() : duration.seconds()

			    this.$store.dispatch('dataSoal/saveDurasiSubmateri', {
			    	index: parseInt(this.$route.params.index),
			    	durasi: (duration.minutes()*60)+duration.seconds()
			    })
		    	this.visible2 = false
		    } else {
		    	let durasi_submateri = JSON.parse(window.localStorage.getItem('durasi_submateri'))
		    	let hasil = findIndex(durasi_submateri, (o) => o >= 1)
		    	if (hasil === -1) {
		    		this.visible = true
		    	} else {		    		
		        this.$store.dispatch('dataSoal/saveDurasiSubmateri', {
		          index: parseInt(this.$route.params.index),
		          durasi: 0
		        })
		        this.time.h = 0
		        this.time.m = 0
		        this.time.s = 0
		    		this.visible2 = true
		    	}
		    }
		  },
		  pause () {
		  	this.$store.dispatch('dataSoal/pauseUjian', {
		  		inedx: parseInt(this.$route.params.index),
		  		durasi: (this.time.m*60)+this.time.s
		  	})
		  }
		},
	}
</script>