import { getDataDashboard } from '@/config/services'

var dataDashboard = {
	namespaced: true,
	state: {
		isLoading: false,
		status: null,
		dataDashboard: []
	},
	mutations: {
		updateDataDashboard (state, payload) {
			state.dataDashboard = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataDashboard (context, payload) {
			getDataDashboard (payload)
				.then(res => {
					if (res.status === 200) {
						var arr = new Array(res.data)
						context.commit('updateDataDashboard', arr)
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataDashboard