<template>
	<v-card class="my-6">
		<v-card-title>
			Peserta Terbaik
		</v-card-title>
		<v-card-text>
      <v-simple-table>
        <template v-slot:default>
          <thead class="white">
            <tr>
              <th class="text-center" v-for="(item, i) in headers" :key="i">
                {{item.text}}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="dataHasil.length === 0">
              <td colspan="9">
                <v-alert type="info" class="mt-4">
                  <div>Data tidak ditemukan silahkan coba lagi</div>
                </v-alert>
              </td>
            </tr>
            <tr v-else v-for="(item, i) in dataHasil" :key="i">
              <td class="text-left">{{ rank+i }}</td>
              <td class="text-left">{{ item.nama_peserta }}</td>
              <td class="text-left">{{ item.sekolah.nama }}</td>
              <td class="text-left">{{ item.sekolah.provinsi }}</td>
              <td class="text-left">{{ item.ptn.kelompok_ujian }}</td>
              <td class="text-left">{{item.ptn.nama}}</td>
              <td class="text-left">{{ item.ptn.prodi }}</td>
              <td class="text-left">{{ parseFloat(item.nilai_akhir).toFixed(2) }}</td>
              <!-- <td class="text-center">{{ item.rank }}</td> -->
            </tr>
          </tbody>
        </template>
      </v-simple-table>
		</v-card-text>
    <v-card-actions>
      <!-- <v-spacer></v-spacer> -->
      <v-pagination
        v-model="pagination.current"
        :length="pagination.last_page > 10?10:pagination.last_page"
      ></v-pagination>
    </v-card-actions>
	</v-card>
</template>

<script>
	import {mapState} from 'vuex'
  import {flattenDeep, findIndex} from 'lodash'
	export default {
		props: ['dataPeringkat'],
		data: () => ({
			headers: [
        { text: 'Ranking', value: 'calories', sortable: false, },
        { text: 'Nama', value: 'nama_peserta' },
        { text: 'Asal Sekolah', value: 'sekolah.nama' },
        { text: 'Provinsi', value: 'sekolah.provinsi' },
        { text: 'Kelompok Uji', value: 'ptn.kelompok_ujian', sortable: false, },
        { text: 'PTN', value: 'ptn.nama' },
        { text: 'Prodi', value: 'ptn.prodi' },
        { text: 'Nilai Akhir', value: 'nilai_akhir' },
        // { text: 'Ranking', value: 'rank' },
      ]
		}),
		computed: {
			...mapState('dataAssessment', ['isLoading', 'pagination']),
      ...mapState('dataDashboard', ['dataDashboard']),
      jumlahPaket () {
        var jadi = this.dataDashboard.map(paket => {
          return paket.paket.map(data => {
            return data
          })
        })

        return flattenDeep(jadi)
      },
      dataHasil () {   
        var hasil1 = [] 
      	var paketPremium = this.jumlahPaket.filter(e => {
          // console.log(e.status)
          if (e.nama_paket.indexOf('FREE') === -1) {
            return e
          }
        })
        var hasil = findIndex(paketPremium, o => o.status === true)
        if (this.$route.params.id !== undefined && this.$route.params.token !== undefined) {
          Object.keys(this.dataPeringkat).map((key, index) => {
            hasil1[index] = this.dataPeringkat[key]
          })
        }else if (hasil === -1) {
          this.$emit('free', true)
        } else {
          Object.keys(this.dataPeringkat).map((key, index) => {
            hasil1[index] = this.dataPeringkat[key]
          })
        }
        
        return hasil1
      },
      rank () {
        return (10 * (this.pagination.current-1)) + 1
      }
		},
    watch: {
      "pagination.current": function (val) {
        // console.log(val)
        this.pagination.page = val
        this.$store.dispatch('dataAssessment/getDataPeringkat', this.pagination)
        // this.$store.dispatch('dataAssessment/updateIsLoading', true)
      }
    }
	}
</script>