<template>
	<div>
		<div class="custom-control custom-radio" v-for="(item, index) in jawaban" :key="index">
		  <!-- <span class="mr-3">{{item.huruf}}.</span> -->
		  <input type="radio" name="jawaban" readonly :id="`jawaban${index}`" v-model="terjawab" :value="item.huruf" class="custom-control-input">
		  <label class="custom-control-label2" v-html="item.teks"></label>
		</div>

		<v-chip color="success" class="mt-3">
			Kunci Jawabannya adalah <b class="mx-3">({{selected}})</b>
		</v-chip>
	</div>
</template>

<style>
	.custom-control p{ margin-bottom: 0px; }
	.custom-control{
		position: relative;
    display: block;
    min-height: 1.5rem;
    padding-left: 1.5rem;
	}
	input[type=radio] {
    box-sizing: border-box;
    padding: 0;
	}
	.custom-control-input {
    position: absolute;
    z-index: -1;
    opacity: 0;
	}
	.custom-control-label2 {
    margin-bottom: 0;
	}
	label {
    display: inline-block;
	}
	.custom-control-label2::before {
    border-radius: 50%;
	}
	.custom-control-label2::before {
    position: absolute;
    top: .25rem;
    left: 0;
    display: block;
    width: 1rem;
    height: 1rem;
    pointer-events: none;
    content: "";
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: #dee2e6;
	}
	.custom-control-label2::after {
    position: absolute;
    top: .25rem;
    left: 0;
    display: block;
    width: 1rem;
    height: 1rem;
    content: "";
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
	}
	input[type=radio]:checked + .custom-control-label2::before {
	  background-color: #599CDE;
	}
</style>


<script>
	import { mapState } from 'vuex'
	export default {
		props: ['jawaban', 'terjawab'],
		data: () => ({
			selected: ''
		}),
		created () {
			this.jawaban.map(data => {
				if (data.kunci) {
					this.selected = data.huruf
				}
			})
		}
	}
</script>