import { postDataRanking, getDataHistory } from '@/config/services'

var dataRanking = {
	namespaced: true,
	state: {
		dataRanking: [],
		isLoading: false,
		status: null,
		dataHistory: []
	},
	mutations: {
		updateIsLoading (state, payload) {
			state.isLoading = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateDataRanking (state, payload) {
			state.dataRanking = payload
		},
		updateDataHistory (state, payload) {
			state.dataHistory = payload
		}
	},
	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateDataHistory (context, payload) {
			context.commit('updateDataHistory', payload)
		},
		getDataHistory (context, payload) {
			getDataHistory (payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDataHistory', res.data.data)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},
		postDataRanking (context, payload) {
			postDataRanking (payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
						context.commit('updateDataRanking', res.data.data)
					} else {
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', false)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataRanking