<template>
	<v-card class="pa-9 mx-md-12">
		<v-card-text>
			<center>
				<p class="display-1 black--text text-capitalize">
					selamat skor ujian kamu adalah
				</p>
				<v-row class="mb-5">
					<v-col cols="12" md="6" sm="12">
						<v-progress-circular 
							:value="value"
							:size="240"
							:width="20"
							:rotate="-90"
							color="yellow"
						>
							<div class="display-3 black--text font-weight-bold">
								{{parseFloat(score.score).toFixed(2)}}
							</div>
						</v-progress-circular>
					</v-col>
					<v-col cols="12" md="6" sm="12" class="black--text text-left keterangan">
						<div>
							<div class="mb-2">
								<v-icon color="primary" class="mr-5">mdi-clock-outline</v-icon>
								{{durasi}} Waktu Pengerjaan
							</div>
							<div class="mb-2">
								<v-icon color="success" class="mr-5">mdi-stop</v-icon>
								{{ score.detail.benar }} Jawaban Benar
							</div>
							<div class="mb-2">
								<v-icon color="red" class="mr-5">mdi-stop</v-icon>
								{{ score.detail.salah }} Jawaban Salah
							</div>
							<div class="mb-2">
								<v-icon color="grey" class="mr-5">mdi-stop</v-icon>
								{{ score.detail.tidak_dijawab }} Soal Tidak Dijawab
							</div>
						</div>
					</v-col>
				</v-row>
				<v-btn color="primary" @click="assessment" v-if="activeAssessment">
					lihat assessment
				</v-btn>				
				<v-btn color="primary" @click="nextTest" v-else>tes selanjutnya</v-btn>				
			</center>
		</v-card-text>
	</v-card>
</template>

<style scoped>
	.keterangan{
		height: 264px;
		display: flex;
		align-items: center;
	}
</style>

<script>
	import { mapState } from 'vuex'
	import { findIndex } from 'lodash'
	import moment from 'moment'
	import {postFinishUjian} from '@/config/services'

	export default{
		data () {
      return {
        interval: {},
        value: 0,
        score: {}   
      }
    },

    created () {
    	var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			var id = window.localStorage.getItem('id_paket_soal')
			var dataPaket = {
				id_peserta: peserta.id,
				id_paket_soal: id
			}
			this.$store.dispatch('dataSoal/getDataPaketSoal', dataPaket)
			// window.localStorage.removeItem('info_ujian')
			this.score = JSON.parse(window.localStorage.getItem('score'))
    },

    computed: {
    	...mapState('dataSoal', ['dataPaketSoal']),
    	...mapState('dataDashboard', ['dataDashboard']),
			activeAssessment () {
				var find = findIndex(this.dataPaketSoal, (data) => data.completed === 0);
				// return find
				if (find === -1) {
					return true
				} else {
					return false
				}
			},
    // 	durasi () {
    // 		var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))
		  // 	var time = moment(info_ujian.timestamp_mulai).format('h:mm').toString()
		  // 	var hasil = moment().format('h:mm').toString()
				// return moment.duration(hasil).asMinutes() - moment.duration(time).asMinutes()
    // 	}
    	durasi () {
    		var durasi = this.score.duration.value
    		return moment.utc(durasi*1000).format('HH:mm:ss');
    	}
    },
  //   beforeDestroy () {
  //     clearInterval(this.interval)
  //   },
		methods: {
      // this.interval = setInterval(() => {
      //   if (this.value === 100) {
      //     return (this.value = 0)
      //   }
      //   this.value += 10
      // }, 1000)
      nextTest () {
				const soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
      	soal_ujian.submateri.map((submateri, i) => window.localStorage.removeItem('jawaban'+i))
				window.localStorage.removeItem('info_ujian')
				window.localStorage.removeItem('submisi')
				window.localStorage.removeItem('durasi_submateri')
      	this.$router.push({ name: 'tes' })
				window.localStorage.removeItem('soal_ujian')
      },
      assessment () {
				const soal_ujian = JSON.parse(window.localStorage.getItem('soal_ujian'))
				soal_ujian.submateri.map((submateri, i) => window.localStorage.removeItem('jawaban'+i))
				var info_ujian = JSON.parse(window.localStorage.getItem('info_ujian'))
				postFinishUjian ({
					ujian_id: info_ujian.id,
					detail_id: info_ujian.detail_id
				})
				.then(res => {
					if (res.status === 200) {
						window.localStorage.removeItem('soal_ujian')
						window.localStorage.removeItem('submisi')						
						window.localStorage.removeItem('durasi_submateri')
						this.$router.push({name: 'analisis'})
					}
				})	
			}
    },
	}
</script>