<template>
	<v-row class="mt-6">
		<v-col cols="12" md="3" v-for="(item, index) in button" :key="index">
			<v-btn 
				v-if="$route.params.id === undefined"
				:outlined="actived(item.name)"
				dark
				depressed
				color="pink" 
				tile block 
				large
				@click="$router.push({ name: item.name })">
				{{item.text}}
			</v-btn>
			<v-btn 
				v-else
				:outlined="actived(item.name)"
				dark
				depressed
				color="pink" 
				tile block 
				large
				@click="$router.push({ name: item.name, params: { id: $route.params.id, token: $route.params.token } })">
				{{item.text}}
			</v-btn>
		</v-col>
	</v-row>
</template>

<script>
	export default {
		data: () => ({
			button: [
				{ text: 'analisis nilai', name: 'analisis' },
				{ text: 'grafik nilai', name: 'grafik' },
				{ text: 'peringkat', name: 'peringkat' },
				{ text: 'Rekomendasi PTN', name: 'rekomendasi' }
			]
		}),
		methods: {
			actived (name) {
				if (this.$route.name === name) {
					return false
				} else {
					return true
				}
			}
		}
	}
</script>