<template>
	<div>
		<v-card class="mb-6">
			<v-card-title>
				Deskripsi Pembelian
			</v-card-title>

			<v-card-text class="black--text">
				<p>{{dataPayment.paket.subpaket}}</p>
			</v-card-text>
		</v-card>
		<!--  -->
		<v-card>
			<v-card-title>
				Detail Pembayaran
			</v-card-title>

			<v-card-text class="black--text d-flex">
				<div class="subtitle text-uppercase">{{dataPayment.kode_pembayaran}}</div>
				<v-spacer></v-spacer>
				<b class="title red--text">
					{{Intl.NumberFormat('id-ID', { maximumSignificantDigits: 4, style: 'currency', currency: 'IDR' }).format(dataPayment.paket.biaya)}}
				</b>
			</v-card-text>
		</v-card>
		<div class="pt-4 d-flex">
			<v-spacer></v-spacer>
			<v-btn color="primary" depressed class="ml-3" @click="next">
	    	lanjutkan pembayaran
	    </v-btn>
		</div>
	</div>
</template>


<script>

	export default {
		props: ['dataPayment'],
		methods: {
			next () {				
				this.step = 2				
				this.$emit('next', this.step)
			}
		}
	}
</script>