<template>
	<v-row>
		<v-col cols="12" md="8" sm="12">	
			<div class="headline font-weight-bold">Assessment</div>
			<div class="d-flex my-6">
				<v-btn color="indigo" class="d-none d-md-flex" dark depressed tile style="height: 40px;">
					paket tryout
				</v-btn>
				<v-select
	        :items="assessment"
	        item-text="paket_soal"
	        return-object
	        v-model="paket"
	        label="Pilih paket tryout"
	        outlined
	        shiped
	        dense
	        color="indigo"
	      ></v-select>
			</div>
		  <v-simple-table>
		    <template v-slot:default>
		      <thead>
		        <tr>
		          <th class="text-left">Perguruan Tinggi</th>
		          <th class="text-left">Program Studi</th>
		          <th class="text-center">Kode</th>
		          <th class="text-center">Kelompok</th>
		        </tr>
		      </thead>
		      <tbody>
		        <tr v-for="(item, i) in detailAssessment.ptn_pilihan" :key="i">
		          <td>{{ item.nama }}</td>
		          <td class="text-left">{{ item.prodi }}</td>
		          <td class="text-center">{{ item.kode }}</td>
		          <td class="text-center">{{ item.kelompok }}</td>
		        </tr>
		      </tbody>
		    </template>
		  </v-simple-table>
		  <v-btn color="indigo" dark class="mt-6 hiddenOnPrint" @click="print">
		  	<v-icon class="mr-3">mdi-file-export</v-icon>
		  	Export ke pdf
		  </v-btn>
		</v-col>
		<v-col cols="12" md="4" sm="12" class="paket-tryout">
			<Profil/>
		</v-col>
	</v-row>
</template>

<style>
	.paket-tryout .v-text-field--outlined .v-input__slot {
     border-radius: 0px; 
	}
</style>

<script>
	import { getDataAssessment, getDetailAssessment } from '@/config/services'
	import { mapState } from 'vuex'
	import Profil from '@/components/Assessment/Profil'

	import axios from 'axios'

  export default {
		components: {
			Profil
		},
    data () {
      return {
      	paket: 0,
      	assessment: []
      }
    },
    created () {
    	if (this.$route.params.id === undefined && this.$route.params.token === undefined) {				
	    	var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))    	
	    	getDataAssessment (peserta.id)
	    		.then(res => {
	    			if (res.status === 200) {
	    				this.assessment = res.data
	    				this.paket = res.data[0]
	    			}
	    		})
			} else {
				const http = axios.create({
					baseURL: 'https://app.sobatutbk.com/api/v1/',
					headers: {
						'Authorization': 'Bearer '+this.$route.params.token
					}
				})
				
				this.getDataAssessment(http, parseInt(this.$route.params.id))
			}
    },
    computed: {
    	...mapState('dataAssessment', ['detailAssessment']),
    },
    watch: {
    	paket (val) {
    		var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
    		var data = {
    			id_peserta: peserta.id,
    			id_ujian: val.id_ujian
    		}
    		window.localStorage.setItem('id_paket_soal', val.id_paket_soal)
    		this.$store.dispatch('dataAssessment/getDetailAssessment', data)
    		this.$store.dispatch('dataAssessment/postDataScoreAll', {id_ujian: data.id_ujian})
    	}
    },
    methods: {
    	print () {
    		// window.print()
    		this.$router.push({name: 'print', params: {id: this.paket}})
    	},
    	getDataAssessment (http, payload) {
				http.get('peserta/'+payload+'/assessment/')
					.then(res => {
	    			if (res.status === 200) {
	    				this.assessment = res.data
	    				this.paket = res.data[0]
	    				window.localStorage.setItem('id_paket_soal', res.data[0].id_paket_soal)
	    			}
	    		})
			},
    }
  }
</script>