<template>
	<v-row class="mt-6">
		<v-col cols="12" sm="12">
			<v-card elevation="4">
				<v-card-title>
					PERINGKAT TRYOUT
				</v-card-title>
				<v-card-text>
					<v-simple-table>
						<template v-slot:default>
				      <thead class="white">
				        <tr>
				          <th class="text-left">Pilihan PTN</th>
				          <th class="text-left">Program Studi</th>
				          <th class="text-center">Daya Tampung</th>
				          <th class="text-center">Peringkat Sekolah</th>
				          <th class="text-center">Peringkat Nasional</th>
				          <th class="text-center">Keterangan</th>
				        </tr>
				      </thead>
				      <tbody>
				        <tr v-for="item in detailAssessment.ptn_pilihan" :key="item.ptn">
				          <td>{{ item.nama }}</td>
				          <td class="text-left">{{ item.prodi }}</td>
				          <td class="text-center">{{ item.daya_tampung }}</td>
				          <td class="text-center">{{ item.peringkat_ujian.sekolah }}</td>
				          <td class="text-center">{{ item.peringkat_ujian.nasional }}</td>
				          <td class="text-center" v-if="item.memenuhi">Memenuhi</td>
	          			<td class="text-center" v-else>Belum Memenuhi</td>
				        </tr>
				      </tbody>
				    </template>
				  </v-simple-table>
				</v-card-text>
			</v-card>
		</v-col>
		<v-col cols="12" md="4" sm="12" class="mt-3" 
			v-for="(item, i) in detailAssessment.peringkat_tryout" 
			:key="i">
			<v-card>
				<v-card-title>
					{{item.kelompok_uji}}
				</v-card-title>
				<v-card-text class="black--text">
					<v-row no-gutters>
						<v-col cols="12" sm="6">							
							<center>
								<v-progress-circular 
									:value="value"
									:size="103"
									:width="10"
									:rotate="-90"
									color="yellow"
								>
									<div class="black--text title font-weight-bold">
										{{dataScore[item.kelompok_uji.toLowerCase()]}}
									</div>
								</v-progress-circular>	
							</center>						
						</v-col>
						<v-col cols="12" sm="6">
							<div class="d-flex align-center" style="height: 103px;">
								<div>
									<div>Peringkat <b>{{item.peringkat.self}}</b></div>
									<div>Dari {{item.peringkat.all}} Peserta</div>
								</div>
							</div>
						</v-col>
					</v-row>
				</v-card-text>
			</v-card>
		</v-col>
	</v-row>
</template>

<style scoped>
	thead tr th {
		font-weight: bold !important;
		color: #000 !important;
		text-transform: uppercase;
		background-color: #f4f4f4;
	}
	table tr:nth-child(even){ background-color: #f4f4f4; }
</style>

<script>
	import {mapState} from 'vuex'
	export default{
		data () {
      return {
        interval: {},
        value: 0,
      }
    },
    computed: {
    	...mapState('dataAssessment', ['detailAssessment', 'dataScore'])
    },
    // methods: {
    // 	nilaiAkhir (paket) {
    // 		return this.dataScore[paket.toLowerCase()]
    // 	}
    // },
    beforeDestroy () {
      clearInterval(this.interval)
    },
		mounted () {
      this.interval = setInterval(() => {
        if (this.value === 100) {
          return (this.value = 0)
        }
        this.value += 10
      }, 1000)
    },
	}
</script>