<template>
	<v-card flat class="mx-12 ba pa-7 scroll" max-height="65vh" height="65vh">
		<v-skeleton-loader 
			v-if="isLoading"
			type="paragraph"
		></v-skeleton-loader>
		<template v-else-if="dataSoal.length === 0">
			<v-card-text class="title">
				Maaf soal belum ada
			</v-card-text>
		</template>
		<template v-else v-for="(soal, index) in dataSoal">
			<span :key="index" v-if="index+1 === page">
				<v-card-text class="black--text">
					<div v-html="soal.pertanyaan"></div>
				</v-card-text>
				<v-card-actions>
					<Jawaban :jawaban="soal.jawaban" :id="soal.id" />
				</v-card-actions>
			</span>
		</template>
		
	</v-card>
</template>

<style scoped>
	.ba{ box-shadow: 0 0 0 1px #cfcfcf; }
	.scroll{ overflow: auto; }
	@media only screen and (max-width: 366px) {
		.v-application .mx-12{
			margin-right: 8px !important;
    	margin-left: 8px !important;
		}
		.v-application .pa-7{
			padding: 8px !important;
		}
	}
</style>

<script>
	import { mapState } from 'vuex'
	import { shuffle } from 'lodash'
	import Jawaban from './Jawaban'

	export default{
		data: () => ({}),
		components: {
			Jawaban
		},
		computed: {
			...mapState('dataSoal', ['page', 'isLoading']),
			dataSoal () {
				var detailSoal = JSON.parse(window.localStorage.getItem('soal_ujian'))
				// return shuffle(detailSoal.submateri[this.$route.params.index].soal)
				return detailSoal.submateri[this.$route.params.index].soal
			}
			// ...mapState('dataSoal', ['page', 'isLoading']),
			// dataSoal () {
			// 	return JSON.parse(window.localStorage.getItem('soal'))
			// }
		}
	}
</script>