<template>
	<div
		class="tes"
		:style="`background-image: url(${require('@/assets/login_bg.png')})`">
		<Navbar/>		

		<v-col cols="12" lg="6" md="8" sm="12" class="my-12 mx-auto">
			<v-alert class="mb-12 title" type="info">
				Untuk bisa melihat Assessment Report kamu harus
				menyelesaikan semua Tes terlebih dahulu. <br>
				<v-btn 
					color="white" 
					light 
					depressed 
					:loading="isLoading"
					class="mt-3"
					:disabled="activeAssessment"
					@click="assessment">
					Assessment report
				</v-btn>
			</v-alert>
			<v-card class="mb-12" v-for="(data, index) in dataPaketSoal" :key="index">			
				<v-img 
					:src="require(`@/assets/banner_assesment/${index+1}.svg`)" 
					alt="sekolatik">
					<v-card-title class="d-flex">
						<div class="white--text font-weight-bold">{{data.materi_uji}}</div>
						<v-spacer></v-spacer>
						<v-btn
							v-if="data.completed === 1"
							color="indigo" 
							dark depressed>
							selesai
						</v-btn>
						<v-btn 
							v-else
							color="indigo" 
							dark depressed 
							:loading="isLoading"
							@click="mulai(data)">
							mulai
						</v-btn>
					</v-card-title>
				</v-img>
				<v-card-text class="pt-4">					
				  <v-simple-table>
				    <template v-slot:default>
				      <thead class="grey lighten-2">
				        <tr>
				          <th>No</th>
				          <th class="text-left">Materi Uji</th>
				          <th>Butir Soal</th>
				        </tr>
				      </thead>
				      <tbody>
				        <tr v-for="(item, index) in data.submateri" :key="index">
				          <td>{{ index+1 }}</td>
				          <td class="text-capitalize">{{ item.nama }}</td>
				          <td>{{ item.butir_soal }}</td>
				        </tr>
				      </tbody>
				    </template>
				  </v-simple-table>
				</v-card-text>
			</v-card>
		</v-col>
	</div>
</template>

<style scoped>
	.tes{
		background-size: cover;
		background-position: center;
		background-attachment: fixed;
		padding-top: 56px;
	}
	img{ width: 100%; }
</style>

<script>
	import {mapState} from 'vuex'
	import {findIndex} from 'lodash'
	import moment from 'moment'
	import Navbar from '@/components/Navbar'
	import Footer from '@/components/Footer'

	export default{
		data: () => ({
			idUjian: 0
		}),
		components: {
			Footer,
			Navbar,
		},

		methods: {
			mulai (data) {
				var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
				var id_paket_soal = JSON.parse(window.localStorage.getItem('id_paket_soal'))
				var pilihan_ptn = window.localStorage.getItem('pilihan_ptn')
				var id_subpaket_tryout = JSON.parse(window.localStorage.getItem('id_subpaket_tryout'))

				var data2 = {
					pilihan_ptn: pilihan_ptn,
				  id_peserta: peserta.id,
				  id_subpaket_tryout: id_subpaket_tryout,
				  id_paket_soal: id_paket_soal,
				  id_materi_uji: data.id_materi_uji
				}
				var data3 = {
					ptn: pilihan_ptn,
				  // id_peserta: peserta.id,
				  subpaket_id: id_subpaket_tryout,
				  paketsoal_id: id_paket_soal,
				  materiuji_id: data.id_materi_uji
				}
				// console.log(data2)
				// this.$store.dispatch('dataSoal/postDataUjian', data2)
				this.$store.dispatch('dataSoal/postDataUjian2', data3)
				this.$store.dispatch('dataSoal/updateIsLoading', true)

				window.localStorage.setItem('detailPaketSoal', JSON.stringify(data))
				var waktu = moment().format('YYYY-MM-DD hh:mm:ss')
				window.localStorage.setItem('durasi_ujian_mulai_0', JSON.stringify(waktu))
				window.localStorage.removeItem('submisi')
			},
			assessment () {
				this.$router.push({name: 'analisis'})
			}
		},

		computed: {
			...mapState('dataSoal', ['dataPaketSoal', 'isLoading']),
			activeAssessment () {
				var find = findIndex(this.dataPaketSoal, (data) => data.completed === 0);
				if (find === -1) {
					return false
				} else {
					return true
				}
			}
		},

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			var id = window.localStorage.getItem('id_paket_soal')
			var dataPaket = {
				id_peserta: peserta.id,
				id_paket_soal: id
			}
			this.$store.dispatch('dataSoal/getDataPaketSoal', dataPaket)			
			this.$store.dispatch('dataSoal/updateIsLoading', true)			
		}
	}
</script>