<template>
	<div>
		<div 
			v-for="(item, index) in detailAssessment.analisis_nilai.analisis_butir_soal" 
			:key="index">
			<v-btn large dark color="indigo" tile class="mb-12 mt-6" depressed>
				{{item.materi_uji}}
			</v-btn>
			<v-card elevation="4">
				<v-card-text class="black--text">
					<v-row
						v-for="(val, i) in item.detail" 
						:key="i">
						<v-col cols="12" md="6" sm="12">
							<v-sheet class="ba pa-3">
								<div class="text-uppercase title">{{val.submateri}}</div>
								<span>
									Dikerjakan dalam waktu kurang lebih 
									{{ waktu(val.durasi_pengerjaan.value) }}
								</span>
							</v-sheet>
						</v-col>
						<v-col cols="12" md="6" sm="12">
							<p>
								{{val.benar}} benar / {{val.total_soal}} Soal
								<v-btn 
									v-show="$route.params.id === undefined"
									outlined 
									color="primary" 
									small class="ml-3" 
									:disabled="disable(i)"
									@click="pembahasan(val, index)">
									Review
								</v-btn>
							</p>
							<v-progress-linear :value="progress(val.benar, val.total_soal)" height="10"></v-progress-linear>
						</v-col>
					</v-row>
				</v-card-text>			
			</v-card>
		</div>
	</div>
</template>

<style scoped>
	.ba{ box-shadow: 0 0 0 1px indigo;border-radius: 3px; }
</style>

<script>
	import {mapState} from 'vuex'
	import {flattenDeep, findIndex} from 'lodash'
	import moment from 'moment'

	export default {
		computed: {
			...mapState('dataAssessment', ['detailAssessment']),
			...mapState('dataSoal', ['dataPaketSoal']),
			...mapState('dataDashboard', ['dataDashboard']),		
			jumlahPaket () {
				var jadi = this.dataDashboard.map(paket => {
					return paket.paket.map(data => {
						return data
					})
				})

				return flattenDeep(jadi)
			}
		},
		methods: {
			progress (val, total) {
				return (val*100)/total
			},
			pembahasan (val, i) {
				var data = {
					id_sub: this.detailAssessment.analisis_nilai.analisis_butir_soal[i].id_sub,
					id_ujian: this.detailAssessment.info_ujian.id_ujian
				}
				// console.log(val)
				window.localStorage.setItem('paramAssessment' ,JSON.stringify(data))
				console.log(val)
				this.$router.push({name: 'pembahasan', params: {id: val.id_kategori_submateri}})
			},
			disable (index) {
				var paketPremium = this.jumlahPaket.filter(e => {
          // console.log(e.status)
          if (e.nama_paket.indexOf('FREE') === -1) {
            return e
          }
        })
        var hasil = findIndex(paketPremium, o => o.status === true)
        // console.log(hasil)        
        if (hasil === -1 && index === 0) {
          return false
        } else if (hasil > -1) {
        	return false
        } else {
          return true
        }
			},
			waktu (time) {
				var durasi = moment.duration(time, 'seconds')

				return durasi.minutes()+' menit '+durasi.seconds()+' detik'
			}
		}
	}
</script>