import Vue from 'vue'
import Vuex from 'vuex'

import dataLogin from './login'
import dataSoal from './soal'
import dataDashboard from './dashboard'
import dataPTN from './ptn'
import dataPaket from './paket'
import dataPayment from './payment'
import dataAssessment from './assessment'
import dataRanking from './ranking'
import dataBiodata from './biodata'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    drawer: false,
    connection: null
  },
  mutations: {
    updateDrawer (state, payload) {
      state.drawer = payload
    },
    updateConnection (state, payload) {
      state.connection = payload
    }
  },
  getters: {},
  actions: {
    updateDrawer (context, payload) {
      context.commit('updateDrawer', payload)
    },
    updateConnection (context, payload) {
      context.commit('updateConnection', payload)
    }
  },
  modules: {
  	dataBiodata: dataBiodata,
    dataRanking: dataRanking,
    dataAssessment: dataAssessment,
    dataPayment: dataPayment,
    dataPaket: dataPaket,
  	dataPTN: dataPTN,
  	dataDashboard: dataDashboard,
  	dataSoal: dataSoal,
  	dataLogin: dataLogin
  }
})

export default store
