<template>
	<div style="padding-top: 56px">		
		<Navbar/>

		<section 
			class="banner" 
			:style="`background-image: url(${require('@/assets/banner.png')})`">		
		</section>

		<div style="position: relative;">
			<div style="position: absolute;top: -100px;width: 100%;">
				<v-col cols="12" md="8" sm="12" class="mx-auto pa-0">
					<v-container>
						<Header/>
					</v-container>

					<v-container>
						<Nilai/>
					</v-container>

					<v-container v-if="dataRanking !== null">
						<v-card elevation="12">
							<v-card-text class="black--text">
								<Peringkat/>
								<NilaiPaketSoal/>
								<DetailStudi/>
								<Status/>
							</v-card-text>
						</v-card>
					</v-container>
				</v-col>

				<Footer class="mt-12"/>							
			</div>
		</div>
	</div>
</template>

<style scoped>
	.banner{
		width: 100%;
		height: 200px;
		background-size: cover;
		background-position: center;
	}
</style>

<script>
	import { mapState } from 'vuex'
	import { flattenDeep } from 'lodash'
	import Navbar from '@/components/Navbar'
	import Footer from '@/components/Footer'
	import Header from '@/components/Simulasi/Header'
	import Nilai from '@/components/Simulasi/Nilai'
	import Peringkat from '@/components/Simulasi/Peringkat'
	import NilaiPaketSoal from '@/components/Simulasi/NilaiPaketSoal'
	import Status from '@/components/Simulasi/Status'
	import DetailStudi from '@/components/Simulasi/DetailStudi'
	export default {
		components: {
			Status,
			DetailStudi,
			NilaiPaketSoal,
			Peringkat,
			Nilai,
			Header,
			Footer,
			Navbar
		},

		created () {
			var peserta = JSON.parse(window.localStorage.getItem('dataPeserta'))
			this.$store.dispatch('dataRanking/getDataHistory', peserta.id)
			this.$store.dispatch('dataDashboard/getDataDashboard', peserta.id)
		},

		computed: {
			...mapState('dataRanking', ['dataRanking'])
		}
	}
</script>